import { PhotoIcon } from '@heroicons/react/24/solid';
import { useSellerAdminStyles } from './useSellerAdminStyles';

export function ListServiceTab() {
    const { darkMode, cardClasses, inputClasses, textPrimary, textSecondary } = useSellerAdminStyles();

    return (
        <div className="space-y-6">
            <h2 className={`text-2xl font-bold ${textPrimary}`}>List New Service</h2>

            <div className={cardClasses}>
                <div className="p-6 space-y-6">
                    <div className="grid md:grid-cols-2 gap-4">
                        <div>
                            <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Service Name *</label>
                            <input type="text" placeholder="e.g., Dyno Tuning Session" className={inputClasses} />
                        </div>
                        <div>
                            <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Price *</label>
                            <input type="number" placeholder="199.99" className={inputClasses} />
                        </div>
                    </div>

                    <div>
                        <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Description *</label>
                        <textarea rows={4} placeholder="Describe your service, what's included, duration, etc." className={inputClasses} />
                    </div>

                    <div className="grid md:grid-cols-2 gap-4">
                        <div>
                            <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Service Category</label>
                            <select className={inputClasses}>
                                <option>Dyno Tuning</option>
                                <option>Installation</option>
                                <option>Fabrication</option>
                                <option>Maintenance</option>
                                <option>Consultation</option>
                            </select>
                        </div>
                        <div>
                            <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Estimated Duration</label>
                            <input type="text" placeholder="e.g., 2-3 hours" className={inputClasses} />
                        </div>
                    </div>

                    <div>
                        <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>What's Included</label>
                        <textarea rows={3} placeholder="List what's included in this service..." className={inputClasses} />
                    </div>

                    <div>
                        <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Featured Image</label>
                        <div className={`border-2 border-dashed ${darkMode ? 'border-zinc-600' : 'border-gray-300'} rounded-xl p-8 text-center`}>
                            <PhotoIcon className={`w-12 h-12 mx-auto mb-4 ${textSecondary}`} />
                            <p className={textSecondary}>Drag and drop an image or click to upload</p>
                            <button className={`mt-4 px-4 py-2 ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'} rounded-lg ${textPrimary}`}>
                                Browse Files
                            </button>
                        </div>
                    </div>

                    <div className="flex items-center gap-4">
                        <label className="flex items-center gap-2">
                            <input type="checkbox" className="rounded" />
                            <span className={textSecondary}>Requires Appointment</span>
                        </label>
                        <label className="flex items-center gap-2">
                            <input type="checkbox" className="rounded" />
                            <span className={textSecondary}>Available Weekends</span>
                        </label>
                    </div>

                    <div className="flex items-center gap-4 pt-4">
                        <button className="px-6 py-3 bg-gradient-to-r from-green-500 via-teal-500 to-blue-500 text-white font-bold rounded-xl hover:shadow-lg transition-all">
                            Publish Service
                        </button>
                        <button className={`px-6 py-3 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} font-medium rounded-xl transition-all`}>
                            Save as Draft
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
