const categories = [
    'All Categories',
    'Air Intake',
    'Exhaust',
    'Forced Induction',
    'Fuel System',
    'Engine Internals',
    'Cooling',
    'Sensors',
    'Electronics',
];

export async function loader({ request }: DataFunctionArgs) {
    // TODO: Replace with Vendure parts collection data when available
    return json({ parts: demoParts });
}

export default function PartsIndex() {
    const { parts } = useLoaderData<typeof loader>();
    const { darkMode } = useDarkMode();
    const { t } = useTranslation();

    const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-orange-500/10 via-red-500/10 to-yellow-500/10 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-24 px-6 flex flex-col items-center text-center">
                    <WrenchScrewdriverIcon className="w-16 h-16 text-orange-500 mb-6" />
                    <h1 className="text-4xl sm:text-5xl lg:text-6xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-orange-400 via-red-500 to-yellow-500">
                            Performance Parts
                        </span>
                    </h1>
                    <p className="mt-6 text-xl text-gray-300 max-w-2xl">
                        Shop high-quality performance parts from trusted brands. Intakes, exhausts, turbos, and everything you need for your build.
                    </p>

                    {/* Search Bar */}
                    <div className="mt-8 w-full max-w-xl">
                        <div className="relative">
                            <MagnifyingGlassIcon className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
                            <input
                                type="text"
                                placeholder="Search parts by name, brand, or vehicle..."
                                className={`w-full pl-12 pr-4 py-4 rounded-xl ${
                                    darkMode
                                        ? 'bg-zinc-800 border-zinc-700 text-white placeholder-gray-500'
                                        : 'bg-white border-gray-200 text-gray-900 placeholder-gray-400'
                                } border focus:ring-2 focus:ring-orange-500 focus:outline-none`}
                            />
                        </div>
                    </div>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-24 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            <div className="max-w-6xl mx-auto px-6 py-12">
                {/* Stats & Filters Bar */}
                <div className={`${cardClasses} p-4 mb-8`}>
                    <div className="flex flex-wrap items-center justify-between gap-4">
                        <div className={textSecondary}>
                            <span className={`font-bold ${textPrimary}`}>{parts.length}</span> parts available
                        </div>
                        <div className="flex items-center gap-4">
                            <select className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 border-zinc-600 text-white' : 'bg-gray-50 border-gray-200 text-gray-900'} border text-sm`}>
                                {categories.map((cat) => (
                                    <option key={cat}>{cat}</option>
                                ))}
                            </select>
                            <select className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 border-zinc-600 text-white' : 'bg-gray-50 border-gray-200 text-gray-900'} border text-sm`}>
                                <option>Sort: Featured</option>
                                <option>Price: Low to High</option>
                                <option>Price: High to Low</option>
                                <option>Top Rated</option>
                                <option>Best Sellers</option>
                            </select>
                        </div>
                    </div>
                </div>

                {/* Parts Grid */}
                <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-6">
                    {parts.map((part) => (
                        <Link
                            key={part.id}
                            to={`/products/${part.slug}`}
                            prefetch="intent"
                            className={`${cardClasses} overflow-hidden group hover:shadow-2xl hover:shadow-orange-500/10 transition-all duration-300 hover:-translate-y-1`}
                        >
                            {/* Image Placeholder */}
                            <div className="relative h-48 overflow-hidden">
                                {part.image ? (
                                    <img
                                        src={part.image}
                                        alt={part.name}
                                        className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-500"
                                    />
                                ) : (
                                    <div className="w-full h-full bg-gradient-to-br from-orange-500/30 via-red-500/30 to-yellow-500/30 flex items-center justify-center">
                                        <WrenchScrewdriverIcon className="w-16 h-16 text-white/30" />
                                    </div>
                                )}

                                {/* Badges */}
                                <div className="absolute top-3 left-3 flex flex-col gap-2">
                                    {part.originalPrice && (
                                        <span className="px-2 py-1 bg-red-500 text-white text-xs font-bold rounded">
                                            SALE
                                        </span>
                                    )}
                                    {!part.inStock && (
                                        <span className="px-2 py-1 bg-gray-800 text-white text-xs font-bold rounded">
                                            OUT OF STOCK
                                        </span>
                                    )}
                                </div>

                                {part.freeShipping && (
                                    <div className="absolute top-3 right-3">
                                        <span className="px-2 py-1 bg-green-500 text-white text-xs font-bold rounded flex items-center gap-1">
                                            <TruckIcon className="w-3 h-3" />
                                            FREE SHIP
                                        </span>
                                    </div>
                                )}

                                <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent" />
                            </div>

                            <div className="p-4">
                                {/* Category & Brand */}
                                <div className="flex items-center justify-between text-xs mb-2">
                                    <span className={`px-2 py-1 rounded ${darkMode ? 'bg-zinc-700 text-gray-300' : 'bg-gray-100 text-gray-600'}`}>
                                        {part.category}
                                    </span>
                                    <span className={textSecondary}>{part.brand}</span>
                                </div>

                                {/* Name */}
                                <h3 className={`font-bold text-lg mb-2 ${textPrimary} group-hover:text-orange-500 transition-colors`}>
                                    {part.name}
                                </h3>

                                {/* Rating */}
                                <div className="flex items-center mb-3">
                                    {[...Array(5)].map((_, i) => (
                                        <StarIcon key={i} className={`w-4 h-4 ${i < Math.floor(part.rating) ? 'text-yellow-400' : 'text-gray-600'}`} />
                                    ))}
                                    <span className={`text-sm ml-2 ${textSecondary}`}>
                                        ({part.reviewCount})
                                    </span>
                                </div>

                                {/* Platforms */}
                                <div className="flex flex-wrap gap-1 mb-3">
                                    {part.platforms.slice(0, 2).map((platform) => (
                                        <span
                                            key={platform}
                                            className={`px-2 py-0.5 text-xs rounded ${darkMode ? 'bg-zinc-700 text-gray-400' : 'bg-gray-100 text-gray-500'}`}
                                        >
                                            {platform}
                                        </span>
                                    ))}
                                    {part.platforms.length > 2 && (
                                        <span className={`px-2 py-0.5 text-xs rounded ${darkMode ? 'bg-zinc-700 text-gray-400' : 'bg-gray-100 text-gray-500'}`}>
                                            +{part.platforms.length - 2}
                                        </span>
                                    )}
                                </div>

                                {/* Price */}
                                <div className={`pt-3 border-t ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex items-center justify-between`}>
                                    <div>
                                        <span className={`text-xl font-bold ${textPrimary}`}>
                                            ${part.price.toFixed(2)}
                                        </span>
                                        {part.originalPrice && (
                                            <span className={`text-sm ml-2 line-through ${textSecondary}`}>
                                                ${part.originalPrice.toFixed(2)}
                                            </span>
                                        )}
                                    </div>
                                    <span className="text-orange-500 group-hover:translate-x-1 transition-transform">
                                        &rarr;
                                    </span>
                                </div>
                            </div>
                        </Link>
                    ))}
                </div>

                {/* Load More */}
                <div className="mt-12 text-center">
                    <button className={`px-8 py-3 ${darkMode ? 'bg-zinc-800 text-white hover:bg-zinc-700' : 'bg-white text-gray-900 hover:bg-gray-50'} font-bold rounded-xl shadow-lg transition-all`}>
                        Load More Parts
                    </button>
                </div>

                {/* Sell Parts CTA */}
                <div className="mt-16">
                    <div className="relative overflow-hidden rounded-3xl bg-gradient-to-r from-orange-500 via-red-500 to-yellow-500 p-1">
                        <div className={`${darkMode ? 'bg-gray-900' : 'bg-white'} rounded-3xl py-12 px-6 text-center`}>
                            <h2 className={`text-2xl sm:text-3xl font-bold mb-4 ${textPrimary}`}>
                                Have Parts to Sell?
                            </h2>
                            <p className={`mb-6 max-w-2xl mx-auto ${textSecondary}`}>
                                List your performance parts on TunerSwap and reach thousands of enthusiasts looking for quality components.
                            </p>
                            <Link
                                to="/tunersignup"
                                className="inline-block px-8 py-3 bg-gradient-to-r from-orange-500 via-red-500 to-yellow-500 text-white font-bold rounded-xl shadow-lg hover:shadow-2xl hover:shadow-orange-500/25 transition-all duration-300"
                            >
                                Start Selling Today
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
