import { useLoaderData, Link } from '@remix-run/react';
import { DataFunctionArgs, json } from '@remix-run/server-runtime';
import { useDarkMode } from '~/root';
import { getCollections } from '~/providers/collections/collections';
import { useTranslation } from 'react-i18next';
import {
    Squares2X2Icon,
    MagnifyingGlassIcon,
} from '@heroicons/react/24/solid';

export async function loader({ request }: DataFunctionArgs) {
    const collections = await getCollections(request, { take: 100 });
    return json({ collections });
}

export default function CollectionsIndex() {
    const { collections } = useLoaderData<typeof loader>();
    const { darkMode } = useDarkMode();
    const { t } = useTranslation();

    // Separate top-level collections from sub-collections
    const topLevelCollections = collections?.filter(
        (c) => !c.parent?.name || c.parent.name === '__root_collection__'
    ) || [];

    const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-yellow-500/10 via-red-500/10 to-blue-500/10 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-24 px-6 flex flex-col items-center text-center">
                    <Squares2X2Icon className="w-16 h-16 text-red-500 mb-6" />
                    <h1 className="text-4xl sm:text-5xl lg:text-6xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-yellow-400 via-red-500 to-blue-500">
                            Browse Collections
                        </span>
                    </h1>
                    <p className="mt-6 text-xl text-gray-300 max-w-2xl">
                        Explore our curated collections of performance tunes, parts, and accessories for all major platforms.
                    </p>

                    {/* Search Bar */}
                    <div className="mt-8 w-full max-w-xl">
                        <div className="relative">
                            <MagnifyingGlassIcon className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
                            <input
                                type="text"
                                placeholder="Search collections..."
                                className={`w-full pl-12 pr-4 py-4 rounded-xl ${
                                    darkMode
                                        ? 'bg-zinc-800 border-zinc-700 text-white placeholder-gray-500'
                                        : 'bg-white border-gray-200 text-gray-900 placeholder-gray-400'
                                } border focus:ring-2 focus:ring-red-500 focus:outline-none`}
                            />
                        </div>
                    </div>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-24 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            {/* Collections Grid */}
            <div className="max-w-6xl mx-auto px-6 py-12">
                {/* Stats Bar */}
                <div className={`${cardClasses} p-4 mb-8`}>
                    <div className="flex flex-wrap items-center justify-between gap-4">
                        <div className={textSecondary}>
                            <span className={`font-bold ${textPrimary}`}>{topLevelCollections.length}</span> collections available
                        </div>
                        <div className="flex items-center gap-4">
                            <select className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 border-zinc-600 text-white' : 'bg-gray-50 border-gray-200 text-gray-900'} border text-sm`}>
                                <option>All Categories</option>
                                <option>Tunes</option>
                                <option>Parts</option>
                                <option>Accessories</option>
                            </select>
                            <select className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 border-zinc-600 text-white' : 'bg-gray-50 border-gray-200 text-gray-900'} border text-sm`}>
                                <option>Sort: A-Z</option>
                                <option>Sort: Z-A</option>
                                <option>Most Popular</option>
                                <option>Newest</option>
                            </select>
                        </div>
                    </div>
                </div>

                {/* Collections Grid */}
                {topLevelCollections.length > 0 ? (
                    <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-6">
                        {topLevelCollections.map((collection) => (
                            <Link
                                key={collection.id}
                                to={`/collections/${collection.slug}`}
                                prefetch="intent"
                                className={`${cardClasses} overflow-hidden group hover:shadow-2xl hover:shadow-red-500/10 transition-all duration-300 hover:-translate-y-1`}
                            >
                                <div className="relative h-48 overflow-hidden">
                                    {collection.featuredAsset?.preview ? (
                                        <img
                                            src={`${collection.featuredAsset.preview}?w=600&h=400`}
                                            alt={collection.name}
                                            className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-500"
                                        />
                                    ) : (
                                        <div className="w-full h-full bg-gradient-to-br from-yellow-500 via-red-500 to-blue-500 flex items-center justify-center">
                                            <Squares2X2Icon className="w-16 h-16 text-white/50" />
                                        </div>
                                    )}
                                    <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/20 to-transparent" />
                                    <div className="absolute bottom-0 left-0 right-0 p-4">
                                        <h3 className="text-xl font-bold text-white">{collection.name}</h3>
                                    </div>
                                </div>
                                <div className="p-4">
                                    <div className="flex items-center justify-between">
                                        <span className={`text-sm ${textSecondary}`}>
                                            View collection
                                        </span>
                                        <span className="text-red-500 group-hover:translate-x-1 transition-transform">
                                            &rarr;
                                        </span>
                                    </div>
                                </div>
                            </Link>
                        ))}
                    </div>
                ) : (
                    <div className={`${cardClasses} p-12 text-center`}>
                        <Squares2X2Icon className={`w-16 h-16 mx-auto mb-4 ${darkMode ? 'text-zinc-600' : 'text-gray-300'}`} />
                        <h3 className={`text-xl font-bold mb-2 ${textPrimary}`}>No Collections Found</h3>
                        <p className={textSecondary}>Check back later for new collections.</p>
                    </div>
                )}

                {/* Featured Section */}
                <div className="mt-16">
                    <div className="relative overflow-hidden rounded-3xl bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500 p-1">
                        <div className={`${darkMode ? 'bg-gray-900' : 'bg-white'} rounded-3xl py-12 px-6 text-center`}>
                            <h2 className={`text-2xl sm:text-3xl font-bold mb-4 ${textPrimary}`}>
                                Can't Find What You're Looking For?
                            </h2>
                            <p className={`mb-6 max-w-2xl mx-auto ${textSecondary}`}>
                                Browse all our products or contact us for custom tuning solutions tailored to your vehicle.
                            </p>
                            <div className="flex flex-col sm:flex-row gap-4 justify-center">
                                <Link
                                    to="/search"
                                    className="px-8 py-3 bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500 text-white font-bold rounded-xl shadow-lg hover:shadow-2xl hover:shadow-red-500/25 transition-all duration-300"
                                >
                                    Search All Products
                                </Link>
                                <Link
                                    to="/contact"
                                    className={`px-8 py-3 ${darkMode ? 'bg-zinc-800 text-white hover:bg-zinc-700' : 'bg-gray-100 text-gray-900 hover:bg-gray-200'} font-bold rounded-xl transition-all duration-300`}
                                >
                                    Contact Us
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
