const platforms = [
    'All Platforms',
    'GM/Chevy',
    'Ford',
    'Mopar/Dodge',
    'BMW',
    'Audi/VW',
    'Diesel',
    'Japanese',
];

export async function loader({ request }: DataFunctionArgs) {
    // TODO: Replace with Vendure tunes collection data when available
    return json({ tunes: demoTunes });
}

export default function TunesIndex() {
    const { tunes } = useLoaderData<typeof loader>();
    const { darkMode } = useDarkMode();
    const { t } = useTranslation();

    const featuredTunes = tunes.filter(t => t.featured);
    const allTunes = tunes;

    const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-yellow-500/10 via-red-500/10 to-blue-500/10 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-24 px-6 flex flex-col items-center text-center">
                    <CpuChipIcon className="w-16 h-16 text-red-500 mb-6" />
                    <h1 className="text-4xl sm:text-5xl lg:text-6xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-yellow-400 via-red-500 to-blue-500">
                            Browse Tunes
                        </span>
                    </h1>
                    <p className="mt-6 text-xl text-gray-300 max-w-2xl">
                        Discover performance tunes from verified professionals. Unlock your vehicle's full potential with calibrations for every platform.
                    </p>

                    {/* Search Bar */}
                    <div className="mt-8 w-full max-w-xl">
                        <div className="relative">
                            <MagnifyingGlassIcon className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
                            <input
                                type="text"
                                placeholder="Search tunes by name, platform, or tuner..."
                                className={`w-full pl-12 pr-4 py-4 rounded-xl ${
                                    darkMode
                                        ? 'bg-zinc-800 border-zinc-700 text-white placeholder-gray-500'
                                        : 'bg-white border-gray-200 text-gray-900 placeholder-gray-400'
                                } border focus:ring-2 focus:ring-red-500 focus:outline-none`}
                            />
                        </div>
                    </div>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-24 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            <div className="max-w-6xl mx-auto px-6 py-12">
                {/* Stats & Filters Bar */}
                <div className={`${cardClasses} p-4 mb-8`}>
                    <div className="flex flex-wrap items-center justify-between gap-4">
                        <div className={textSecondary}>
                            <span className={`font-bold ${textPrimary}`}>{allTunes.length}</span> tunes available
                        </div>
                        <div className="flex items-center gap-4">
                            <select className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 border-zinc-600 text-white' : 'bg-gray-50 border-gray-200 text-gray-900'} border text-sm`}>
                                {platforms.map((platform) => (
                                    <option key={platform}>{platform}</option>
                                ))}
                            </select>
                            <select className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 border-zinc-600 text-white' : 'bg-gray-50 border-gray-200 text-gray-900'} border text-sm`}>
                                <option>Sort: Featured</option>
                                <option>Price: Low to High</option>
                                <option>Price: High to Low</option>
                                <option>Top Rated</option>
                                <option>Most Reviews</option>
                                <option>Newest</option>
                            </select>
                        </div>
                    </div>
                </div>

                {/* Featured Tunes */}
                {featuredTunes.length > 0 && (
                    <div className="mb-12">
                        <h2 className={`text-2xl font-bold mb-6 ${textPrimary} flex items-center gap-2`}>
                            <StarIcon className="w-6 h-6 text-yellow-500" />
                            Featured Tunes
                        </h2>
                        <div className="grid md:grid-cols-3 gap-6">
                            {featuredTunes.map((tune) => (
                                <Link
                                    key={tune.id}
                                    to={`/products/${tune.slug}`}
                                    prefetch="intent"
                                    className={`${cardClasses} overflow-hidden group hover:shadow-2xl hover:shadow-red-500/10 transition-all duration-300 hover:-translate-y-1`}
                                >
                                    {/* Featured Badge */}
                                    <div className="bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500 text-white text-xs font-bold text-center py-1">
                                        FEATURED TUNE
                                    </div>

                                    {/* Image Placeholder */}
                                    <div className="relative h-36 overflow-hidden">
                                        <div className="w-full h-full bg-gradient-to-br from-yellow-500/20 via-red-500/20 to-blue-500/20 flex items-center justify-center">
                                            <CpuChipIcon className="w-16 h-16 text-white/20" />
                                        </div>
                                        {tune.originalPrice && (
                                            <span className="absolute top-3 right-3 px-2 py-1 bg-red-500 text-white text-xs font-bold rounded">
                                                SALE
                                            </span>
                                        )}
                                    </div>

                                    <div className="p-4">
                                        {/* Platform Badge */}
                                        <span className={`inline-block px-2 py-1 text-xs rounded ${darkMode ? 'bg-zinc-700 text-gray-300' : 'bg-gray-100 text-gray-600'} mb-2`}>
                                            {tune.platform}
                                        </span>

                                        {/* Name */}
                                        <h3 className={`font-bold text-lg mb-1 ${textPrimary} group-hover:text-red-500 transition-colors`}>
                                            {tune.name}
                                        </h3>

                                        {/* Tuner */}
                                        <div className="flex items-center gap-1 mb-2">
                                            <span className={`text-sm ${textSecondary}`}>by</span>
                                            <span className={`text-sm font-medium ${darkMode ? 'text-blue-400' : 'text-blue-600'}`}>
                                                {tune.tuner}
                                            </span>
                                            {tune.verified && (
                                                <CheckBadgeIcon className="w-4 h-4 text-blue-500" />
                                            )}
                                        </div>

                                        {/* Gains */}
                                        <div className="flex items-center gap-3 mb-3">
                                            <span className="flex items-center text-sm text-green-500 font-medium">
                                                <BoltIcon className="w-4 h-4 mr-1" />
                                                {tune.hpGain}
                                            </span>
                                            <span className="flex items-center text-sm text-green-500 font-medium">
                                                <AdjustmentsHorizontalIcon className="w-4 h-4 mr-1" />
                                                {tune.torqueGain}
                                            </span>
                                        </div>

                                        {/* Rating */}
                                        <div className="flex items-center mb-3">
                                            {[...Array(5)].map((_, i) => (
                                                <StarIcon key={i} className={`w-4 h-4 ${i < Math.floor(tune.rating) ? 'text-yellow-400' : 'text-gray-600'}`} />
                                            ))}
                                            <span className={`text-sm ml-2 ${textSecondary}`}>
                                                ({tune.reviewCount})
                                            </span>
                                        </div>

                                        {/* Price */}
                                        <div className={`pt-3 border-t ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex items-center justify-between`}>
                                            <div>
                                                <span className={`text-xl font-bold ${textPrimary}`}>
                                                    ${tune.price.toFixed(2)}
                                                </span>
                                                {tune.originalPrice && (
                                                    <span className={`text-sm ml-2 line-through ${textSecondary}`}>
                                                        ${tune.originalPrice.toFixed(2)}
                                                    </span>
                                                )}
                                            </div>
                                            <span className="text-red-500 group-hover:translate-x-1 transition-transform">
                                                &rarr;
                                            </span>
                                        </div>
                                    </div>
                                </Link>
                            ))}
                        </div>
                    </div>
                )}

                {/* All Tunes Grid */}
                <div>
                    <h2 className={`text-2xl font-bold mb-6 ${textPrimary}`}>
                        All Tunes
                    </h2>
                    <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-6">
                        {allTunes.map((tune) => (
                            <Link
                                key={tune.id}
                                to={`/products/${tune.slug}`}
                                prefetch="intent"
                                className={`${cardClasses} overflow-hidden group hover:shadow-2xl hover:shadow-red-500/10 transition-all duration-300 hover:-translate-y-1`}
                            >
                                {/* Image Placeholder */}
                                <div className="relative h-32 overflow-hidden">
                                    <div className="w-full h-full bg-gradient-to-br from-yellow-500/20 via-red-500/20 to-blue-500/20 flex items-center justify-center">
                                        <CpuChipIcon className="w-12 h-12 text-white/20" />
                                    </div>
                                    {tune.originalPrice && (
                                        <span className="absolute top-2 right-2 px-2 py-0.5 bg-red-500 text-white text-xs font-bold rounded">
                                            SALE
                                        </span>
                                    )}
                                </div>

                                <div className="p-4">
                                    {/* Platform */}
                                    <span className={`inline-block px-2 py-0.5 text-xs rounded ${darkMode ? 'bg-zinc-700 text-gray-400' : 'bg-gray-100 text-gray-500'} mb-2`}>
                                        {tune.platform}
                                    </span>

                                    {/* Name */}
                                    <h3 className={`font-bold mb-1 ${textPrimary} group-hover:text-red-500 transition-colors`}>
                                        {tune.name}
                                    </h3>

                                    {/* Tuner */}
                                    <div className="flex items-center gap-1 mb-2">
                                        <span className={`text-xs ${textSecondary}`}>by {tune.tuner}</span>
                                        {tune.verified && (
                                            <CheckBadgeIcon className="w-3 h-3 text-blue-500" />
                                        )}
                                    </div>

                                    {/* Gains */}
                                    <div className="flex items-center gap-2 mb-2">
                                        <span className="text-xs text-green-500 font-medium">{tune.hpGain}</span>
                                        <span className="text-xs text-green-500 font-medium">{tune.torqueGain}</span>
                                    </div>

                                    {/* Rating & Price */}
                                    <div className={`pt-2 border-t ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex items-center justify-between`}>
                                        <div className="flex items-center">
                                            <StarIcon className="w-4 h-4 text-yellow-400" />
                                            <span className={`text-sm ml-1 ${textSecondary}`}>
                                                {tune.rating}
                                            </span>
                                        </div>
                                        <div>
                                            <span className={`font-bold ${textPrimary}`}>
                                                ${tune.price.toFixed(2)}
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        ))}
                    </div>
                </div>

                {/* Load More */}
                <div className="mt-12 text-center">
                    <button className={`px-8 py-3 ${darkMode ? 'bg-zinc-800 text-white hover:bg-zinc-700' : 'bg-white text-gray-900 hover:bg-gray-50'} font-bold rounded-xl shadow-lg transition-all`}>
                        Load More Tunes
                    </button>
                </div>

                {/* Become a Tuner CTA */}
                <div className="mt-16">
                    <div className="relative overflow-hidden rounded-3xl bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500 p-1">
                        <div className={`${darkMode ? 'bg-gray-900' : 'bg-white'} rounded-3xl py-12 px-6 text-center`}>
                            <h2 className={`text-2xl sm:text-3xl font-bold mb-4 ${textPrimary}`}>
                                Are You a Tuner?
                            </h2>
                            <p className={`mb-6 max-w-2xl mx-auto ${textSecondary}`}>
                                Join TunerSwap and start selling your tunes to enthusiasts worldwide. Reach more customers and grow your business.
                            </p>
                            <Link
                                to="/tunersignup"
                                className="inline-block px-8 py-3 bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500 text-white font-bold rounded-xl shadow-lg hover:shadow-2xl hover:shadow-red-500/25 transition-all duration-300"
                            >
                                Apply to Sell Tunes
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
