export async function loader({ request }: DataFunctionArgs) {
    // TODO: Replace with Vendure seller data when available
    return json({ sellers: demoSellers });
}

export default function SellersIndex() {
    const { sellers } = useLoaderData<typeof loader>();
    const { darkMode } = useDarkMode();
    const { t } = useTranslation();

    const featuredSellers = sellers.filter(s => s.featured);
    const allSellers = sellers;

    const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-blue-500/10 via-purple-500/10 to-red-500/10 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-24 px-6 flex flex-col items-center text-center">
                    <UserGroupIcon className="w-16 h-16 text-blue-500 mb-6" />
                    <h1 className="text-4xl sm:text-5xl lg:text-6xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-blue-400 via-purple-500 to-red-500">
                            Browse Tuners
                        </span>
                    </h1>
                    <p className="mt-6 text-xl text-gray-300 max-w-2xl">
                        Connect with verified tuning professionals. Find the perfect expert for your vehicle and performance goals.
                    </p>

                    {/* Search Bar */}
                    <div className="mt-8 w-full max-w-xl">
                        <div className="relative">
                            <MagnifyingGlassIcon className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
                            <input
                                type="text"
                                placeholder="Search tuners by name, specialty, or location..."
                                className={`w-full pl-12 pr-4 py-4 rounded-xl ${
                                    darkMode
                                        ? 'bg-zinc-800 border-zinc-700 text-white placeholder-gray-500'
                                        : 'bg-white border-gray-200 text-gray-900 placeholder-gray-400'
                                } border focus:ring-2 focus:ring-blue-500 focus:outline-none`}
                            />
                        </div>
                    </div>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-24 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            <div className="max-w-6xl mx-auto px-6 py-12">
                {/* Stats Bar */}
                <div className={`${cardClasses} p-4 mb-8`}>
                    <div className="flex flex-wrap items-center justify-between gap-4">
                        <div className={textSecondary}>
                            <span className={`font-bold ${textPrimary}`}>{allSellers.length}</span> tuners available
                        </div>
                        <div className="flex items-center gap-4">
                            <select className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 border-zinc-600 text-white' : 'bg-gray-50 border-gray-200 text-gray-900'} border text-sm`}>
                                <option>All Specialties</option>
                                <option>V8 Performance</option>
                                <option>Euro/Import</option>
                                <option>Diesel</option>
                                <option>JDM</option>
                                <option>Forced Induction</option>
                            </select>
                            <select className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 border-zinc-600 text-white' : 'bg-gray-50 border-gray-200 text-gray-900'} border text-sm`}>
                                <option>Sort: Top Rated</option>
                                <option>Most Tunes Sold</option>
                                <option>Most Reviews</option>
                                <option>Newest</option>
                            </select>
                        </div>
                    </div>
                </div>

                {/* Featured Tuners */}
                {featuredSellers.length > 0 && (
                    <div className="mb-12">
                        <h2 className={`text-2xl font-bold mb-6 ${textPrimary} flex items-center gap-2`}>
                            <StarIcon className="w-6 h-6 text-yellow-500" />
                            Featured Tuners
                        </h2>
                        <div className="grid md:grid-cols-3 gap-6">
                            {featuredSellers.map((seller) => (
                                <Link
                                    key={seller.id}
                                    to={`/sellers/${seller.slug}`}
                                    prefetch="intent"
                                    className={`${cardClasses} overflow-hidden group hover:shadow-2xl hover:shadow-blue-500/10 transition-all duration-300 hover:-translate-y-1`}
                                >
                                    {/* Featured Badge */}
                                    <div className="bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500 text-white text-xs font-bold text-center py-1">
                                        FEATURED TUNER
                                    </div>

                                    <div className="p-6">
                                        <div className="flex items-start gap-4">
                                            <div className="w-16 h-16 rounded-full bg-gradient-to-br from-blue-400 via-purple-500 to-red-500 flex items-center justify-center text-white font-bold text-xl shadow-lg flex-shrink-0">
                                                {seller.initials}
                                            </div>
                                            <div className="flex-1 min-w-0">
                                                <div className="flex items-center gap-2">
                                                    <h3 className={`font-bold text-lg truncate ${textPrimary}`}>{seller.name}</h3>
                                                    {seller.verified && (
                                                        <CheckBadgeIcon className="w-5 h-5 text-blue-500 flex-shrink-0" />
                                                    )}
                                                </div>
                                                <p className={`text-sm ${textSecondary}`}>{seller.specialty}</p>
                                            </div>
                                        </div>

                                        <div className={`flex items-center text-sm mt-4 ${textSecondary}`}>
                                            <MapPinIcon className="w-4 h-4 mr-1" />
                                            {seller.location}
                                        </div>

                                        <div className="flex items-center mt-3">
                                            {[...Array(5)].map((_, i) => (
                                                <StarIcon key={i} className={`w-4 h-4 ${i < Math.floor(seller.rating) ? 'text-yellow-400' : 'text-gray-600'}`} />
                                            ))}
                                            <span className={`text-sm ml-2 ${textSecondary}`}>
                                                {seller.rating} ({seller.reviewCount} reviews)
                                            </span>
                                        </div>

                                        <div className="flex flex-wrap gap-2 mt-4">
                                            {seller.platforms.slice(0, 3).map((platform) => (
                                                <span
                                                    key={platform}
                                                    className={`px-2 py-1 text-xs rounded-full ${darkMode ? 'bg-zinc-700 text-gray-300' : 'bg-gray-100 text-gray-700'}`}
                                                >
                                                    {platform}
                                                </span>
                                            ))}
                                            {seller.platforms.length > 3 && (
                                                <span className={`px-2 py-1 text-xs rounded-full ${darkMode ? 'bg-zinc-700 text-gray-300' : 'bg-gray-100 text-gray-700'}`}>
                                                    +{seller.platforms.length - 3} more
                                                </span>
                                            )}
                                        </div>

                                        <div className={`mt-4 pt-4 border-t ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex justify-between text-sm`}>
                                            <span className={textSecondary}>
                                                <span className={`font-bold ${textPrimary}`}>{seller.tunesSold}</span> tunes sold
                                            </span>
                                            <span className="text-blue-500 group-hover:translate-x-1 transition-transform">
                                                View Profile &rarr;
                                            </span>
                                        </div>
                                    </div>
                                </Link>
                            ))}
                        </div>
                    </div>
                )}

                {/* All Tuners Grid */}
                <div>
                    <h2 className={`text-2xl font-bold mb-6 ${textPrimary}`}>
                        All Tuners
                    </h2>
                    <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-6">
                        {allSellers.map((seller) => (
                            <Link
                                key={seller.id}
                                to={`/sellers/${seller.slug}`}
                                prefetch="intent"
                                className={`${cardClasses} p-6 group hover:shadow-2xl hover:shadow-blue-500/10 transition-all duration-300 hover:-translate-y-1`}
                            >
                                <div className="flex items-start gap-4">
                                    <div className={`w-14 h-14 rounded-full flex items-center justify-center text-white font-bold text-lg shadow-lg flex-shrink-0 ${
                                        seller.featured
                                            ? 'bg-gradient-to-br from-blue-400 via-purple-500 to-red-500'
                                            : darkMode ? 'bg-zinc-600' : 'bg-gray-400'
                                    }`}>
                                        {seller.initials}
                                    </div>
                                    <div className="flex-1 min-w-0">
                                        <div className="flex items-center gap-2">
                                            <h3 className={`font-bold truncate ${textPrimary}`}>{seller.name}</h3>
                                            {seller.verified && (
                                                <CheckBadgeIcon className="w-4 h-4 text-blue-500 flex-shrink-0" />
                                            )}
                                        </div>
                                        <p className={`text-sm ${textSecondary}`}>{seller.specialty}</p>
                                    </div>
                                </div>

                                <div className={`flex items-center text-sm mt-3 ${textSecondary}`}>
                                    <MapPinIcon className="w-4 h-4 mr-1" />
                                    {seller.location}
                                </div>

                                <div className="flex items-center mt-2">
                                    {[...Array(5)].map((_, i) => (
                                        <StarIcon key={i} className={`w-3 h-3 ${i < Math.floor(seller.rating) ? 'text-yellow-400' : 'text-gray-600'}`} />
                                    ))}
                                    <span className={`text-xs ml-2 ${textSecondary}`}>
                                        {seller.rating} ({seller.reviewCount})
                                    </span>
                                </div>

                                <div className={`mt-3 pt-3 border-t ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex justify-between text-sm`}>
                                    <span className={textSecondary}>
                                        {seller.tunesSold} tunes
                                    </span>
                                    <span className="text-blue-500 group-hover:translate-x-1 transition-transform">
                                        &rarr;
                                    </span>
                                </div>
                            </Link>
                        ))}
                    </div>
                </div>

                {/* Become a Tuner CTA */}
                <div className="mt-16">
                    <div className="relative overflow-hidden rounded-3xl bg-gradient-to-r from-blue-500 via-purple-500 to-red-500 p-1">
                        <div className={`${darkMode ? 'bg-gray-900' : 'bg-white'} rounded-3xl py-12 px-6 text-center`}>
                            <h2 className={`text-2xl sm:text-3xl font-bold mb-4 ${textPrimary}`}>
                                Are You a Tuner?
                            </h2>
                            <p className={`mb-6 max-w-2xl mx-auto ${textSecondary}`}>
                                Join our growing community of professional tuners. Sell your tunes, reach more customers, and grow your business.
                            </p>
                            <Link
                                to="/tunersignup"
                                className="inline-block px-8 py-3 bg-gradient-to-r from-blue-500 via-purple-500 to-red-500 text-white font-bold rounded-xl shadow-lg hover:shadow-2xl hover:shadow-purple-500/25 transition-all duration-300"
                            >
                                Apply to Become a Tuner
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
