import { demoOnly, type FallbackResult } from '../utils/vendure-fallback';
import {
    buyerMessages,
    type BuyerMessage
} from '~/data/buyer-demo-data';

/**
 * Get buyer's conversations with sellers
 */
export async function getBuyerMessages(): Promise<FallbackResult<BuyerMessage[]>> {
    return demoOnly(buyerMessages);
}

/**
 * Send a message to a seller
 */
export async function sendMessageToSeller(
    _conversationId: string,
    _text: string
): Promise<FallbackResult<{ success: boolean }>> {
    return demoOnly({ success: true });
}

/**
 * Mark conversation as read
 */
export async function markConversationRead(
    _conversationId: string
): Promise<FallbackResult<{ success: boolean }>> {
    return demoOnly({ success: true });
}

/**
 * Start a new conversation with a seller
 */
export async function startConversation(
    _sellerId: string,
    _subject: string,
    _message: string
): Promise<FallbackResult<{ success: boolean; conversationId?: string }>> {
    return demoOnly({ success: true, conversationId: 'demo-convo-id' });
}
