import { useDarkMode } from '~/root';
import { Link } from '@remix-run/react';
import {
    ComputerDesktopIcon,
    CheckBadgeIcon,
    ArrowTopRightOnSquareIcon,
    StarIcon,
} from '@heroicons/react/24/solid';

export default function SoftwarePage() {
    const { darkMode } = useDarkMode();

    const software = [
        {
            name: "VCM Suite / VCM Editor",
            company: "HP Tuners",
            description: "Industry-leading tuning software for GM, Ford, Dodge, and more. VCM Suite includes VCM Editor for calibration editing and VCM Scanner for real-time diagnostics and data logging.",
            platforms: ["GM/Chevy", "Ford", "Dodge/Chrysler", "Some imports"],
            features: ["Full ECU calibration editing", "Real-time data logging", "Virtual dyno", "DTC management", "Custom OS support"],
            link: "https://www.hptuners.com",
            popular: true,
            rating: 5,
        },
        {
            name: "EFI Live",
            company: "EFI Live",
            description: "Professional-grade tuning software specializing in GM and Cummins diesel platforms. Known for its robust diesel tuning capabilities and transmission tuning.",
            platforms: ["GM Gas & Diesel", "Cummins Diesel", "Duramax"],
            features: ["Advanced diesel tuning", "Transmission tuning", "DSP switching", "AutoCal licensing", "Custom operating systems"],
            link: "https://www.efilive.com",
            popular: true,
            rating: 5,
        },
        {
            name: "SCT / Livewire",
            company: "SCT Performance",
            description: "Popular tuning solution for Ford vehicles. Offers both handheld tuners and professional software for custom calibration development.",
            platforms: ["Ford", "Some GM vehicles"],
            features: ["Custom tune creation", "Data logging", "Performance monitoring", "Canned tune library", "Cloud tune delivery"],
            link: "https://www.sctflash.com",
            popular: true,
            rating: 4,
        },
        {
            name: "Cobb Accessport",
            company: "Cobb Tuning",
            description: "All-in-one tuning solution popular with Subaru, Ford, and import enthusiasts. Combines hardware and software with excellent community support.",
            platforms: ["Subaru", "Ford Focus RS/ST", "VW/Audi", "Nissan GTR", "Porsche"],
            features: ["Plug-and-play tuning", "Real-time monitoring", "Data logging", "AccessTuner Pro", "OTS maps included"],
            link: "https://www.cobbtuning.com",
            popular: true,
            rating: 5,
        },
        {
            name: "Hondata FlashPro",
            company: "Hondata",
            description: "The go-to tuning solution for Honda and Acura vehicles. Offers real-time tuning, data logging, and boost control for turbocharged applications.",
            platforms: ["Honda", "Acura"],
            features: ["Real-time tuning", "Boost control", "Launch control", "Data logging", "Flex fuel support"],
            link: "https://www.hondata.com",
            rating: 5,
        },
        {
            name: "Ecutek",
            company: "Ecutek",
            description: "Professional ECU tuning software specializing in Subaru, Nissan, Mazda, and other Japanese platforms. Known for advanced features and reliability.",
            platforms: ["Subaru", "Nissan", "Mazda", "Toyota/Scion", "Mitsubishi"],
            features: ["RaceROM features", "Launch control", "Flex fuel", "Map switching", "Speed density"],
            link: "https://www.ecutek.com",
            rating: 5,
        },
        {
            name: "TunerStudio",
            company: "EFI Analytics",
            description: "Versatile tuning software for standalone ECUs and DIY projects. Supports MegaSquirt, Speeduino, and other open-source ECU platforms.",
            platforms: ["MegaSquirt", "Speeduino", "MS3Pro", "Custom ECUs"],
            features: ["VE tuning", "Spark tuning", "Data logging", "Auto-tune", "Gauge dashboards"],
            link: "https://www.tunerstudio.com",
            rating: 4,
        },
        {
            name: "HP Tuners Beta (nGauge)",
            company: "HP Tuners",
            description: "Next-generation interface for HP Tuners featuring touchscreen display, real-time gauges, and seamless integration with VCM Suite.",
            platforms: ["All HP Tuners supported vehicles"],
            features: ["Touchscreen interface", "Real-time gauges", "Quick tune changes", "Data logging", "Bluetooth connectivity"],
            link: "https://www.hptuners.com",
            rating: 4,
        },
    ];

    const tips = [
        {
            title: "License Credits",
            desc: "Most professional software requires purchasing license credits per vehicle. Plan accordingly when budgeting."
        },
        {
            title: "Platform Compatibility",
            desc: "Always verify your specific vehicle year and model is supported before purchasing software."
        },
        {
            title: "Learning Curve",
            desc: "Professional tuning software has steep learning curves. Invest time in training and community resources."
        },
        {
            title: "Updates & Support",
            desc: "Check that the software is actively maintained and offers good customer support."
        },
    ];

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-green-500/20 via-teal-500/10 to-green-500/20 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-32 px-6 flex flex-col items-center text-center">
                    <ComputerDesktopIcon className="w-20 h-20 text-green-500 mb-6" />
                    <h1 className="text-5xl sm:text-6xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-green-400 via-teal-500 to-green-500">
                            Recommended Software
                        </span>
                    </h1>
                    <p className="mt-6 text-xl text-gray-300 max-w-3xl">
                        Professional tuning software used by top tuners. Find the right tools for your platform and start creating custom calibrations.
                    </p>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            {/* Software Grid */}
            <section className="max-w-6xl mx-auto px-6 py-16">
                <div className="grid gap-8">
                    {software.map((item) => (
                        <div
                            key={item.name}
                            className={`${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl p-6 sm:p-8 hover:shadow-2xl hover:shadow-green-500/10 transition-all duration-300 ${item.popular ? 'ring-2 ring-green-500' : ''}`}
                        >
                            <div className="flex flex-col lg:flex-row lg:items-start gap-6">
                                <div className="flex-1">
                                    <div className="flex items-center gap-3 mb-2">
                                        <h3 className={`text-2xl font-bold ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                                            {item.name}
                                        </h3>
                                        {item.popular && (
                                            <span className="px-3 py-1 rounded-full bg-green-500/20 text-green-400 text-xs font-semibold">
                                                Popular
                                            </span>
                                        )}
                                    </div>
                                    <p className={`text-sm mb-4 ${darkMode ? 'text-gray-400' : 'text-gray-500'}`}>
                                        by {item.company}
                                    </p>
                                    <p className={`mb-4 ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>
                                        {item.description}
                                    </p>

                                    <div className="flex items-center gap-1 mb-4">
                                        {[...Array(5)].map((_, i) => (
                                            <StarIcon
                                                key={i}
                                                className={`w-5 h-5 ${i < item.rating ? 'text-yellow-400' : darkMode ? 'text-zinc-600' : 'text-gray-300'}`}
                                            />
                                        ))}
                                    </div>

                                    <div className="mb-4">
                                        <span className={`text-sm font-semibold ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                                            Supported Platforms:
                                        </span>
                                        <div className="flex flex-wrap gap-2 mt-2">
                                            {item.platforms.map((platform) => (
                                                <span
                                                    key={platform}
                                                    className={`px-3 py-1 rounded-full text-xs font-medium ${darkMode ? 'bg-zinc-700 text-gray-300' : 'bg-gray-100 text-gray-700'}`}
                                                >
                                                    {platform}
                                                </span>
                                            ))}
                                        </div>
                                    </div>

                                    <div>
                                        <span className={`text-sm font-semibold ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                                            Key Features:
                                        </span>
                                        <div className="flex flex-wrap gap-2 mt-2">
                                            {item.features.map((feature) => (
                                                <span key={feature} className="flex items-center gap-1">
                                                    <CheckBadgeIcon className="w-4 h-4 text-green-500" />
                                                    <span className={`text-sm ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>
                                                        {feature}
                                                    </span>
                                                </span>
                                            ))}
                                        </div>
                                    </div>
                                </div>

                                <div className="lg:flex-shrink-0">
                                    <a
                                        href={item.link}
                                        target="_blank"
                                        rel="noopener noreferrer"
                                        className="inline-flex items-center gap-2 px-6 py-3 bg-gradient-to-r from-green-500 to-teal-500 text-white font-semibold rounded-xl hover:shadow-lg hover:shadow-green-500/25 transition-all duration-300"
                                    >
                                        Visit Website
                                        <ArrowTopRightOnSquareIcon className="w-4 h-4" />
                                    </a>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </section>

            {/* Tips Section */}
            <section className={`py-16 ${darkMode ? 'bg-zinc-800/30' : 'bg-gray-50'}`}>
                <div className="max-w-6xl mx-auto px-6">
                    <h2 className={`text-3xl font-bold text-center mb-12 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                        Before You Buy
                    </h2>
                    <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6">
                        {tips.map((tip) => (
                            <div
                                key={tip.title}
                                className={`${darkMode ? 'bg-zinc-800 border-zinc-700' : 'bg-white border-gray-200'} border rounded-xl p-6`}
                            >
                                <h3 className={`font-bold mb-2 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                                    {tip.title}
                                </h3>
                                <p className={`text-sm ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                                    {tip.desc}
                                </p>
                            </div>
                        ))}
                    </div>
                </div>
            </section>

            {/* CTA */}
            <section className="max-w-6xl mx-auto px-6 py-16">
                <div className={`${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl p-8 text-center`}>
                    <h2 className={`text-2xl font-bold mb-4 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                        Need Hardware Too?
                    </h2>
                    <p className={`mb-6 ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                        Check out our recommended tuning hardware and interfaces.
                    </p>
                    <Link
                        to="/hardware"
                        className="inline-block px-8 py-4 bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500 text-white font-bold rounded-xl shadow-lg hover:shadow-2xl hover:shadow-red-500/25 transition-all duration-300"
                    >
                        View Recommended Hardware
                    </Link>
                </div>
            </section>
        </div>
    );
}
