import {
    CreditCardIcon,
    TruckIcon,
    PlusIcon,
    CheckCircleIcon,
    XCircleIcon,
} from '@heroicons/react/24/solid';
import { useBuyerAdminStyles } from './useBuyerAdminStyles';
import { buyerConnections } from '~/data/buyer-demo-data';

export function ConnectionsTab() {
    const { darkMode, cardClasses, textPrimary, textSecondary } = useBuyerAdminStyles();

    const paymentMethods = buyerConnections.filter(c => c.type === 'payment');
    const vehicles = buyerConnections.filter(c => c.type === 'vehicle');

    return (
        <div className="space-y-6">
            <h2 className={`text-2xl font-bold ${textPrimary}`}>Connections</h2>

            {/* Payment Methods */}
            <div className={cardClasses}>
                <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex items-center justify-between`}>
                    <h3 className={`font-semibold ${textPrimary}`}>Payment Methods</h3>
                    <button className="px-3 py-1.5 bg-blue-500 hover:bg-blue-600 text-white text-sm rounded-lg transition-colors flex items-center gap-1">
                        <PlusIcon className="w-4 h-4" />
                        Add Payment
                    </button>
                </div>
                <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                    {paymentMethods.map((method, idx) => (
                        <div key={idx} className="p-4 flex items-center justify-between">
                            <div className="flex items-center gap-4">
                                <div className={`w-12 h-12 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'} flex items-center justify-center`}>
                                    <CreditCardIcon className="w-6 h-6 text-blue-500" />
                                </div>
                                <div>
                                    <p className={`font-medium ${textPrimary}`}>{method.name}</p>
                                    {method.details && <p className={`text-sm ${textSecondary}`}>{method.details}</p>}
                                    {method.lastUsed && <p className={`text-xs ${textSecondary}`}>Last used: {method.lastUsed}</p>}
                                </div>
                            </div>
                            <div className="flex items-center gap-3">
                                {method.status === 'connected' ? (
                                    <span className="flex items-center gap-1 text-green-500 text-sm">
                                        <CheckCircleIcon className="w-4 h-4" />
                                        Connected
                                    </span>
                                ) : (
                                    <span className="flex items-center gap-1 text-gray-500 text-sm">
                                        <XCircleIcon className="w-4 h-4" />
                                        Not Connected
                                    </span>
                                )}
                                <button className={`px-3 py-1 text-sm ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                                    Manage
                                </button>
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Linked Vehicles */}
            <div className={cardClasses}>
                <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex items-center justify-between`}>
                    <h3 className={`font-semibold ${textPrimary}`}>Linked Vehicles</h3>
                    <button className="px-3 py-1.5 bg-blue-500 hover:bg-blue-600 text-white text-sm rounded-lg transition-colors flex items-center gap-1">
                        <PlusIcon className="w-4 h-4" />
                        Add Vehicle
                    </button>
                </div>
                <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                    {vehicles.map((vehicle, idx) => (
                        <div key={idx} className="p-4 flex items-center justify-between">
                            <div className="flex items-center gap-4">
                                <div className={`w-12 h-12 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'} flex items-center justify-center`}>
                                    <TruckIcon className="w-6 h-6 text-orange-500" />
                                </div>
                                <div>
                                    <p className={`font-medium ${textPrimary}`}>{vehicle.name}</p>
                                    {vehicle.details && <p className={`text-sm ${textSecondary}`}>{vehicle.details}</p>}
                                </div>
                            </div>
                            <div className="flex items-center gap-3">
                                <span className="flex items-center gap-1 text-green-500 text-sm">
                                    <CheckCircleIcon className="w-4 h-4" />
                                    Verified
                                </span>
                                <button className={`px-3 py-1 text-sm ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                                    Edit
                                </button>
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Saved Addresses */}
            <div className={cardClasses}>
                <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex items-center justify-between`}>
                    <h3 className={`font-semibold ${textPrimary}`}>Shipping Addresses</h3>
                    <button className="px-3 py-1.5 bg-blue-500 hover:bg-blue-600 text-white text-sm rounded-lg transition-colors flex items-center gap-1">
                        <PlusIcon className="w-4 h-4" />
                        Add Address
                    </button>
                </div>
                <div className="p-4">
                    <div className={`p-4 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'} flex items-start justify-between`}>
                        <div>
                            <div className="flex items-center gap-2 mb-1">
                                <p className={`font-medium ${textPrimary}`}>Home</p>
                                <span className="px-2 py-0.5 text-xs bg-blue-500/20 text-blue-500 rounded">Default</span>
                            </div>
                            <p className={textSecondary}>123 Main Street</p>
                            <p className={textSecondary}>Denver, CO 80202</p>
                            <p className={textSecondary}>United States</p>
                        </div>
                        <button className={`px-3 py-1 text-sm ${darkMode ? 'bg-zinc-600 hover:bg-zinc-500' : 'bg-gray-200 hover:bg-gray-300'} ${textPrimary} rounded-lg transition-colors`}>
                            Edit
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
