import { useState } from 'react';
import {
    CpuChipIcon,
    ArrowDownTrayIcon,
    ArrowPathIcon,
    CheckBadgeIcon,
    BoltIcon,
} from '@heroicons/react/24/solid';
import { useBuyerAdminStyles } from './useBuyerAdminStyles';
import { buyerTunes } from '~/data/buyer-demo-data';

export function TunesTab() {
    const { darkMode, cardClasses, textPrimary, textSecondary } = useBuyerAdminStyles();
    const [selectedTune, setSelectedTune] = useState<string | null>(null);

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <h2 className={`text-2xl font-bold ${textPrimary}`}>My Tunes</h2>
                <button className="px-4 py-2 bg-purple-500 hover:bg-purple-600 text-white rounded-lg transition-colors">
                    Browse Tunes
                </button>
            </div>

            <div className="grid lg:grid-cols-3 gap-6">
                {/* Tunes List */}
                <div className={`${cardClasses} lg:col-span-1`}>
                    <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                        <h3 className={`font-semibold ${textPrimary}`}>Purchased Tunes</h3>
                    </div>
                    <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                        {buyerTunes.map((tune) => (
                            <div
                                key={tune.id}
                                onClick={() => setSelectedTune(tune.id)}
                                className={`p-4 cursor-pointer transition-colors ${
                                    selectedTune === tune.id
                                        ? darkMode ? 'bg-zinc-700' : 'bg-gray-100'
                                        : `${darkMode ? 'hover:bg-zinc-700/50' : 'hover:bg-gray-50'}`
                                }`}
                            >
                                <div className="flex items-center justify-between mb-2">
                                    <span className={`font-medium ${textPrimary}`}>{tune.name}</span>
                                    <span className={`text-xs px-2 py-0.5 rounded ${
                                        tune.status === 'active' ? 'bg-green-500/20 text-green-500' :
                                        tune.status === 'available-update' ? 'bg-blue-500/20 text-blue-500' :
                                        'bg-gray-500/20 text-gray-500'
                                    }`}>{tune.status === 'available-update' ? 'Update Available' : tune.status}</span>
                                </div>
                                <p className={`text-sm ${textSecondary}`}>{tune.vehicle}</p>
                                <p className={`text-xs ${textSecondary}`}>v{tune.version}</p>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Tune Details */}
                <div className={`${cardClasses} lg:col-span-2`}>
                    {selectedTune ? (() => {
                        const tune = buyerTunes.find(t => t.id === selectedTune)!;
                        return (
                            <>
                                <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                                    <div className="flex items-center justify-between">
                                        <div className="flex items-center gap-3">
                                            <div className="w-12 h-12 rounded-lg bg-purple-500/20 flex items-center justify-center">
                                                <CpuChipIcon className="w-6 h-6 text-purple-500" />
                                            </div>
                                            <div>
                                                <h3 className={`font-semibold ${textPrimary}`}>{tune.name}</h3>
                                                <p className={`text-sm ${textSecondary}`}>{tune.vehicle}</p>
                                            </div>
                                        </div>
                                        <span className={`text-sm ${textSecondary}`}>v{tune.version}</span>
                                    </div>
                                </div>
                                <div className="p-4 space-y-4">
                                    {/* Seller */}
                                    <div>
                                        <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Tuner</h4>
                                        <div className="flex items-center gap-3">
                                            <div className={`w-10 h-10 rounded-full bg-purple-500 flex items-center justify-center text-white font-medium`}>
                                                {tune.seller.initials}
                                            </div>
                                            <div>
                                                <div className="flex items-center gap-1">
                                                    <p className={textPrimary}>{tune.seller.name}</p>
                                                    {tune.seller.verified && (
                                                        <CheckBadgeIcon className="w-4 h-4 text-blue-500" />
                                                    )}
                                                </div>
                                                <button className="text-sm text-purple-500 hover:text-purple-400">View Profile</button>
                                            </div>
                                        </div>
                                    </div>

                                    {/* Specs */}
                                    <div>
                                        <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Performance Specs</h4>
                                        <div className="grid grid-cols-3 gap-3">
                                            <div className={`p-3 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'} text-center`}>
                                                <BoltIcon className="w-5 h-5 text-yellow-500 mx-auto mb-1" />
                                                <p className={`text-lg font-bold ${textPrimary}`}>{tune.specs.hp}</p>
                                                <p className={`text-xs ${textSecondary}`}>Horsepower</p>
                                            </div>
                                            <div className={`p-3 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'} text-center`}>
                                                <BoltIcon className="w-5 h-5 text-orange-500 mx-auto mb-1" />
                                                <p className={`text-lg font-bold ${textPrimary}`}>{tune.specs.torque}</p>
                                                <p className={`text-xs ${textSecondary}`}>lb-ft Torque</p>
                                            </div>
                                            <div className={`p-3 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'} text-center`}>
                                                <span className="text-lg">⛽</span>
                                                <p className={`text-sm font-bold ${textPrimary}`}>{tune.specs.fuel}</p>
                                                <p className={`text-xs ${textSecondary}`}>Fuel Type</p>
                                            </div>
                                        </div>
                                    </div>

                                    {/* Purchase Info */}
                                    <div>
                                        <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Purchase Info</h4>
                                        <div className={`p-3 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'}`}>
                                            <p className={textPrimary}>Purchased: {tune.purchaseDate}</p>
                                            <p className={`text-sm ${textSecondary}`}>Version: {tune.version}</p>
                                        </div>
                                    </div>

                                    <div className="flex gap-3 pt-4">
                                        <button className="px-4 py-2 bg-purple-500 hover:bg-purple-600 text-white rounded-lg transition-colors flex items-center gap-2">
                                            <ArrowDownTrayIcon className="w-4 h-4" />
                                            Download Tune
                                        </button>
                                        {tune.status === 'available-update' && (
                                            <button className="px-4 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded-lg transition-colors flex items-center gap-2">
                                                <ArrowPathIcon className="w-4 h-4" />
                                                Update Available
                                            </button>
                                        )}
                                        <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                                            Contact Tuner
                                        </button>
                                    </div>
                                </div>
                            </>
                        );
                    })() : (
                        <div className="flex-1 flex items-center justify-center p-12">
                            <div className="text-center">
                                <CpuChipIcon className={`w-16 h-16 mx-auto mb-4 ${textSecondary}`} />
                                <p className={textSecondary}>Select a tune to view details</p>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
