import { PhotoIcon } from '@heroicons/react/24/solid';
import { useSellerAdminStyles } from './useSellerAdminStyles';

export function ListTuneTab() {
    const { darkMode, cardClasses, inputClasses, textPrimary, textSecondary } = useSellerAdminStyles();

    return (
        <div className="space-y-6">
            <h2 className={`text-2xl font-bold ${textPrimary}`}>List New Tune</h2>

            <div className={cardClasses}>
                <div className="p-6 space-y-6">
                    <div className="grid md:grid-cols-2 gap-4">
                        <div>
                            <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Tune Name *</label>
                            <input type="text" placeholder="e.g., Stage 2 LS3 Performance Tune" className={inputClasses} />
                        </div>
                        <div>
                            <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Price *</label>
                            <input type="number" placeholder="349.99" className={inputClasses} />
                        </div>
                    </div>

                    <div>
                        <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Description *</label>
                        <textarea rows={4} placeholder="Describe your tune, what it includes, expected gains, etc." className={inputClasses} />
                    </div>

                    <div className="grid md:grid-cols-2 gap-4">
                        <div>
                            <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Platform/Vehicle *</label>
                            <input type="text" placeholder="e.g., GM LS3/L99 (2010-2015 Camaro)" className={inputClasses} />
                        </div>
                        <div>
                            <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Required Software</label>
                            <select className={inputClasses}>
                                <option>HP Tuners</option>
                                <option>EFI Live</option>
                                <option>SCT</option>
                                <option>Other</option>
                            </select>
                        </div>
                    </div>

                    <div className="grid md:grid-cols-2 gap-4">
                        <div>
                            <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Expected HP Gain</label>
                            <input type="text" placeholder="e.g., +45 HP" className={inputClasses} />
                        </div>
                        <div>
                            <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Expected Torque Gain</label>
                            <input type="text" placeholder="e.g., +40 lb-ft" className={inputClasses} />
                        </div>
                    </div>

                    <div>
                        <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Required Modifications</label>
                        <input type="text" placeholder="e.g., CAI, Headers, Exhaust (or 'Stock')'" className={inputClasses} />
                    </div>

                    <div>
                        <label className={`block text-sm font-medium mb-1 ${textSecondary}`}>Featured Image</label>
                        <div className={`border-2 border-dashed ${darkMode ? 'border-zinc-600' : 'border-gray-300'} rounded-xl p-8 text-center`}>
                            <PhotoIcon className={`w-12 h-12 mx-auto mb-4 ${textSecondary}`} />
                            <p className={textSecondary}>Drag and drop an image or click to upload</p>
                            <button className={`mt-4 px-4 py-2 ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'} rounded-lg ${textPrimary}`}>
                                Browse Files
                            </button>
                        </div>
                    </div>

                    <div className="flex items-center gap-4 pt-4">
                        <button className="px-6 py-3 bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500 text-white font-bold rounded-xl hover:shadow-lg transition-all">
                            Publish Tune
                        </button>
                        <button className={`px-6 py-3 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} font-medium rounded-xl transition-all`}>
                            Save as Draft
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
