import { Link } from '@remix-run/react';
import { ShoppingBagIcon, SunIcon, MoonIcon } from '@heroicons/react/24/outline';
import { SearchBar } from '~/components/header/SearchBar';
import { useRootLoader } from '~/utils/use-root-loader';
import { UserIcon } from '@heroicons/react/24/solid';
import { useScrollingUp } from '~/utils/use-scrolling-up';
import { classNames } from '~/utils/class-names';
import { useTranslation } from 'react-i18next';
import { useState, useRef } from "react";
import { useDarkMode } from '~/root';

export function Header({
    onCartIconClick,
    cartQuantity,
}: {
    onCartIconClick: () => void;
    cartQuantity: number;
}) {
    const data = useRootLoader();
    const isSignedIn = !!data.activeCustomer.activeCustomer?.id;
    const isScrollingUp = useScrollingUp();
    const { t } = useTranslation();
    const { darkMode, toggleDarkMode } = useDarkMode();

    // Unified dropdown state
    const [openMenu, setOpenMenu] = useState<string | null>(null);
    const closeTimer = useRef<NodeJS.Timeout | null>(null);

    const openDropdown = (key: string) => {
        if (closeTimer.current) clearTimeout(closeTimer.current);
        setOpenMenu(key);
    };

    const delayCloseDropdown = () => {
        if (closeTimer.current) clearTimeout(closeTimer.current);
        closeTimer.current = setTimeout(() => {
            setOpenMenu(null);
        }, 800);
    };

    return (
        <header
            className={classNames(
                isScrollingUp ? 'sticky top-0 z-10 animate-dropIn' : '',
                'bg-gradient-to-r from-gray-900 via-zinc-900 to-black shadow-lg'
            )}
        >
            {/* TOP BAR - Promo Banner */}
            <div className="bg-black text-gray-400 shadow-inner text-center text-sm py-2 px-2 xl:px-0">
                <div className="max-w-6xl mx-2 md:mx-auto flex items-center justify-center">
                    <p>
                        {t('vendure.exclusive')}{' '}
                        <a href="/tunersignup" target="_blank" className="text-red-400 hover:text-red-300">
                            {t('vendure.repoLinkLabel')}
                        </a>
                    </p>
                </div>
            </div>

            {/* MAIN HEADER */}
            <div className="max-w-6xl mx-auto p-4 flex items-center space-x-4">
                <h1 className="text-white w-10">
                    <Link to="/">
                        <img
                            src="/cube-logo-small.webp"
                            width={40}
                            height={31}
                            alt={t('commmon.logoAlt')}
                        />
                    </Link>
                </h1>

                {/* NAV LINKS */}
                <div className="hidden sm:flex space-x-4">

                    {/* ======================= SHOP DROPDOWN ======================= */}
                    <div
                        className="relative"
                        onMouseEnter={() => openDropdown("shop")}
                        onMouseLeave={delayCloseDropdown}
                    >
                        <Link to="/collections" className="text-sm md:text-base text-gray-200 hover:text-white">
                            Shop
                        </Link>

                        {openMenu === "shop" && (
                            <div className="absolute left-0 mt-2 w-56 bg-zinc-900 border border-zinc-700 rounded-xl shadow-xl p-4 z-50">
                                <Link
                                    to="/collections"
                                    className="block text-gray-300 hover:text-white text-sm mb-3 pb-3 border-b border-zinc-700"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Browse All
                                </Link>
                                <Link
                                    to="/tunes"
                                    className="block text-gray-300 hover:text-white text-sm mb-2"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Browse Tunes
                                </Link>
                                <Link
                                    to="/sellers"
                                    className="block text-gray-300 hover:text-white text-sm mb-2"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Browse Tuners
                                </Link>
                                <Link
                                    to="/parts"
                                    className="block text-gray-300 hover:text-white text-sm mb-2"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Performance Parts
                                </Link>
                                <Link
                                    to="/services"
                                    className="block text-gray-300 hover:text-white text-sm"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    In-Person Services
                                </Link>
                            </div>
                        )}
                    </div>

                    {/* ======================= LEARN DROPDOWN ======================= */}
                    <div
                        className="relative"
                        onMouseEnter={() => openDropdown("learn")}
                        onMouseLeave={delayCloseDropdown}
                    >
                        <Link to="/learn" className="text-sm md:text-base text-gray-200 hover:text-white">
                            Learn
                        </Link>

                        {openMenu === "learn" && (
                            <div className="absolute left-0 mt-2 w-56 bg-zinc-900 border border-zinc-700 rounded-xl shadow-xl p-4 z-50">
                                <Link
                                    to="/learn"
                                    className="block text-gray-300 hover:text-white text-sm mb-3 pb-3 border-b border-zinc-700"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Learning Hub
                                </Link>
                                <Link
                                    to="/how"
                                    className="block text-gray-300 hover:text-white text-sm mb-2"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    How It Works
                                </Link>
                                <Link
                                    to="/software"
                                    className="block text-gray-300 hover:text-white text-sm mb-2"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Recommended Software
                                </Link>
                                <Link
                                    to="/hardware"
                                    className="block text-gray-300 hover:text-white text-sm mb-2"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Recommended Hardware
                                </Link>
                                <Link
                                    to="/disclaimer"
                                    className="block text-gray-300 hover:text-white text-sm mb-2"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Tuning Disclaimer
                                </Link>
                                <Link
                                    to="/about"
                                    className="block text-gray-300 hover:text-white text-sm"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    About Us
                                </Link>
                            </div>
                        )}
                    </div>

                    {/* ======================= COMMUNITY DROPDOWN ======================= */}
                    <div
                        className="relative"
                        onMouseEnter={() => openDropdown("community")}
                        onMouseLeave={delayCloseDropdown}
                    >
                        <Link to="/community" className="text-sm md:text-base text-gray-200 hover:text-white">
                            Community
                        </Link>

                        {openMenu === "community" && (
                            <div className="absolute left-0 mt-2 w-56 bg-zinc-900 border border-zinc-700 rounded-xl shadow-xl p-4 z-50">
                                <Link
                                    to="/community"
                                    className="block text-gray-300 hover:text-white text-sm mb-3 pb-3 border-b border-zinc-700"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Community Hub
                                </Link>
                                <Link
                                    to="/forums"
                                    className="block text-gray-300 hover:text-white text-sm mb-2"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Forums
                                </Link>
                                <Link
                                    to="/blog"
                                    className="block text-gray-300 hover:text-white text-sm"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Blog
                                </Link>
                            </div>
                        )}
                    </div>

                    {/* ======================= DEV DROPDOWN ======================= */}
                    <div
                        className="relative"
                        onMouseEnter={() => openDropdown("dev")}
                        onMouseLeave={delayCloseDropdown}
                    >
                        <Link to="/dev" className="text-sm md:text-base text-gray-200 hover:text-white">
                            Dev
                        </Link>

                        {openMenu === "dev" && (
                            <div className="absolute left-0 mt-2 w-56 bg-zinc-900 border border-zinc-700 rounded-xl shadow-xl p-4 z-50">
                                <Link
                                    to="/dev"
                                    className="block text-gray-300 hover:text-white text-sm mb-3 pb-3 border-b border-zinc-700"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Developer Hub
                                </Link>
                                <Link
                                    to="/dev/docs"
                                    className="block text-gray-300 hover:text-white text-sm mb-2"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Documentation
                                </Link>
                                <Link
                                    to="/dev/roadmap"
                                    className="block text-gray-300 hover:text-white text-sm mb-2"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Roadmap
                                </Link>
                                <Link
                                    to="/dev/changelog"
                                    className="block text-gray-300 hover:text-white text-sm mb-2"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Changelog
                                </Link>
                                <Link
                                    to="/dev/contribute"
                                    className="block text-gray-300 hover:text-white text-sm mb-2"
                                    onClick={() => setOpenMenu(null)}
                                >
                                    Contribute
                                </Link>
                            </div>
                        )}
                    </div>

                    {/* Static links (non-dropdown) */}
                    <Link className="text-sm md:text-base text-gray-200 hover:text-white" to="/contact">
                        Contact
                    </Link>
                </div>

                {/* SEARCH BOX */}
                <div className="flex-1 md:pr-8">
                    <SearchBar />
                </div>

                {/* USER SIGN IN / ACCOUNT */}
                <Link
                    to={isSignedIn ? '/account' : '/sign-in'}
                    className="flex items-center gap-2 px-3 py-2 bg-white bg-opacity-20 rounded text-white text-sm hover:bg-opacity-30 transition-all"
                >
                    <UserIcon className="w-4 h-4" />
                    <span className="hidden md:inline">
                        {isSignedIn ? t('account.myAccount') : t('account.signIn')}
                    </span>
                </Link>

                {/* DARK MODE TOGGLE */}
                <button
                    className="w-9 h-9 bg-white bg-opacity-20 rounded text-white p-1 hover:bg-opacity-30 transition-all"
                    onClick={toggleDarkMode}
                    aria-label="Toggle dark mode"
                >
                    {darkMode ? (
                        <SunIcon className="w-full h-full text-yellow-400" />
                    ) : (
                        <MoonIcon className="w-full h-full" />
                    )}
                </button>

                {/* CART */}
                <button
                    className="relative w-9 h-9 bg-white bg-opacity-20 rounded text-white p-1 hover:bg-opacity-30 transition-all"
                    onClick={onCartIconClick}
                    aria-label="Open cart tray"
                >
                    <ShoppingBagIcon />
                    {cartQuantity ? (
                        <div className="absolute rounded-full -top-2 -right-2 bg-red-500 min-w-6 min-h-6 flex items-center justify-center text-xs p-1">
                            {cartQuantity}
                        </div>
                    ) : null}
                </button>
            </div>
        </header>
    );
}
