import { useState, useEffect } from 'react';
import { useFetcher } from '@remix-run/react';
import { PlusIcon, TrashIcon, StarIcon } from '@heroicons/react/24/solid';
import { useBuyerAdminStyles } from './useBuyerAdminStyles';
import { type BuyerData, type Vehicle } from '~/data/buyer-demo-data';

// Empty default profile - no demo data
const emptyProfile: BuyerData = {
    name: '',
    initials: '',
    email: '',
    phone: '',
    location: '',
    memberSince: '',
    vehicles: [],
};

interface ProfileTabProps {
    buyer?: BuyerData;
    onSave?: (data: Partial<BuyerData>) => void;
}

export function ProfileTab({ buyer }: ProfileTabProps) {
    const { darkMode, cardClasses, inputClasses, textPrimary, textSecondary } = useBuyerAdminStyles();
    const [profile, setProfile] = useState(buyer || emptyProfile);
    const fetcher = useFetcher();

    // Update profile when buyer prop changes
    useEffect(() => {
        if (buyer) {
            setProfile(buyer);
        }
    }, [buyer]);

    return (
        <div className="space-y-6">
            <h2 className={`text-2xl font-bold ${textPrimary}`}>My Profile</h2>

            <div className="grid lg:grid-cols-3 gap-6">
                {/* Profile Info */}
                <div className={`${cardClasses} lg:col-span-2`}>
                    <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                        <h3 className={`font-semibold ${textPrimary}`}>Personal Information</h3>
                    </div>
                    <div className="p-4 space-y-4">
                        <div className="grid md:grid-cols-2 gap-4">
                            <div>
                                <label className={`block text-sm font-medium mb-2 ${textSecondary}`}>Full Name</label>
                                <input
                                    type="text"
                                    value={profile.name}
                                    onChange={(e) => setProfile({ ...profile, name: e.target.value })}
                                    className={inputClasses}
                                />
                            </div>
                            <div>
                                <label className={`block text-sm font-medium mb-2 ${textSecondary}`}>Email</label>
                                <input
                                    type="email"
                                    value={profile.email}
                                    onChange={(e) => setProfile({ ...profile, email: e.target.value })}
                                    className={inputClasses}
                                />
                            </div>
                        </div>
                        <div className="grid md:grid-cols-2 gap-4">
                            <div>
                                <label className={`block text-sm font-medium mb-2 ${textSecondary}`}>Phone</label>
                                <input
                                    type="tel"
                                    value={profile.phone}
                                    onChange={(e) => setProfile({ ...profile, phone: e.target.value })}
                                    className={inputClasses}
                                />
                            </div>
                            <div>
                                <label className={`block text-sm font-medium mb-2 ${textSecondary}`}>Location</label>
                                <input
                                    type="text"
                                    value={profile.location}
                                    onChange={(e) => setProfile({ ...profile, location: e.target.value })}
                                    className={inputClasses}
                                />
                            </div>
                        </div>
                        <div className="pt-4">
                            <fetcher.Form method="post" action="/buyeradmin/profile">
                                <input type="hidden" name="intent" value="updateProfile" />
                                <input type="hidden" name="name" value={profile.name} />
                                <input type="hidden" name="email" value={profile.email} />
                                <input type="hidden" name="phone" value={profile.phone} />
                                <input type="hidden" name="location" value={profile.location} />
                                <button
                                    type="submit"
                                    disabled={fetcher.state !== 'idle'}
                                    className="px-4 py-2 bg-blue-500 hover:bg-blue-600 disabled:bg-blue-400 text-white rounded-lg transition-colors"
                                >
                                    {fetcher.state !== 'idle' ? 'Saving...' : 'Save Changes'}
                                </button>
                            </fetcher.Form>
                        </div>
                    </div>
                </div>

                {/* Profile Card */}
                <div className={cardClasses}>
                    <div className="p-6 text-center">
                        <div className="w-24 h-24 mx-auto rounded-full bg-gradient-to-br from-blue-400 via-purple-500 to-pink-500 flex items-center justify-center text-white text-2xl font-bold mb-4">
                            {profile.initials}
                        </div>
                        <h3 className={`text-xl font-bold ${textPrimary}`}>{profile.name}</h3>
                        <p className={`text-sm ${textSecondary}`}>{profile.location}</p>
                        <p className={`text-sm ${textSecondary} mt-1`}>Member since {profile.memberSince}</p>
                    </div>
                </div>
            </div>

            {/* Vehicles */}
            <div className={cardClasses}>
                <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex items-center justify-between`}>
                    <h3 className={`font-semibold ${textPrimary}`}>My Vehicles</h3>
                    <button className="px-3 py-1.5 bg-blue-500 hover:bg-blue-600 text-white text-sm rounded-lg transition-colors flex items-center gap-1">
                        <PlusIcon className="w-4 h-4" />
                        Add Vehicle
                    </button>
                </div>
                <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                    {profile.vehicles.map((vehicle) => (
                        <div key={vehicle.id} className="p-4 flex items-center justify-between">
                            <div className="flex items-center gap-4">
                                <div className={`w-12 h-12 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'} flex items-center justify-center`}>
                                    <span className="text-2xl">🚗</span>
                                </div>
                                <div>
                                    <div className="flex items-center gap-2">
                                        <p className={`font-medium ${textPrimary}`}>
                                            {vehicle.year} {vehicle.make} {vehicle.model}
                                        </p>
                                        {vehicle.isPrimary && (
                                            <span className="px-2 py-0.5 text-xs bg-blue-500/20 text-blue-500 rounded">Primary</span>
                                        )}
                                    </div>
                                    {vehicle.trim && <p className={`text-sm ${textSecondary}`}>{vehicle.trim}</p>}
                                    {vehicle.mileage && (
                                        <p className={`text-sm ${textSecondary}`}>{vehicle.mileage.toLocaleString()} miles</p>
                                    )}
                                </div>
                            </div>
                            <div className="flex items-center gap-2">
                                <button className={`p-2 rounded-lg ${darkMode ? 'hover:bg-zinc-700' : 'hover:bg-gray-100'} transition-colors`}>
                                    <StarIcon className={`w-5 h-5 ${vehicle.isPrimary ? 'text-yellow-500' : textSecondary}`} />
                                </button>
                                <button className={`p-2 rounded-lg hover:bg-red-500/20 transition-colors`}>
                                    <TrashIcon className="w-5 h-5 text-red-500" />
                                </button>
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Account Settings */}
            <div className={cardClasses}>
                <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                    <h3 className={`font-semibold ${textPrimary}`}>Account Settings</h3>
                </div>
                <div className="p-4 space-y-4">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className={`font-medium ${textPrimary}`}>Email Notifications</p>
                            <p className={`text-sm ${textSecondary}`}>Receive order updates and promotions</p>
                        </div>
                        <label className="relative inline-flex items-center cursor-pointer">
                            <input type="checkbox" defaultChecked className="sr-only peer" />
                            <div className="w-11 h-6 bg-gray-600 peer-focus:ring-2 peer-focus:ring-blue-500 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-blue-500"></div>
                        </label>
                    </div>
                    <div className="flex items-center justify-between">
                        <div>
                            <p className={`font-medium ${textPrimary}`}>SMS Notifications</p>
                            <p className={`text-sm ${textSecondary}`}>Get text alerts for appointments</p>
                        </div>
                        <label className="relative inline-flex items-center cursor-pointer">
                            <input type="checkbox" className="sr-only peer" />
                            <div className="w-11 h-6 bg-gray-600 peer-focus:ring-2 peer-focus:ring-blue-500 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-blue-500"></div>
                        </label>
                    </div>
                    <div className="pt-4 border-t border-zinc-700">
                        <button className="text-red-500 hover:text-red-400 text-sm">Delete Account</button>
                    </div>
                </div>
            </div>
        </div>
    );
}
