import { useState } from 'react';
import {
    WrenchScrewdriverIcon,
    TruckIcon,
    MapPinIcon,
    ShieldCheckIcon,
} from '@heroicons/react/24/solid';
import { useBuyerAdminStyles } from './useBuyerAdminStyles';
import { buyerParts } from '~/data/buyer-demo-data';

export function PartsTab() {
    const { darkMode, cardClasses, textPrimary, textSecondary } = useBuyerAdminStyles();
    const [selectedPart, setSelectedPart] = useState<string | null>(null);

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <h2 className={`text-2xl font-bold ${textPrimary}`}>My Parts</h2>
                <button className="px-4 py-2 bg-orange-500 hover:bg-orange-600 text-white rounded-lg transition-colors">
                    Shop Parts
                </button>
            </div>

            <div className="grid lg:grid-cols-3 gap-6">
                {/* Parts List */}
                <div className={`${cardClasses} lg:col-span-1`}>
                    <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                        <h3 className={`font-semibold ${textPrimary}`}>Ordered Parts</h3>
                    </div>
                    <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                        {buyerParts.map((part) => (
                            <div
                                key={part.id}
                                onClick={() => setSelectedPart(part.id)}
                                className={`p-4 cursor-pointer transition-colors ${
                                    selectedPart === part.id
                                        ? darkMode ? 'bg-zinc-700' : 'bg-gray-100'
                                        : `${darkMode ? 'hover:bg-zinc-700/50' : 'hover:bg-gray-50'}`
                                }`}
                            >
                                <div className="flex items-center justify-between mb-2">
                                    <span className={`font-medium ${textPrimary}`}>{part.name}</span>
                                    <span className={`text-xs px-2 py-0.5 rounded ${
                                        part.status === 'delivered' ? 'bg-green-500/20 text-green-500' :
                                        part.status === 'shipped' ? 'bg-blue-500/20 text-blue-500' :
                                        'bg-yellow-500/20 text-yellow-500'
                                    }`}>{part.status}</span>
                                </div>
                                <p className={`text-sm ${textSecondary}`}>{part.vehicle}</p>
                                <p className={`text-xs ${textSecondary}`}>${part.price}</p>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Part Details */}
                <div className={`${cardClasses} lg:col-span-2`}>
                    {selectedPart ? (() => {
                        const part = buyerParts.find(p => p.id === selectedPart)!;
                        return (
                            <>
                                <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                                    <div className="flex items-center justify-between">
                                        <div className="flex items-center gap-3">
                                            <div className="w-12 h-12 rounded-lg bg-orange-500/20 flex items-center justify-center">
                                                <WrenchScrewdriverIcon className="w-6 h-6 text-orange-500" />
                                            </div>
                                            <div>
                                                <h3 className={`font-semibold ${textPrimary}`}>{part.name}</h3>
                                                <p className={`text-sm ${textSecondary}`}>{part.vehicle}</p>
                                            </div>
                                        </div>
                                        <span className={`text-lg font-bold ${textPrimary}`}>${part.price}</span>
                                    </div>
                                </div>
                                <div className="p-4 space-y-4">
                                    {/* Seller */}
                                    <div>
                                        <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Seller</h4>
                                        <div className="flex items-center gap-3">
                                            <div className={`w-10 h-10 rounded-full bg-orange-500 flex items-center justify-center text-white font-medium`}>
                                                {part.seller.initials}
                                            </div>
                                            <div>
                                                <p className={textPrimary}>{part.seller.name}</p>
                                                <button className="text-sm text-orange-500 hover:text-orange-400">View Store</button>
                                            </div>
                                        </div>
                                    </div>

                                    {/* Order Status */}
                                    <div>
                                        <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Order Status</h4>
                                        <div className={`p-3 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'}`}>
                                            <div className="flex items-center gap-2 mb-2">
                                                <TruckIcon className={`w-5 h-5 ${
                                                    part.status === 'delivered' ? 'text-green-500' :
                                                    part.status === 'shipped' ? 'text-blue-500' :
                                                    'text-yellow-500'
                                                }`} />
                                                <p className={`font-medium ${textPrimary} capitalize`}>{part.status}</p>
                                            </div>
                                            {part.tracking && (
                                                <div className="flex items-start gap-2 mt-2">
                                                    <MapPinIcon className="w-4 h-4 text-orange-500 mt-0.5" />
                                                    <div>
                                                        <p className={`text-sm ${textSecondary}`}>Tracking:</p>
                                                        <p className={textPrimary}>{part.tracking}</p>
                                                    </div>
                                                </div>
                                            )}
                                        </div>
                                    </div>

                                    {/* Warranty */}
                                    {part.warranty && (
                                        <div>
                                            <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Warranty</h4>
                                            <div className={`p-3 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'}`}>
                                                <div className="flex items-center gap-2">
                                                    <ShieldCheckIcon className="w-5 h-5 text-green-500" />
                                                    <p className={textPrimary}>{part.warranty} Warranty</p>
                                                </div>
                                            </div>
                                        </div>
                                    )}

                                    {/* Purchase Info */}
                                    <div>
                                        <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Purchase Info</h4>
                                        <div className={`p-3 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'}`}>
                                            <p className={textPrimary}>Ordered: {part.purchaseDate}</p>
                                        </div>
                                    </div>

                                    <div className="flex gap-3 pt-4">
                                        {part.tracking && part.status === 'shipped' && (
                                            <button className="px-4 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded-lg transition-colors flex items-center gap-2">
                                                <TruckIcon className="w-4 h-4" />
                                                Track Package
                                            </button>
                                        )}
                                        <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                                            Contact Seller
                                        </button>
                                        {part.status === 'delivered' && (
                                            <button className="px-4 py-2 bg-green-500/20 text-green-500 hover:bg-green-500/30 rounded-lg transition-colors">
                                                Leave Review
                                            </button>
                                        )}
                                    </div>
                                </div>
                            </>
                        );
                    })() : (
                        <div className="flex-1 flex items-center justify-center p-12">
                            <div className="text-center">
                                <WrenchScrewdriverIcon className={`w-16 h-16 mx-auto mb-4 ${textSecondary}`} />
                                <p className={textSecondary}>Select a part to view details</p>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
