import { useDarkMode } from '~/root';
import { Link } from '@remix-run/react';
import {
    HeartIcon,
    CodeBracketIcon,
    LightBulbIcon,
    ChatBubbleLeftRightIcon,
    BugAntIcon,
    PencilSquareIcon,
    CurrencyDollarIcon,
    ShareIcon
} from '@heroicons/react/24/solid';

export default function ContributePage() {
    const { darkMode } = useDarkMode();

    const contributionTypes = [
        {
            title: 'Report Bugs',
            description: 'Found something broken? Let us know so we can fix it quickly.',
            icon: BugAntIcon,
            color: 'text-red-500',
            bg: 'bg-red-500/20',
        },
        {
            title: 'Suggest Features',
            description: 'Have an idea that would make TunerSwap better? We want to hear it.',
            icon: LightBulbIcon,
            color: 'text-yellow-500',
            bg: 'bg-yellow-500/20',
        },
        {
            title: 'Write Code',
            description: 'Developers can contribute directly to the codebase.',
            icon: CodeBracketIcon,
            color: 'text-blue-500',
            bg: 'bg-blue-500/20',
        },
        {
            title: 'Improve Docs',
            description: 'Help make our documentation clearer and more comprehensive.',
            icon: PencilSquareIcon,
            color: 'text-green-500',
            bg: 'bg-green-500/20',
        },
        {
            title: 'Give Feedback',
            description: 'Share your experience using TunerSwap - what works, what doesn\'t.',
            icon: ChatBubbleLeftRightIcon,
            color: 'text-purple-500',
            bg: 'bg-purple-500/20',
        },
        {
            title: 'Spread the Word',
            description: 'Tell other tuners and car enthusiasts about TunerSwap.',
            icon: ShareIcon,
            color: 'text-pink-500',
            bg: 'bg-pink-500/20',
        },
    ];

    const guidelines = [
        'Be respectful and constructive in all communications',
        'Search existing issues before creating new ones',
        'Provide detailed descriptions and steps to reproduce bugs',
        'Follow the existing code style and conventions',
        'Test your changes thoroughly before submitting',
        'Keep pull requests focused on a single feature or fix',
    ];

    const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-pink-500/10 via-red-500/10 to-orange-500/10 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-32 px-6 flex flex-col items-center text-center">
                    <HeartIcon className="w-16 h-16 text-pink-500 mb-6" />
                    <h1 className="text-5xl sm:text-6xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-pink-400 via-red-500 to-orange-500">
                            Contribute
                        </span>
                    </h1>
                    <p className="mt-6 text-xl text-gray-300 max-w-2xl">
                        TunerSwap is built by the community, for the community. Here's how you can help make it even better.
                    </p>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            <main className="max-w-4xl mx-auto px-6 py-16">
                {/* Ways to Contribute */}
                <section className="mb-16">
                    <h2 className={`text-2xl font-bold mb-8 ${textPrimary}`}>Ways to Contribute</h2>
                    <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-6">
                        {contributionTypes.map((type) => (
                            <div key={type.title} className={`${cardClasses} p-6 hover:shadow-2xl hover:shadow-pink-500/10 transition-all duration-300`}>
                                <div className={`w-12 h-12 rounded-xl ${type.bg} flex items-center justify-center mb-4`}>
                                    <type.icon className={`w-6 h-6 ${type.color}`} />
                                </div>
                                <h3 className={`font-bold mb-2 ${textPrimary}`}>{type.title}</h3>
                                <p className={`text-sm ${textSecondary}`}>{type.description}</p>
                            </div>
                        ))}
                    </div>
                </section>

                {/* Getting Started */}
                <section className="mb-16">
                    <div className={`${cardClasses} p-8`}>
                        <h2 className={`text-2xl font-bold mb-6 ${textPrimary}`}>Getting Started</h2>

                        <div className="space-y-6">
                            <div>
                                <h3 className={`font-bold mb-2 ${textPrimary}`}>1. Check the Roadmap</h3>
                                <p className={`${textSecondary} mb-2`}>
                                    See what features are planned and in progress. This helps you understand where help is needed most.
                                </p>
                                <Link to="/dev/roadmap" className="text-pink-500 hover:text-pink-400 text-sm">
                                    View Roadmap &rarr;
                                </Link>
                            </div>

                            <div>
                                <h3 className={`font-bold mb-2 ${textPrimary}`}>2. Read the Documentation</h3>
                                <p className={`${textSecondary} mb-2`}>
                                    Familiarize yourself with how TunerSwap works before diving in.
                                </p>
                                <Link to="/dev/docs" className="text-pink-500 hover:text-pink-400 text-sm">
                                    Read Docs &rarr;
                                </Link>
                            </div>

                            <div>
                                <h3 className={`font-bold mb-2 ${textPrimary}`}>3. Reach Out</h3>
                                <p className={`${textSecondary} mb-2`}>
                                    Have questions or want to discuss a contribution? Get in touch with us.
                                </p>
                                <Link to="/contact" className="text-pink-500 hover:text-pink-400 text-sm">
                                    Contact Us &rarr;
                                </Link>
                            </div>
                        </div>
                    </div>
                </section>

                {/* Guidelines */}
                <section className="mb-16">
                    <div className={`${cardClasses} p-8`}>
                        <h2 className={`text-2xl font-bold mb-6 ${textPrimary}`}>Contribution Guidelines</h2>
                        <ul className="space-y-3">
                            {guidelines.map((guideline, index) => (
                                <li key={index} className="flex items-start gap-3">
                                    <span className="flex-shrink-0 w-6 h-6 rounded-full bg-gradient-to-r from-pink-500 to-red-500 flex items-center justify-center text-white text-xs font-bold">
                                        {index + 1}
                                    </span>
                                    <span className={textSecondary}>{guideline}</span>
                                </li>
                            ))}
                        </ul>
                    </div>
                </section>

                {/* Tech Stack */}
                <section className="mb-16">
                    <div className={`${cardClasses} p-8`}>
                        <h2 className={`text-2xl font-bold mb-6 ${textPrimary}`}>Tech Stack</h2>
                        <div className="grid grid-cols-2 sm:grid-cols-4 gap-4">
                            {['Remix', 'React', 'TypeScript', 'Tailwind CSS', 'Vendure', 'GraphQL', 'Node.js', 'PostgreSQL'].map((tech) => (
                                <div key={tech} className={`px-4 py-3 rounded-xl text-center ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'}`}>
                                    <span className={`font-medium ${textPrimary}`}>{tech}</span>
                                </div>
                            ))}
                        </div>
                    </div>
                </section>

                {/* CTA */}
                <section>
                    <div className={`${cardClasses} p-8 text-center`}>
                        <HeartIcon className="w-12 h-12 mx-auto mb-4 text-pink-500" />
                        <h2 className={`text-2xl font-bold mb-4 ${textPrimary}`}>Ready to Contribute?</h2>
                        <p className={`${textSecondary} max-w-xl mx-auto mb-6`}>
                            Every contribution matters, no matter how small. Join us in building the best platform for the tuning community.
                        </p>
                        <div className="flex flex-wrap justify-center gap-4">
                            <Link
                                to="/contact"
                                className="px-6 py-3 bg-gradient-to-r from-pink-500 to-red-500 text-white font-bold rounded-xl hover:shadow-lg hover:shadow-pink-500/25 transition-all"
                            >
                                Get in Touch
                            </Link>
                            <Link
                                to="/dev/roadmap"
                                className={`px-6 py-3 rounded-xl font-bold ${darkMode ? 'bg-zinc-700 text-white hover:bg-zinc-600' : 'bg-gray-200 text-gray-900 hover:bg-gray-300'} transition-all`}
                            >
                                View Roadmap
                            </Link>
                        </div>
                    </div>
                </section>
            </main>
        </div>
    );
}
