import type { MetaFunction } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useOutletContext } from '@remix-run/react';
import { ProfileTab } from '~/components/buyer-admin';
import type { BuyerData } from '~/data/buyer-demo-data';

export const meta: MetaFunction = () => {
    return [{ title: 'Profile | My Dashboard | TunerSwap' }];
};

interface OutletContext {
    buyer: BuyerData;
    isDemo: boolean;
}

export default function BuyerProfileRoute() {
    const context = useOutletContext<OutletContext>();

    return <ProfileTab buyer={context?.buyer} />;
}
