import type { LoaderFunctionArgs, MetaFunction } from '@remix-run/node';
import { json } from '@remix-run/node';
import { useLoaderData } from '@remix-run/react';
import { ProfileTab } from '~/components/seller-admin';
import { getSellerProfile } from '~/providers/seller';

export const meta: MetaFunction = () => {
    return [{ title: 'Profile | Seller Dashboard | TunerSwap' }];
};

export async function loader({ request }: LoaderFunctionArgs) {
    const profileResult = await getSellerProfile();

    return json({
        profile: profileResult.data,
        isDemo: profileResult.isDemo
    });
}

export default function SellerProfileRoute() {
    const { isDemo } = useLoaderData<typeof loader>();

    return (
        <>
            {isDemo && (
                <div className="mb-4 px-4 py-2 bg-yellow-500/20 border border-yellow-500/50 rounded-lg text-yellow-500 text-sm">
                    Demo Mode: Displaying sample data (Vendure unavailable)
                </div>
            )}
            <ProfileTab />
        </>
    );
}
