// Buyer Demo Data - isolated for later Vendure API migration

export interface BuyerData {
    name: string;
    initials: string;
    email: string;
    phone: string;
    location: string;
    memberSince: string;
    vehicles: Vehicle[];
}

export interface Vehicle {
    id: string;
    year: number;
    make: string;
    model: string;
    trim?: string;
    vin?: string;
    mileage?: number;
    isPrimary?: boolean;
}

export interface BuyerStatsData {
    ordersCompleted: number;
    tunesInstalled: number;
    partsOrdered: number;
    servicesBooked: number;
    totalSpent: number;
    savedSellers: number;
}

export interface BuyerOrder {
    id: string;
    type: 'tune' | 'parts' | 'service';
    product: string;
    seller: {
        name: string;
        initials: string;
    };
    vehicle: string;
    price: number;
    status: 'pending' | 'processing' | 'shipped' | 'completed' | 'scheduled' | 'in-progress';
    date: string;
    tracking?: string;
    downloadUrl?: string;
}

export interface BuyerTune {
    id: string;
    name: string;
    seller: {
        name: string;
        initials: string;
        verified: boolean;
    };
    vehicle: string;
    purchaseDate: string;
    downloadUrl: string;
    version: string;
    specs: {
        hp: number;
        torque: number;
        fuel: string;
    };
    status: 'active' | 'available-update' | 'expired';
}

export interface BuyerPart {
    id: string;
    name: string;
    seller: {
        name: string;
        initials: string;
    };
    vehicle: string;
    purchaseDate: string;
    price: number;
    status: 'delivered' | 'shipped' | 'processing';
    tracking?: string;
    warranty?: string;
}

export interface BuyerService {
    id: string;
    service: string;
    seller: {
        name: string;
        initials: string;
        location: string;
    };
    vehicle: string;
    date: string;
    time: string;
    price: number;
    status: 'scheduled' | 'completed' | 'cancelled';
    notes?: string;
}

export interface BuyerMessage {
    id: string;
    seller: {
        name: string;
        initials: string;
    };
    subject: string;
    lastMessage: string;
    timestamp: string;
    unread: boolean;
    messages: {
        id: string;
        sender: 'buyer' | 'seller';
        text: string;
        timestamp: string;
    }[];
}

export interface BuyerCalendarEvent {
    id: string;
    title: string;
    date: string;
    time: string;
    type: 'service' | 'pickup' | 'delivery';
    status: 'confirmed' | 'pending';
    seller?: string;
}

export interface BuyerConnection {
    type: 'payment' | 'vehicle';
    name: string;
    status: 'connected' | 'not-connected';
    lastUsed?: string;
    details?: string;
}

