import { useDarkMode } from '~/root';
import { Link } from '@remix-run/react';
import {
    CodeBracketIcon,
    BookOpenIcon,
    ClockIcon,
    RocketLaunchIcon,
    HeartIcon,
    CommandLineIcon,
} from '@heroicons/react/24/solid';

export default function DevIndexPage() {
    const { darkMode } = useDarkMode();

    const devLinks = [
        {
            title: 'Documentation',
            description: 'Comprehensive guides and references for using TunerSwap.',
            icon: BookOpenIcon,
            color: 'text-red-500',
            bg: 'from-red-500/20 to-orange-500/20',
            href: '/dev/docs',
        },
        {
            title: 'Roadmap',
            description: 'See what features we\'re building and what\'s coming next.',
            icon: RocketLaunchIcon,
            color: 'text-purple-500',
            bg: 'from-purple-500/20 to-violet-500/20',
            href: '/dev/roadmap',
        },
        {
            title: 'Changelog',
            description: 'Track every update, improvement, and fix we\'ve shipped.',
            icon: ClockIcon,
            color: 'text-blue-500',
            bg: 'from-blue-500/20 to-cyan-500/20',
            href: '/dev/changelog',
        },
        {
            title: 'Contribute',
            description: 'Help build TunerSwap - contribute code, ideas, or feedback.',
            icon: HeartIcon,
            color: 'text-pink-500',
            bg: 'from-pink-500/20 to-red-500/20',
            href: '/dev/contribute',
        },
    ];

    const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-green-500/10 via-blue-500/10 to-purple-500/10 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-32 px-6 flex flex-col items-center text-center">
                    <CommandLineIcon className="w-16 h-16 text-green-500 mb-6" />
                    <h1 className="text-5xl sm:text-6xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-green-400 via-blue-500 to-purple-500">
                            Developer Hub
                        </span>
                    </h1>
                    <p className="mt-6 text-xl text-gray-300 max-w-2xl">
                        Resources for developers, contributors, and anyone who wants to dive deeper into TunerSwap.
                    </p>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            {/* Dev Links Grid */}
            <main className="max-w-4xl mx-auto px-6 py-16">
                <div className="grid sm:grid-cols-2 gap-6">
                    {devLinks.map((link) => (
                        <Link
                            key={link.title}
                            to={link.href}
                            className={`${cardClasses} p-8 group hover:shadow-2xl hover:shadow-green-500/10 transition-all duration-300 hover:-translate-y-1`}
                        >
                            <div className={`w-14 h-14 rounded-2xl bg-gradient-to-br ${link.bg} flex items-center justify-center mb-4`}>
                                <link.icon className={`w-7 h-7 ${link.color}`} />
                            </div>
                            <h2 className={`text-xl font-bold mb-2 ${textPrimary} group-hover:text-green-400 transition-colors`}>
                                {link.title}
                            </h2>
                            <p className={textSecondary}>{link.description}</p>
                            <div className="mt-4 text-green-500 text-sm font-medium group-hover:translate-x-2 transition-transform">
                                Explore &rarr;
                            </div>
                        </Link>
                    ))}
                </div>

                {/* Stats Section */}
                <div className={`${cardClasses} p-8 mt-12`}>
                    <h3 className={`text-lg font-bold mb-6 ${textPrimary}`}>Project Stats</h3>
                    <div className="grid grid-cols-2 sm:grid-cols-4 gap-6 text-center">
                        <div>
                            <div className="text-3xl font-black text-green-500">0.3.0</div>
                            <div className={`text-sm ${textSecondary}`}>Current Version</div>
                        </div>
                        <div>
                            <div className="text-3xl font-black text-blue-500">Active</div>
                            <div className={`text-sm ${textSecondary}`}>Development</div>
                        </div>
                        <div>
                            <div className="text-3xl font-black text-purple-500">Remix</div>
                            <div className={`text-sm ${textSecondary}`}>Framework</div>
                        </div>
                        <div>
                            <div className="text-3xl font-black text-pink-500">Open</div>
                            <div className={`text-sm ${textSecondary}`}>To Contributors</div>
                        </div>
                    </div>
                </div>

                {/* Jump To Links */}
                <div className={`${cardClasses} p-8 mt-6`}>
                    <h3 className={`text-lg font-bold mb-4 ${textPrimary}`}>Jump To</h3>
                    <div className="flex flex-wrap gap-3">
                        <Link to="/dev/docs/getting-started/platform-overview" className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} text-sm transition-colors`}>
                            Getting Started
                        </Link>
                        <Link to="/dev/docs/buyers/browsing-tunes" className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} text-sm transition-colors`}>
                            Buyer Guides
                        </Link>
                        <Link to="/dev/docs/sellers/becoming-tuner" className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} text-sm transition-colors`}>
                            Seller Guides
                        </Link>
                        <Link to="/dev/docs/api/rest-api-overview" className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} text-sm transition-colors`}>
                            API Docs
                        </Link>
                        <Link to="/contact" className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} text-sm transition-colors`}>
                            Contact Us
                        </Link>
                    </div>
                </div>
            </main>
        </div>
    );
}
