import { useState } from 'react';
import {
    ChevronLeftIcon,
    ChevronRightIcon,
    CalendarDaysIcon,
    TruckIcon,
} from '@heroicons/react/24/solid';
import { useBuyerAdminStyles } from './useBuyerAdminStyles';
import { buyerCalendarEvents } from '~/data/buyer-demo-data';

export function CalendarTab() {
    const { darkMode, cardClasses, textPrimary, textSecondary } = useBuyerAdminStyles();
    const [currentMonth] = useState('January 2024');
    const days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

    // Generate calendar days (simplified for demo)
    const calendarDays = Array.from({ length: 35 }, (_, i) => {
        const day = i - 2; // offset to start on correct day
        return day > 0 && day <= 31 ? day : null;
    });

    const getEventsForDay = (day: number | null) => {
        if (!day) return [];
        const dateStr = `2024-01-${day.toString().padStart(2, '0')}`;
        return buyerCalendarEvents.filter(e => e.date === dateStr);
    };

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <h2 className={`text-2xl font-bold ${textPrimary}`}>My Calendar</h2>
                <div className="flex items-center gap-2">
                    <button className={`p-2 ${darkMode ? 'bg-zinc-800 hover:bg-zinc-700' : 'bg-gray-100 hover:bg-gray-200'} rounded-lg transition-colors`}>
                        <ChevronLeftIcon className={`w-5 h-5 ${textPrimary}`} />
                    </button>
                    <span className={`px-4 py-2 font-medium ${textPrimary}`}>{currentMonth}</span>
                    <button className={`p-2 ${darkMode ? 'bg-zinc-800 hover:bg-zinc-700' : 'bg-gray-100 hover:bg-gray-200'} rounded-lg transition-colors`}>
                        <ChevronRightIcon className={`w-5 h-5 ${textPrimary}`} />
                    </button>
                </div>
            </div>

            <div className={cardClasses}>
                {/* Calendar Header */}
                <div className={`grid grid-cols-7 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                    {days.map((day) => (
                        <div key={day} className={`p-3 text-center font-medium ${textSecondary}`}>
                            {day}
                        </div>
                    ))}
                </div>

                {/* Calendar Grid */}
                <div className="grid grid-cols-7">
                    {calendarDays.map((day, idx) => {
                        const events = getEventsForDay(day);
                        const isToday = day === 15;

                        return (
                            <div
                                key={idx}
                                className={`min-h-[100px] p-2 border-b border-r ${darkMode ? 'border-zinc-700' : 'border-gray-200'} ${
                                    day ? `cursor-pointer ${darkMode ? 'hover:bg-zinc-800/50' : 'hover:bg-gray-50'}` : ''
                                }`}
                            >
                                {day && (
                                    <>
                                        <div className={`text-sm mb-1 ${
                                            isToday
                                                ? 'w-6 h-6 bg-blue-500 text-white rounded-full flex items-center justify-center'
                                                : textPrimary
                                        }`}>
                                            {day}
                                        </div>
                                        <div className="space-y-1">
                                            {events.slice(0, 2).map((event) => (
                                                <div
                                                    key={event.id}
                                                    className={`text-xs p-1 rounded truncate ${
                                                        event.type === 'service'
                                                            ? 'bg-green-500/20 text-green-500'
                                                            : event.type === 'delivery'
                                                            ? 'bg-orange-500/20 text-orange-500'
                                                            : 'bg-blue-500/20 text-blue-500'
                                                    }`}
                                                >
                                                    {event.time} {event.title.split(' - ')[0]}
                                                </div>
                                            ))}
                                            {events.length > 2 && (
                                                <div className={`text-xs ${textSecondary}`}>
                                                    +{events.length - 2} more
                                                </div>
                                            )}
                                        </div>
                                    </>
                                )}
                            </div>
                        );
                    })}
                </div>
            </div>

            {/* Upcoming Events */}
            <div className={cardClasses}>
                <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                    <h3 className={`font-semibold ${textPrimary}`}>Upcoming Events</h3>
                </div>
                <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                    {buyerCalendarEvents.map((event) => (
                        <div key={event.id} className="p-4 flex items-center justify-between">
                            <div className="flex items-center gap-4">
                                <div className={`w-10 h-10 rounded-lg flex items-center justify-center ${
                                    event.type === 'service'
                                        ? 'bg-green-500/20'
                                        : event.type === 'delivery'
                                        ? 'bg-orange-500/20'
                                        : 'bg-blue-500/20'
                                }`}>
                                    {event.type === 'service' ? (
                                        <CalendarDaysIcon className="w-5 h-5 text-green-500" />
                                    ) : (
                                        <TruckIcon className="w-5 h-5 text-orange-500" />
                                    )}
                                </div>
                                <div>
                                    <p className={`font-medium ${textPrimary}`}>{event.title}</p>
                                    <p className={`text-sm ${textSecondary}`}>{event.date} at {event.time}</p>
                                    {event.seller && <p className={`text-sm ${textSecondary}`}>{event.seller}</p>}
                                </div>
                            </div>
                            <span className={`px-2 py-1 text-xs rounded ${
                                event.status === 'confirmed' ? 'bg-green-500/20 text-green-500' : 'bg-yellow-500/20 text-yellow-500'
                            }`}>{event.status}</span>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}
