import { useState } from 'react';
import { Link, Outlet, useLocation, useLoaderData, Form } from '@remix-run/react';
import type { LoaderFunctionArgs } from '@remix-run/node';
import { json, redirect } from '@remix-run/node';
import { useDarkMode } from '~/root';
import {
    HomeIcon,
    UserCircleIcon,
    ShoppingCartIcon,
    CalendarDaysIcon,
    ChatBubbleLeftRightIcon,
    CpuChipIcon,
    WrenchScrewdriverIcon,
    BuildingStorefrontIcon,
    LinkIcon,
    Bars3Icon,
    XMarkIcon,
    BellIcon,
    ArrowRightOnRectangleIcon,
    TruckIcon,
} from '@heroicons/react/24/solid';

import { getActiveCustomerDetails } from '~/providers/customer/customer';
import type { BuyerData, BuyerStatsData } from '~/data/buyer-demo-data';

export async function loader({ request }: LoaderFunctionArgs) {
    try {
        // Check if user is logged in and get their data
        const { activeCustomer } = await getActiveCustomerDetails({ request });

        if (!activeCustomer) {
            return redirect('/sign-in?redirectTo=/buyeradmin');
        }

        // Map Vendure customer to buyer data format
        const firstName = activeCustomer.firstName || '';
        const lastName = activeCustomer.lastName || '';
        const name = `${firstName} ${lastName}`.trim() || 'User';
        const initials = `${firstName[0] || ''}${lastName[0] || ''}`.toUpperCase() || 'U';

        const buyer: BuyerData = {
            name,
            initials,
            email: activeCustomer.emailAddress || '',
            phone: (activeCustomer as any).phoneNumber || '',
            location: (activeCustomer as any).customFields?.location || '',
            memberSince: activeCustomer.createdAt
                ? new Date(activeCustomer.createdAt).toLocaleDateString('en-US', { month: 'long', year: 'numeric' })
                : 'Recently',
            vehicles: [], // Will be populated from customFields
        };

        // Parse vehicles from customFields if available
        try {
            const vehiclesJson = (activeCustomer as any).customFields?.vehicles;
            if (vehiclesJson) {
                buyer.vehicles = JSON.parse(vehiclesJson);
            }
        } catch (e) {
            // Keep empty array
        }

        // Stats - zeros until we have order history
        const stats: BuyerStatsData = {
            ordersCompleted: 0,
            tunesInstalled: 0,
            partsOrdered: 0,
            servicesBooked: 0,
            totalSpent: 0,
            savedSellers: 0,
        };

        return json({
            buyer,
            stats,
            isDemo: false,
        });
    } catch (error) {
        console.error('Buyeradmin loader error:', error);
        // Return fallback data instead of crashing
        return json({
            buyer: {
                name: 'User',
                initials: 'U',
                email: '',
                phone: '',
                location: '',
                memberSince: 'Recently',
                vehicles: [],
            },
            stats: {
                ordersCompleted: 0,
                tunesInstalled: 0,
                partsOrdered: 0,
                servicesBooked: 0,
                totalSpent: 0,
                savedSellers: 0,
            },
            isDemo: true,
        });
    }
}

// Sidebar navigation items with route paths
const sidebarItems = [
    { id: 'overview', label: 'Overview', icon: HomeIcon, path: '/buyeradmin' },
    { id: 'profile', label: 'Profile', icon: UserCircleIcon, path: '/buyeradmin/profile' },
    { id: 'garage', label: 'My Garage', icon: TruckIcon, path: '/buyeradmin/garage' },
    { id: 'calendar', label: 'Calendar', icon: CalendarDaysIcon, path: '/buyeradmin/calendar' },
    { id: 'divider0', type: 'divider' },
    { id: 'orders', label: 'Orders', icon: ShoppingCartIcon, badge: 2, path: '/buyeradmin/orders' },
    { id: 'messages', label: 'Messages', icon: ChatBubbleLeftRightIcon, badge: 2, path: '/buyeradmin/messages' },
    { id: 'divider1', type: 'divider' },
    { id: 'tunes', label: 'My Tunes', icon: CpuChipIcon, path: '/buyeradmin/tunes' },
    { id: 'parts', label: 'My Parts', icon: WrenchScrewdriverIcon, path: '/buyeradmin/parts' },
    { id: 'services', label: 'My Services', icon: BuildingStorefrontIcon, path: '/buyeradmin/services' },
    { id: 'divider2', type: 'divider' },
    { id: 'connections', label: 'Connections', icon: LinkIcon, path: '/buyeradmin/connections' },
];

export default function BuyerAdminLayout() {
    const { buyer, stats, isDemo } = useLoaderData<typeof loader>();
    const { darkMode } = useDarkMode();
    const location = useLocation();
    const [sidebarOpen, setSidebarOpen] = useState(false);

    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

    // Use real data from loader
    const buyerData = buyer;

    // Determine active tab from current path
    const getActiveTab = () => {
        const path = location.pathname;
        // Find exact match first
        const exactMatch = sidebarItems.find(item => item.path === path);
        if (exactMatch) return exactMatch.id;
        // Check if current path starts with any item's path
        const partialMatch = sidebarItems
            .filter(item => item.path && path.startsWith(item.path) && item.path !== '/buyeradmin')
            .sort((a, b) => (b.path?.length || 0) - (a.path?.length || 0))[0];
        if (partialMatch) return partialMatch.id;
        return 'overview';
    };

    const activeTab = getActiveTab();

    function SidebarContent() {
        return (
            <div className="p-4">
                {/* Buyer Info */}
                <div className="mb-6">
                    <div className="flex items-center gap-3 mb-4">
                        <div className="w-12 h-12 rounded-full bg-gradient-to-br from-blue-400 via-purple-500 to-pink-500 flex items-center justify-center text-white font-bold">
                            {buyerData.initials}
                        </div>
                        <div>
                            <p className={`font-semibold ${textPrimary}`}>{buyerData.name}</p>
                            <p className={`text-sm ${textSecondary}`}>{buyerData.location}</p>
                        </div>
                    </div>
                    <Link
                        to="/"
                        className={`block text-center py-2 rounded-lg text-sm ${darkMode ? 'bg-zinc-800 hover:bg-zinc-700 text-gray-300' : 'bg-gray-100 hover:bg-gray-200 text-gray-700'} transition-colors`}
                    >
                        Browse Marketplace
                    </Link>
                </div>

                {/* Navigation */}
                <nav className="space-y-1">
                    {sidebarItems.map((item) => {
                        if (item.type === 'divider') {
                            return <div key={item.id} className={`my-4 border-t ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`} />;
                        }
                        const Icon = item.icon!;
                        const isActive = activeTab === item.id;
                        return (
                            <Link
                                key={item.id}
                                to={item.path!}
                                onClick={() => setSidebarOpen(false)}
                                className={`w-full flex items-center justify-between px-3 py-2 rounded-lg text-left transition-colors ${
                                    isActive
                                        ? 'bg-blue-500 text-white'
                                        : `${darkMode ? 'text-gray-300 hover:bg-zinc-800' : 'text-gray-700 hover:bg-gray-100'}`
                                }`}
                            >
                                <div className="flex items-center gap-3">
                                    <Icon className="w-5 h-5" />
                                    <span>{item.label}</span>
                                </div>
                                {item.badge && (
                                    <span className={`px-2 py-0.5 text-xs rounded-full ${
                                        isActive ? 'bg-white/20' : 'bg-blue-500 text-white'
                                    }`}>
                                        {item.badge}
                                    </span>
                                )}
                            </Link>
                        );
                    })}
                </nav>

                {/* Sign Out & Back to Site */}
                <div className={`mt-8 pt-4 border-t ${darkMode ? 'border-zinc-700' : 'border-gray-200'} space-y-1`}>
                    <Link
                        to="/"
                        className={`flex items-center gap-3 px-3 py-2 rounded-lg ${darkMode ? 'text-gray-400 hover:text-white hover:bg-zinc-800' : 'text-gray-500 hover:text-gray-900 hover:bg-gray-100'} transition-colors`}
                    >
                        <ArrowRightOnRectangleIcon className="w-5 h-5" />
                        <span>Back to Site</span>
                    </Link>
                    <Form method="post" action="/api/logout">
                        <button
                            type="submit"
                            className={`w-full flex items-center gap-3 px-3 py-2 rounded-lg text-red-500 hover:bg-red-500/10 transition-colors`}
                        >
                            <ArrowRightOnRectangleIcon className="w-5 h-5" />
                            <span>Sign Out</span>
                        </button>
                    </Form>
                </div>
            </div>
        );
    }

    return (
        <div className={`min-h-screen ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Mobile Header */}
            <div className={`lg:hidden fixed top-0 left-0 right-0 z-50 ${darkMode ? 'bg-zinc-900 border-zinc-700' : 'bg-white border-gray-200'} border-b p-4 flex items-center justify-between`}>
                <button onClick={() => setSidebarOpen(true)} className={textPrimary}>
                    <Bars3Icon className="w-6 h-6" />
                </button>
                <span className={`font-bold ${textPrimary}`}>My Dashboard</span>
                <button className={textPrimary}>
                    <BellIcon className="w-6 h-6" />
                </button>
            </div>

            {/* Mobile Sidebar Overlay */}
            {sidebarOpen && (
                <div className="lg:hidden fixed inset-0 z-50 bg-black/50" onClick={() => setSidebarOpen(false)}>
                    <div
                        className={`w-72 h-full ${darkMode ? 'bg-zinc-900' : 'bg-white'} p-4`}
                        onClick={(e) => e.stopPropagation()}
                    >
                        <div className="flex items-center justify-between mb-6">
                            <span className={`font-bold text-xl ${textPrimary}`}>Menu</span>
                            <button onClick={() => setSidebarOpen(false)}>
                                <XMarkIcon className={`w-6 h-6 ${textPrimary}`} />
                            </button>
                        </div>
                        <SidebarContent />
                    </div>
                </div>
            )}

            {/* Desktop Sidebar */}
            <div className={`hidden lg:block fixed left-0 top-0 bottom-0 w-64 ${darkMode ? 'bg-zinc-900 border-zinc-700' : 'bg-white border-gray-200'} border-r overflow-y-auto`}>
                <SidebarContent />
            </div>

            {/* Main Content - Outlet renders nested routes */}
            <div className="lg:ml-64 pt-16 lg:pt-0">
                <div className="p-6">
                    {isDemo && (
                        <div className="mb-4 px-4 py-2 bg-yellow-500/20 border border-yellow-500/50 rounded-lg text-yellow-500 text-sm">
                            Demo Mode: Displaying sample data (Vendure unavailable or not logged in)
                        </div>
                    )}
                    <Outlet context={{ buyer: buyerData, stats, isDemo }} />
                </div>
            </div>
        </div>
    );
}
