import { RootLoaderData } from '~/root';
import { Link } from '@remix-run/react';
import { useTranslation } from 'react-i18next';

const navigation = {
    support: [
        { page: 'help', href: '#' },
        { page: 'trackOrder', href: '#' },
        { page: 'shipping', href: '#' },
        { page: 'returns', href: '#' },
    ],
    company: [
        { page: 'about', href: '#' },
        { page: 'blog', href: '#' },
        { page: 'responsibility', href: '#' },
        { page: 'press', href: '#' },
    ],
};

export default function Footer({
    collections,
}: {
    collections: RootLoaderData['collections'];
}) {
    const { t } = useTranslation();

    return (
        <footer
            className="
        border-t border-red-900/50
        bg-gradient-to-b from-gray-900 via-zinc-950 to-black
        text-gray-300
      "
            aria-labelledby="footer-heading"
        >
            <h2 id="footer-heading" className="sr-only">
                {t('footer.title')}
            </h2>

            <div className="max-w-7xl mx-auto py-14 px-6 lg:py-20 lg:px-8">
                <div className="xl:grid xl:grid-cols-3 xl:gap-12">

                    {/* LEFT SECTIONS */}
                    <div className="grid grid-cols-2 gap-12 xl:col-span-2">

                        {/* SHOP + SUPPORT */}
                        <div className="md:grid md:grid-cols-2 md:gap-12">

                            {/* SHOP */}
                            <div>
                                <h3 className="text-sm font-semibold text-red-400 uppercase tracking-wider">
                                    {t('footer.shop')}
                                </h3>
                                <ul role="list" className="mt-5 space-y-3">
                                    {collections.map((collection) => (
                                        <li key={collection.id}>
                                            <Link
                                                className="
                          text-base text-gray-400 hover:text-white 
                          transition-colors duration-150
                        "
                                                to={'/collections/' + collection.slug}
                                                prefetch="intent"
                                            >
                                                {collection.name}
                                            </Link>
                                        </li>
                                    ))}
                                </ul>
                            </div>

                            {/* SUPPORT */}
                            <div className="mt-12 md:mt-0">
                                <h3 className="text-sm font-semibold text-red-400 uppercase tracking-wider">
                                    {t('footer.support')}
                                </h3>
                                <ul role="list" className="mt-5 space-y-3">
                                    {navigation.support.map(({ page, href }) => (
                                        <li key={page}>
                                            <a
                                                href={href}
                                                className="
                          text-base text-gray-400 hover:text-white 
                          transition-colors duration-150
                        "
                                            >
                                                {t(`navigation.support.${page}`)}
                                            </a>
                                        </li>
                                    ))}
                                </ul>
                            </div>

                        </div>

                        {/* COMPANY + TUNERS */}
                        <div className="md:grid md:grid-cols-2 md:gap-12">
                            <div>
                                <h3 className="text-sm font-semibold text-red-400 uppercase tracking-wider">
                                    {t('account.company')}
                                </h3>
                                <ul role="list" className="mt-5 space-y-3">
                                    {navigation.company.map(({ page, href }) => (
                                        <li key={page}>
                                            <a
                                                href={href}
                                                className="
                          text-base text-gray-400 hover:text-white
                          transition-colors duration-150
                        "
                                            >
                                                {t(`navigation.company.${page}`)}
                                            </a>
                                        </li>
                                    ))}
                                </ul>
                            </div>

                            {/* TUNERS */}
                            <div className="mt-12 md:mt-0">
                                <h3 className="text-sm font-semibold text-blue-400 uppercase tracking-wider">
                                    For Tuners
                                </h3>
                                <ul role="list" className="mt-5 space-y-3">
                                    <li>
                                        <Link
                                            to="/tuner-sign-in"
                                            className="text-base text-gray-400 hover:text-white transition-colors duration-150"
                                        >
                                            Tuner Sign In
                                        </Link>
                                    </li>
                                    <li>
                                        <Link
                                            to="/tunersignup"
                                            className="text-base text-gray-400 hover:text-white transition-colors duration-150"
                                        >
                                            Become a Tuner
                                        </Link>
                                    </li>
                                    <li>
                                        <Link
                                            to="/selleradmin"
                                            className="text-base text-gray-400 hover:text-white transition-colors duration-150"
                                        >
                                            Seller Dashboard
                                        </Link>
                                    </li>
                                </ul>
                            </div>
                        </div>

                    </div>

                    {/* SUBSCRIBE */}
                    <div className="mt-12 xl:mt-0">
                        <h3 className="text-sm font-semibold text-red-400 uppercase tracking-wider">
                            {t('footer.subscribeHeader')}
                        </h3>
                        <p className="mt-5 text-base text-gray-400">
                            {t('footer.subscribeIntro')}
                        </p>

                        <form className="mt-5 sm:flex sm:max-w-md">
                            <label htmlFor="email-address" className="sr-only">
                                {t('acount.emailAddress')}
                            </label>

                            <input
                                type="email"
                                name="email-address"
                                id="email-address"
                                autoComplete="email"
                                required
                                className="
                  w-full bg-zinc-800 border border-zinc-700 rounded-lg
                  py-2.5 px-4 text-gray-100 placeholder-gray-500
                  focus:outline-none focus:ring-2 focus:ring-red-500
                  transition-all
                "
                                placeholder={t('footer.emailPlaceholder')}
                            />

                            <div className="mt-4 sm:mt-0 sm:ml-4 sm:flex-shrink-0">
                                <button
                                    type="submit"
                                    className="
                    w-full bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500
                    hover:from-yellow-400 hover:via-red-400 hover:to-blue-400
                    border border-transparent rounded-lg py-2.5 px-4
                    flex items-center justify-center text-base font-medium
                    text-white shadow-lg shadow-red-500/20 transition
                  "
                                >
                                    {t('footer.subscribe')}
                                </button>
                            </div>
                        </form>
                    </div>

                </div>

                {/* BOTTOM COPYRIGHT */}
                <div className="mt-12 border-t border-zinc-700 pt-8">
                    <a
                        className="
              flex items-center space-x-4 font-medium text-gray-400 
              hover:text-white transition-colors
            "
                        href="/contact"
                    >
                        <svg
                            fill="currentColor"
                            viewBox="0 0 24 24"
                            className="h-6 w-6"
                        >
                            <path
                                fillRule="evenodd"
                                d="M12 2C6.477 2 2 6.484 2 12.017c0 4.425 2.865 8.18 6.839..."
                                clipRule="evenodd"
                            />
                        </svg>
                        <span>Tuner Swap 2025</span>
                    </a>
                </div>
            </div>
        </footer>
    );
}
