import { useDarkMode } from '~/root';
import { Link } from '@remix-run/react';
import {
    CpuChipIcon,
    CheckBadgeIcon,
    ArrowTopRightOnSquareIcon,
    StarIcon,
    BoltIcon,
} from '@heroicons/react/24/solid';

export default function HardwarePage() {
    const { darkMode } = useDarkMode();

    const hardware = [
        {
            name: "HP Tuners MPVI3",
            company: "HP Tuners",
            description: "The latest generation interface from HP Tuners. Features faster processing, improved connectivity, and support for the widest range of vehicles. The go-to choice for professional tuners.",
            platforms: ["GM/Chevy", "Ford", "Dodge/Chrysler", "RAM", "Jeep", "Select imports"],
            features: ["USB-C connectivity", "Bluetooth capable", "CAN/J1850/ISO protocols", "Fastest read/write speeds", "VCM Suite compatible"],
            price: "$599+",
            link: "https://www.hptuners.com",
            popular: true,
            rating: 5,
        },
        {
            name: "HP Tuners MPVI2",
            company: "HP Tuners",
            description: "Previous generation HP Tuners interface. Still fully supported and capable, available at a lower price point. Great for tuners on a budget.",
            platforms: ["GM/Chevy", "Ford", "Dodge/Chrysler", "Select imports"],
            features: ["USB connectivity", "Proven reliability", "Wide vehicle support", "VCM Suite compatible", "Data logging capable"],
            price: "$399+",
            link: "https://www.hptuners.com",
            rating: 4,
        },
        {
            name: "EFI Live FlashScan V2",
            company: "EFI Live",
            description: "Professional-grade interface for GM gas and diesel vehicles. Industry standard for Duramax and Cummins diesel tuning with DSP5 switch support.",
            platforms: ["GM Gas", "Duramax Diesel", "Cummins Diesel", "Allison Transmissions"],
            features: ["5 DSP tune positions", "AutoCal compatible", "High-speed logging", "Pass-through programming", "Controller diagnostics"],
            price: "$849+",
            link: "https://www.efilive.com",
            popular: true,
            rating: 5,
        },
        {
            name: "EFI Live AutoCal V3",
            company: "EFI Live",
            description: "Consumer-friendly device for flashing EFI Live tunes. Perfect for customers who want to flash tunes themselves without full tuning software.",
            platforms: ["GM Gas", "Duramax Diesel", "Cummins Diesel"],
            features: ["Customer self-flash", "5 tune positions", "Data logging", "DTC reading", "Tuner licensing available"],
            price: "$450+",
            link: "https://www.efilive.com",
            rating: 4,
        },
        {
            name: "SCT X4 Power Flash",
            company: "SCT Performance",
            description: "Handheld tuning device for Ford vehicles. Load custom or pre-loaded tunes, monitor parameters, and read/clear diagnostic codes.",
            platforms: ["Ford Mustang", "Ford F-150", "Ford Super Duty", "Ford Focus/Fiesta ST"],
            features: ["Touchscreen display", "WiFi updates", "Custom tune loading", "Real-time monitoring", "Pre-loaded tune library"],
            price: "$399+",
            link: "https://www.sctflash.com",
            popular: true,
            rating: 4,
        },
        {
            name: "Cobb Accessport V3",
            company: "Cobb Tuning",
            description: "Popular handheld tuner for Subaru, Ford, and European vehicles. Includes off-the-shelf maps and supports professional custom tuning.",
            platforms: ["Subaru WRX/STI", "Ford Focus RS/ST", "Ford Fiesta ST", "VW GTI/Golf R", "Porsche"],
            features: ["Plug-and-play", "OTS maps included", "Custom tune support", "Real-time gauges", "Data logging"],
            price: "$650+",
            link: "https://www.cobbtuning.com",
            popular: true,
            rating: 5,
        },
        {
            name: "Hondata FlashPro",
            company: "Hondata",
            description: "The definitive Honda/Acura tuning solution. Real-time tuning capability with excellent community support and proven reliability.",
            platforms: ["Honda Civic", "Honda Accord", "Acura RSX", "Honda S2000", "Acura TSX"],
            features: ["Real-time tuning", "Dual maps", "Boost control", "Data logging", "Launch control"],
            price: "$695+",
            link: "https://www.hondata.com",
            rating: 5,
        },
        {
            name: "Ecutek ProECU Kit",
            company: "Ecutek",
            description: "Professional ECU programming kit for Japanese vehicles. Unlocks advanced tuning features and RaceROM capabilities.",
            platforms: ["Subaru", "Nissan 370Z/GTR", "Mazda MX-5/RX-8", "Toyota 86/BRZ", "Mitsubishi Evo"],
            features: ["RaceROM features", "Bluetooth dongle", "License per vehicle", "Map switching", "Anti-theft"],
            price: "$550+",
            link: "https://www.ecutek.com",
            rating: 5,
        },
        {
            name: "nGauge by HP Tuners",
            company: "HP Tuners",
            description: "Touchscreen display and tuning interface. View gauges, change tunes on the fly, and log data without a laptop.",
            platforms: ["All HP Tuners supported vehicles"],
            features: ["3.5\" touchscreen", "Quick tune switching", "Real-time gauges", "Data logging", "Bluetooth connectivity"],
            price: "$399+",
            link: "https://www.hptuners.com",
            rating: 4,
        },
        {
            name: "DiabloSport inTune i3",
            company: "DiabloSport",
            description: "Versatile handheld programmer for GM, Ford, Dodge, and Jeep. Supports both canned and custom tuning with data logging.",
            platforms: ["GM/Chevy", "Ford", "Dodge", "Jeep", "Chrysler"],
            features: ["Custom tuning support", "Performance tunes included", "Data logging", "WiFi updates", "DTC management"],
            price: "$399+",
            link: "https://www.diablosport.com",
            rating: 4,
        },
    ];

    const considerations = [
        {
            title: "Vehicle Compatibility",
            desc: "Always verify your exact year, make, and model is supported. Some vehicles require additional credits or licenses."
        },
        {
            title: "Software Included",
            desc: "Some interfaces come with software, others require separate purchase. Factor this into your total cost."
        },
        {
            title: "Credits & Licensing",
            desc: "Many platforms require purchasing credits per vehicle. Understand the licensing model before buying."
        },
        {
            title: "Future Support",
            desc: "Choose interfaces from companies with active development and good customer support."
        },
    ];

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-orange-500/20 via-amber-500/10 to-orange-500/20 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-32 px-6 flex flex-col items-center text-center">
                    <CpuChipIcon className="w-20 h-20 text-orange-500 mb-6" />
                    <h1 className="text-5xl sm:text-6xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-orange-400 via-amber-500 to-orange-500">
                            Recommended Hardware
                        </span>
                    </h1>
                    <p className="mt-6 text-xl text-gray-300 max-w-3xl">
                        Professional tuning interfaces and devices used by top tuners worldwide. Find the right hardware for your platform.
                    </p>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            {/* Hardware Grid */}
            <section className="max-w-6xl mx-auto px-6 py-16">
                <div className="grid gap-8">
                    {hardware.map((item) => (
                        <div
                            key={item.name}
                            className={`${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl p-6 sm:p-8 hover:shadow-2xl hover:shadow-orange-500/10 transition-all duration-300 ${item.popular ? 'ring-2 ring-orange-500' : ''}`}
                        >
                            <div className="flex flex-col lg:flex-row lg:items-start gap-6">
                                <div className="flex-1">
                                    <div className="flex flex-wrap items-center gap-3 mb-2">
                                        <h3 className={`text-2xl font-bold ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                                            {item.name}
                                        </h3>
                                        {item.popular && (
                                            <span className="px-3 py-1 rounded-full bg-orange-500/20 text-orange-400 text-xs font-semibold">
                                                Popular
                                            </span>
                                        )}
                                        <span className={`px-3 py-1 rounded-full text-sm font-bold ${darkMode ? 'bg-green-500/20 text-green-400' : 'bg-green-100 text-green-700'}`}>
                                            {item.price}
                                        </span>
                                    </div>
                                    <p className={`text-sm mb-4 ${darkMode ? 'text-gray-400' : 'text-gray-500'}`}>
                                        by {item.company}
                                    </p>
                                    <p className={`mb-4 ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>
                                        {item.description}
                                    </p>

                                    <div className="flex items-center gap-1 mb-4">
                                        {[...Array(5)].map((_, i) => (
                                            <StarIcon
                                                key={i}
                                                className={`w-5 h-5 ${i < item.rating ? 'text-yellow-400' : darkMode ? 'text-zinc-600' : 'text-gray-300'}`}
                                            />
                                        ))}
                                    </div>

                                    <div className="mb-4">
                                        <span className={`text-sm font-semibold ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                                            Supported Platforms:
                                        </span>
                                        <div className="flex flex-wrap gap-2 mt-2">
                                            {item.platforms.map((platform) => (
                                                <span
                                                    key={platform}
                                                    className={`px-3 py-1 rounded-full text-xs font-medium ${darkMode ? 'bg-zinc-700 text-gray-300' : 'bg-gray-100 text-gray-700'}`}
                                                >
                                                    {platform}
                                                </span>
                                            ))}
                                        </div>
                                    </div>

                                    <div>
                                        <span className={`text-sm font-semibold ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                                            Key Features:
                                        </span>
                                        <div className="flex flex-wrap gap-x-4 gap-y-2 mt-2">
                                            {item.features.map((feature) => (
                                                <span key={feature} className="flex items-center gap-1">
                                                    <CheckBadgeIcon className="w-4 h-4 text-orange-500" />
                                                    <span className={`text-sm ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>
                                                        {feature}
                                                    </span>
                                                </span>
                                            ))}
                                        </div>
                                    </div>
                                </div>

                                <div className="lg:flex-shrink-0">
                                    <a
                                        href={item.link}
                                        target="_blank"
                                        rel="noopener noreferrer"
                                        className="inline-flex items-center gap-2 px-6 py-3 bg-gradient-to-r from-orange-500 to-amber-500 text-white font-semibold rounded-xl hover:shadow-lg hover:shadow-orange-500/25 transition-all duration-300"
                                    >
                                        Visit Website
                                        <ArrowTopRightOnSquareIcon className="w-4 h-4" />
                                    </a>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </section>

            {/* Considerations Section */}
            <section className={`py-16 ${darkMode ? 'bg-zinc-800/30' : 'bg-gray-50'}`}>
                <div className="max-w-6xl mx-auto px-6">
                    <h2 className={`text-3xl font-bold text-center mb-12 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                        Before You Buy
                    </h2>
                    <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6">
                        {considerations.map((item) => (
                            <div
                                key={item.title}
                                className={`${darkMode ? 'bg-zinc-800 border-zinc-700' : 'bg-white border-gray-200'} border rounded-xl p-6`}
                            >
                                <h3 className={`font-bold mb-2 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                                    {item.title}
                                </h3>
                                <p className={`text-sm ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                                    {item.desc}
                                </p>
                            </div>
                        ))}
                    </div>
                </div>
            </section>

            {/* CTA */}
            <section className="max-w-6xl mx-auto px-6 py-16">
                <div className={`${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl p-8 text-center`}>
                    <h2 className={`text-2xl font-bold mb-4 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                        Need Software Too?
                    </h2>
                    <p className={`mb-6 ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                        Check out our recommended tuning software to pair with your hardware.
                    </p>
                    <Link
                        to="/software"
                        className="inline-block px-8 py-4 bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500 text-white font-bold rounded-xl shadow-lg hover:shadow-2xl hover:shadow-red-500/25 transition-all duration-300"
                    >
                        View Recommended Software
                    </Link>
                </div>
            </section>

            {/* Become a Tuner CTA */}
            <section className="max-w-6xl mx-auto px-6 pb-16">
                <div className="relative overflow-hidden rounded-3xl bg-gradient-to-r from-orange-500 via-amber-500 to-orange-600 p-1">
                    <div className={`${darkMode ? 'bg-gray-900' : 'bg-gray-100'} rounded-3xl py-16 px-6 text-center`}>
                        <BoltIcon className="w-16 h-16 mx-auto mb-6 text-orange-500" />
                        <h2 className={`text-3xl sm:text-4xl font-bold mb-4 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                            Ready to Start Tuning?
                        </h2>
                        <p className={`mb-8 max-w-2xl mx-auto text-lg ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                            Got your hardware and software? Join TunerSwap and start selling your tunes to drivers worldwide.
                        </p>
                        <Link
                            to="/tuners"
                            className="inline-block px-10 py-4 bg-gradient-to-r from-orange-500 via-amber-500 to-orange-600 text-white font-bold rounded-xl shadow-lg hover:shadow-2xl hover:shadow-orange-500/30 transform hover:-translate-y-1 transition-all duration-300"
                        >
                            Learn About Selling on TunerSwap
                        </Link>
                    </div>
                </div>
            </section>
        </div>
    );
}
