import type { MetaFunction } from '@remix-run/node';
import { useOutletContext } from '@remix-run/react';
import { GarageTab } from '~/components/buyer-admin';
import type { BuyerData, Vehicle } from '~/data/buyer-demo-data';

export const meta: MetaFunction = () => {
    return [{ title: 'My Garage | My Dashboard | TunerSwap' }];
};

interface OutletContext {
    buyer: BuyerData;
    isDemo: boolean;
}

export default function BuyerGarageRoute() {
    const context = useOutletContext<OutletContext>();

    return <GarageTab vehicles={context?.buyer?.vehicles || []} />;
}
