import type { LoaderFunctionArgs, MetaFunction } from '@remix-run/node';
import { json, redirect } from '@remix-run/node';
import { useLoaderData } from '@remix-run/react';
import { OverviewTab } from '~/components/seller-admin';
import { getSellerProfile, getOpenOrders } from '~/providers/seller';
import { getActiveCustomerDetails } from '~/providers/customer/customer';

export const meta: MetaFunction = () => {
    return [{ title: 'Overview | Seller Dashboard | TunerSwap' }];
};

export async function loader({ request }: LoaderFunctionArgs) {
    // Check if user is logged in
    const { activeCustomer } = await getActiveCustomerDetails({ request });
    if (!activeCustomer) {
        return redirect('/tuner-sign-in?redirectTo=/selleradmin');
    }

    const [profileResult, ordersResult] = await Promise.all([
        getSellerProfile(request),
        getOpenOrders(request)
    ]);

    return json({
        profile: profileResult.data,
        orders: ordersResult.data,
        isDemo: profileResult.isDemo || ordersResult.isDemo
    });
}

export default function SellerOverviewRoute() {
    const { profile, orders, isDemo } = useLoaderData<typeof loader>();

    return (
        <>
            {isDemo && (
                <div className="mb-4 px-4 py-2 bg-yellow-500/20 border border-yellow-500/50 rounded-lg text-yellow-500 text-sm">
                    Demo Mode: Displaying sample data (Vendure unavailable)
                </div>
            )}
            <OverviewTab stats={profile?.stats} orders={orders} />
        </>
    );
}
