// TypeScript Types
export type SellerData = {
    id: string;
    name: string;
    email: string;
    phone: string;
    location: string;
    address: string;
    bio: string;
    specialty: string;
    initials: string;
    rating: number;
    reviewCount: number;
    tunesSold: number;
    verified: boolean;
    joinDate: string;
    website: string;
    instagram: string;
    facebook: string;
    hours: Record<string, string>;
    platforms: string[];
    software: string[];
    stripeConnected: boolean;
    paypalConnected: boolean;
};

export type StatsData = {
    totalRevenue: number;
    monthlyRevenue: number;
    totalOrders: number;
    pendingOrders: number;
    totalTunes: number;
    totalParts: number;
    totalServices: number;
    avgRating: number;
    views: number;
};

export type Order = {
    id: string;
    customer: string;
    product: string;
    price: number;
    date: string;
    status: string;
    type: 'tune' | 'part' | 'service';
};

export type Message = {
    id: number;
    from: string;
    subject: string;
    preview: string;
    date: string;
    unread: boolean;
};

export type TuneOrderMessage = {
    from: 'customer' | 'seller';
    text: string;
    time: string;
};

export type TuneOrderFile = {
    name: string;
    size: string;
    date: string;
};

export type TuneOrder = {
    id: string;
    customer: { name: string; email: string; initials: string };
    product: string;
    vehicle: string;
    vin: string;
    mods: string;
    status: string;
    price: number;
    date: string;
    messages: TuneOrderMessage[];
    files: TuneOrderFile[];
};

export type PartsOrder = {
    id: string;
    customer: { name: string; email: string; initials: string };
    product: string;
    quantity: number;
    price: number;
    shippingAddress: string;
    status: string;
    date: string;
    tracking: string | null;
};

export type ServiceOrder = {
    id: string;
    customer: { name: string; email: string; initials: string; phone: string };
    service: string;
    vehicle: string;
    date: string;
    time: string;
    duration: string;
    price: number;
    status: string;
    notes: string;
};

export type CalendarEvent = {
    id: number;
    title: string;
    date: string;
    time: string;
    type: 'service' | 'pickup' | 'call';
    status: string;
};

export type ProductTune = {
    id: number;
    name: string;
    price: number;
    sales: number;
    rating: number;
    status: string;
};

export type ProductPart = {
    id: number;
    name: string;
    price: number;
    stock: number;
    sales: number;
    status: string;
};

export type ProductService = {
    id: number;
    name: string;
    price: number;
    bookings: number;
    rating: number;
    status: string;
};

export type ActiveProducts = {
    tunes: ProductTune[];
    parts: ProductPart[];
    services: ProductService[];
};

