import { Link, useFetcher, useSearchParams } from '@remix-run/react';
import { DataFunctionArgs, json, redirect } from '@remix-run/server-runtime';
import { login } from '~/providers/account/account';
import { ErrorResult } from '~/generated/graphql';
import { XCircleIcon, WrenchScrewdriverIcon } from '@heroicons/react/24/solid';
import { ArrowPathIcon } from '@heroicons/react/24/solid';
import { useTranslation } from 'react-i18next';
import { useDarkMode } from '~/root';

export async function action({ params, request }: DataFunctionArgs) {
    const body = await request.formData();
    const email = body.get('email');
    const password = body.get('password');
    if (typeof email === 'string' && typeof password === 'string') {
        const rememberMe = !!body.get('rememberMe');
        const redirectTo = (body.get('redirectTo') || '/selleradmin') as string;
        const result = await login(email, password, rememberMe, { request });
        if (result.__typename === 'CurrentUser') {
            return redirect(redirectTo, { headers: result._headers });
        } else {
            return json(result, {
                status: 401,
            });
        }
    }
}

export default function TunerSignInPage() {
    const [searchParams] = useSearchParams();
    const loginFetcher = useFetcher<ErrorResult>();
    const { t } = useTranslation();
    const { darkMode } = useDarkMode();

    return (
        <div className={`min-h-screen ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Unique Tuner Hero - Dark industrial theme */}
            <div className="relative overflow-hidden">
                {/* Background with grid pattern */}
                <div className="absolute inset-0 bg-gradient-to-br from-zinc-900 via-gray-900 to-black" />
                <div
                    className="absolute inset-0 opacity-10"
                    style={{
                        backgroundImage: `url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23ffffff' fill-opacity='0.4'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E")`
                    }}
                />
                {/* Accent glow */}
                <div className="absolute top-0 left-1/2 -translate-x-1/2 w-96 h-96 bg-blue-500/20 rounded-full blur-3xl" />
                <div className="absolute bottom-0 right-1/4 w-64 h-64 bg-purple-500/20 rounded-full blur-3xl" />

                <div className="relative max-w-6xl mx-auto py-20 px-6 flex flex-col items-center text-center">
                    {/* Icon badge */}
                    <div className="w-20 h-20 rounded-2xl bg-gradient-to-br from-blue-500/20 to-purple-500/20 border border-blue-500/30 flex items-center justify-center mb-6 backdrop-blur-sm">
                        <WrenchScrewdriverIcon className="w-10 h-10 text-blue-400" />
                    </div>

                    <h1 className="text-4xl sm:text-5xl font-black tracking-tight mb-4">
                        <span className="text-white">Tuner </span>
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-blue-400 via-purple-500 to-blue-400">
                            Portal
                        </span>
                    </h1>
                    <p className="text-lg text-gray-400 max-w-md">
                        Access your seller dashboard, manage listings, and track your sales
                    </p>
                </div>
            </div>

            {/* Form Card */}
            <div className="max-w-md mx-auto -mt-8 relative z-10 px-4 pb-24">
                <div className={`${darkMode ? 'bg-zinc-800/90 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl shadow-2xl shadow-blue-500/10 p-8 backdrop-blur-sm`}>
                    {/* Tuner badge */}
                    <div className="flex items-center justify-center gap-2 mb-6 pb-6 border-b border-zinc-700">
                        <div className="w-2 h-2 rounded-full bg-blue-500 animate-pulse" />
                        <span className={`text-sm font-medium ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                            Seller / Tuner Account
                        </span>
                    </div>

                    <loginFetcher.Form method="post">
                        <fieldset disabled={loginFetcher.state !== 'idle'} className="space-y-5">
                            <input
                                type="hidden"
                                name="redirectTo"
                                value={searchParams.get('redirectTo') ?? '/selleradmin'}
                            />

                            <div>
                                <label
                                    htmlFor="email"
                                    className={`block text-sm font-medium mb-2 ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}
                                >
                                    Email Address
                                </label>
                                <input
                                    id="email"
                                    name="email"
                                    type="email"
                                    autoComplete="email"
                                    required
                                    placeholder="tuner@example.com"
                                    className={`appearance-none block w-full px-4 py-3 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent sm:text-sm disabled:cursor-not-allowed transition-colors ${
                                        darkMode
                                            ? 'bg-zinc-700 border-zinc-600 text-white placeholder-gray-400 disabled:text-gray-500 disabled:bg-zinc-800'
                                            : 'bg-gray-50 border-gray-300 text-gray-900 placeholder-gray-500 disabled:bg-gray-100'
                                    } border`}
                                />
                            </div>

                            <div>
                                <label
                                    htmlFor="password"
                                    className={`block text-sm font-medium mb-2 ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}
                                >
                                    Password
                                </label>
                                <input
                                    id="password"
                                    name="password"
                                    type="password"
                                    autoComplete="current-password"
                                    required
                                    placeholder="Enter your password"
                                    className={`appearance-none block w-full px-4 py-3 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent sm:text-sm disabled:cursor-not-allowed transition-colors ${
                                        darkMode
                                            ? 'bg-zinc-700 border-zinc-600 text-white placeholder-gray-400 disabled:text-gray-500 disabled:bg-zinc-800'
                                            : 'bg-gray-50 border-gray-300 text-gray-900 placeholder-gray-500 disabled:bg-gray-100'
                                    } border`}
                                />
                            </div>

                            <div className="flex items-center justify-between">
                                <div className="flex items-center">
                                    <input
                                        id="rememberMe"
                                        name="rememberMe"
                                        type="checkbox"
                                        className={`h-4 w-4 text-blue-500 focus:ring-blue-500 rounded disabled:cursor-not-allowed ${
                                            darkMode ? 'bg-zinc-700 border-zinc-600' : 'bg-gray-100 border-gray-300'
                                        }`}
                                        defaultChecked
                                    />
                                    <label
                                        htmlFor="rememberMe"
                                        className={`ml-2 block text-sm ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}
                                    >
                                        Remember me
                                    </label>
                                </div>

                                <div className="text-sm">
                                    <a
                                        href="#"
                                        className="font-medium text-blue-400 hover:text-blue-300"
                                    >
                                        Forgot password?
                                    </a>
                                </div>
                            </div>

                            {loginFetcher.data && loginFetcher.state === 'idle' && (
                                <div className={`rounded-xl p-4 ${darkMode ? 'bg-red-900/50 border-red-700' : 'bg-red-50 border-red-200'} border`}>
                                    <div className="flex">
                                        <div className="flex-shrink-0">
                                            <XCircleIcon
                                                className="h-5 w-5 text-red-400"
                                                aria-hidden="true"
                                            />
                                        </div>
                                        <div className="ml-3">
                                            <h3 className={`text-sm font-medium ${darkMode ? 'text-red-300' : 'text-red-800'}`}>
                                                Sign in failed
                                            </h3>
                                            <p className={`text-sm mt-1 ${darkMode ? 'text-red-400' : 'text-red-600'}`}>
                                                {loginFetcher.data.message}
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            )}

                            <div>
                                <button
                                    type="submit"
                                    className="w-full flex justify-center py-3.5 px-4 border border-transparent rounded-xl shadow-lg text-sm font-semibold text-white bg-gradient-to-r from-blue-500 to-purple-500 hover:from-blue-400 hover:to-purple-400 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition-all hover:shadow-blue-500/25"
                                >
                                    <span className="flex gap-3 items-center">
                                        {loginFetcher.state !== 'idle' && (
                                            <ArrowPathIcon className="animate-spin h-5 w-5 text-white" />
                                        )}
                                        Sign In to Dashboard
                                    </span>
                                </button>
                            </div>
                        </fieldset>
                    </loginFetcher.Form>

                    {/* Register link */}
                    <div className="mt-6 pt-6 border-t border-zinc-700 text-center">
                        <p className={`text-sm ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                            Don't have a tuner account?{' '}
                            <Link
                                to="/tunersignup"
                                className="font-medium text-blue-400 hover:text-blue-300"
                            >
                                Apply to become a tuner
                            </Link>
                        </p>
                    </div>
                </div>

                {/* Customer sign-in link */}
                <div className={`mt-6 text-center p-4 rounded-xl ${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-gray-50 border-gray-200'} border`}>
                    <p className={`text-sm ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                        Looking to buy tunes?{' '}
                        <Link
                            to="/sign-in"
                            className="font-medium text-red-400 hover:text-red-300"
                        >
                            Customer sign in →
                        </Link>
                    </p>
                </div>
            </div>
        </div>
    );
}
