import { demoOnly, withDemoFallback, type FallbackResult } from '../utils/vendure-fallback';
import {
    openOrders,
    pastOrders,
    tuneOrders,
    partsOrders,
    serviceOrders,
    type Order,
    type TuneOrder,
    type PartsOrder,
    type ServiceOrder
} from '~/data/seller-demo-data';

/**
 * Get open orders for seller dashboard
 * TODO: Implement actual Vendure order queries when order plugin is ready
 */
export async function getOpenOrders(request?: Request): Promise<FallbackResult<Order[]>> {
    // For now, return demo data
    // When Vendure order queries are implemented:
    // return withDemoFallback(
    //     () => fetchSellerOrders(request, 'open'),
    //     openOrders
    // );
    return demoOnly(openOrders);
}

/**
 * Get past/completed orders
 */
export async function getPastOrders(request?: Request): Promise<FallbackResult<Order[]>> {
    return demoOnly(pastOrders);
}

/**
 * Get tune-specific orders (with chat/file delivery)
 */
export async function getTuneOrders(request?: Request): Promise<FallbackResult<TuneOrder[]>> {
    return demoOnly(tuneOrders);
}

/**
 * Get parts orders (with shipping tracking)
 */
export async function getPartsOrders(request?: Request): Promise<FallbackResult<PartsOrder[]>> {
    return demoOnly(partsOrders);
}

/**
 * Get service appointment orders
 */
export async function getServiceOrders(request?: Request): Promise<FallbackResult<ServiceOrder[]>> {
    return demoOnly(serviceOrders);
}

/**
 * Update order status
 */
export async function updateOrderStatus(
    _orderId: string,
    _status: string,
    request?: Request
): Promise<FallbackResult<{ success: boolean }>> {
    return demoOnly({ success: true });
}
