import { useState } from 'react';
import { Link, Outlet, useLocation, useLoaderData } from '@remix-run/react';
import { LoaderFunctionArgs, json, redirect } from '@remix-run/server-runtime';
import { useDarkMode } from '~/root';
import {
    HomeIcon,
    UserCircleIcon,
    ShoppingCartIcon,
    ClockIcon,
    ChatBubbleLeftRightIcon,
    CpuChipIcon,
    WrenchScrewdriverIcon,
    BuildingStorefrontIcon,
    LinkIcon,
    CubeIcon,
    Bars3Icon,
    XMarkIcon,
    BellIcon,
    ArrowRightOnRectangleIcon,
    CheckBadgeIcon,
    CalendarDaysIcon,
    TruckIcon,
} from '@heroicons/react/24/solid';

import { sellerData as demoSellerData } from '~/data/seller-demo-data';
import { getActiveCustomerDetails } from '~/providers/customer/customer';
import { getSellerProfile } from '~/providers/seller';

export async function loader({ request }: LoaderFunctionArgs) {
    // Check if user is logged in
    const { activeCustomer } = await getActiveCustomerDetails({ request });
    if (!activeCustomer) {
        return redirect('/tuner-sign-in?redirectTo=/selleradmin');
    }

    // Get seller profile
    const profileResult = await getSellerProfile(request);

    return json({
        customer: activeCustomer,
        seller: profileResult.data.seller,
        isDemo: profileResult.isDemo,
    });
}

// Sidebar navigation items with route paths
const sidebarItems = [
    { id: 'overview', label: 'Overview', icon: HomeIcon, path: '/selleradmin' },
    { id: 'profile', label: 'Profile', icon: UserCircleIcon, path: '/selleradmin/profile' },
    { id: 'calendar', label: 'Calendar', icon: CalendarDaysIcon, path: '/selleradmin/calendar' },
    { id: 'divider0', type: 'divider' },
    { id: 'open-orders', label: 'Open Orders', icon: ShoppingCartIcon, badge: 4, path: '/selleradmin/orders/open' },
    { id: 'past-orders', label: 'Past Orders', icon: ClockIcon, path: '/selleradmin/orders/past' },
    { id: 'messages', label: 'Messages', icon: ChatBubbleLeftRightIcon, badge: 2, path: '/selleradmin/messages' },
    { id: 'divider1', type: 'divider' },
    { id: 'tune-orders', label: 'Tune Orders', icon: CpuChipIcon, badge: 2, path: '/selleradmin/orders/tunes' },
    { id: 'parts-orders', label: 'Parts Orders', icon: TruckIcon, badge: 3, path: '/selleradmin/orders/parts' },
    { id: 'service-orders', label: 'Service Orders', icon: BuildingStorefrontIcon, badge: 2, path: '/selleradmin/orders/services' },
    { id: 'divider2', type: 'divider' },
    { id: 'list-tune', label: 'List Tune', icon: CpuChipIcon, path: '/selleradmin/list/tune' },
    { id: 'list-parts', label: 'List Parts', icon: WrenchScrewdriverIcon, path: '/selleradmin/list/parts' },
    { id: 'list-service', label: 'List Service', icon: BuildingStorefrontIcon, path: '/selleradmin/list/service' },
    { id: 'divider3', type: 'divider' },
    { id: 'active-products', label: 'Active Products', icon: CubeIcon, path: '/selleradmin/products' },
    { id: 'connections', label: 'Connections', icon: LinkIcon, path: '/selleradmin/connections' },
];

export default function SellerAdminLayout() {
    const { darkMode } = useDarkMode();
    const location = useLocation();
    const [sidebarOpen, setSidebarOpen] = useState(false);
    const loaderData = useLoaderData<typeof loader>();

    // Use seller from loader data, fallback to demo data
    const sellerData = loaderData?.seller || demoSellerData;

    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

    // Determine active tab from current path
    const getActiveTab = () => {
        const path = location.pathname;
        // Find exact match first
        const exactMatch = sidebarItems.find(item => item.path === path);
        if (exactMatch) return exactMatch.id;
        // Check if current path starts with any item's path
        const partialMatch = sidebarItems
            .filter(item => item.path && path.startsWith(item.path) && item.path !== '/selleradmin')
            .sort((a, b) => (b.path?.length || 0) - (a.path?.length || 0))[0];
        if (partialMatch) return partialMatch.id;
        return 'overview';
    };

    const activeTab = getActiveTab();

    function SidebarContent() {
        return (
            <div className="p-4">
                {/* Seller Info */}
                <div className="mb-6">
                    <div className="flex items-center gap-3 mb-4">
                        <div className="w-12 h-12 rounded-full bg-gradient-to-br from-yellow-400 via-red-500 to-blue-500 flex items-center justify-center text-white font-bold">
                            {sellerData.initials}
                        </div>
                        <div>
                            <p className={`font-semibold ${textPrimary}`}>{sellerData.name}</p>
                            {sellerData.verified && (
                            <div className="flex items-center gap-1">
                                <CheckBadgeIcon className="w-4 h-4 text-blue-500" />
                                <span className="text-sm text-blue-500">Verified</span>
                            </div>
                            )}
                        </div>
                    </div>
                    <Link
                        to={`/sellers/${sellerData.name.toLowerCase().replace(/\s+/g, '-')}`}
                        className={`block text-center py-2 rounded-lg text-sm ${darkMode ? 'bg-zinc-800 hover:bg-zinc-700 text-gray-300' : 'bg-gray-100 hover:bg-gray-200 text-gray-700'} transition-colors`}
                    >
                        View Public Profile
                    </Link>
                </div>

                {/* Navigation */}
                <nav className="space-y-1">
                    {sidebarItems.map((item) => {
                        if (item.type === 'divider') {
                            return <div key={item.id} className={`my-4 border-t ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`} />;
                        }
                        const Icon = item.icon!;
                        const isActive = activeTab === item.id;
                        return (
                            <Link
                                key={item.id}
                                to={item.path!}
                                onClick={() => setSidebarOpen(false)}
                                className={`w-full flex items-center justify-between px-3 py-2 rounded-lg text-left transition-colors ${
                                    isActive
                                        ? 'bg-red-500 text-white'
                                        : `${darkMode ? 'text-gray-300 hover:bg-zinc-800' : 'text-gray-700 hover:bg-gray-100'}`
                                }`}
                            >
                                <div className="flex items-center gap-3">
                                    <Icon className="w-5 h-5" />
                                    <span>{item.label}</span>
                                </div>
                                {item.badge && (
                                    <span className={`px-2 py-0.5 text-xs rounded-full ${
                                        isActive ? 'bg-white/20' : 'bg-red-500 text-white'
                                    }`}>
                                        {item.badge}
                                    </span>
                                )}
                            </Link>
                        );
                    })}
                </nav>

                {/* Back to Site */}
                <div className={`mt-8 pt-4 border-t ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                    <Link
                        to="/"
                        className={`flex items-center gap-3 px-3 py-2 rounded-lg ${darkMode ? 'text-gray-400 hover:text-white hover:bg-zinc-800' : 'text-gray-500 hover:text-gray-900 hover:bg-gray-100'} transition-colors`}
                    >
                        <ArrowRightOnRectangleIcon className="w-5 h-5" />
                        <span>Back to Site</span>
                    </Link>
                </div>
            </div>
        );
    }

    return (
        <div className={`min-h-screen ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Mobile Header */}
            <div className={`lg:hidden fixed top-0 left-0 right-0 z-50 ${darkMode ? 'bg-zinc-900 border-zinc-700' : 'bg-white border-gray-200'} border-b p-4 flex items-center justify-between`}>
                <button onClick={() => setSidebarOpen(true)} className={textPrimary}>
                    <Bars3Icon className="w-6 h-6" />
                </button>
                <span className={`font-bold ${textPrimary}`}>Seller Dashboard</span>
                <button className={textPrimary}>
                    <BellIcon className="w-6 h-6" />
                </button>
            </div>

            {/* Mobile Sidebar Overlay */}
            {sidebarOpen && (
                <div className="lg:hidden fixed inset-0 z-50 bg-black/50" onClick={() => setSidebarOpen(false)}>
                    <div
                        className={`w-72 h-full ${darkMode ? 'bg-zinc-900' : 'bg-white'} p-4`}
                        onClick={(e) => e.stopPropagation()}
                    >
                        <div className="flex items-center justify-between mb-6">
                            <span className={`font-bold text-xl ${textPrimary}`}>Menu</span>
                            <button onClick={() => setSidebarOpen(false)}>
                                <XMarkIcon className={`w-6 h-6 ${textPrimary}`} />
                            </button>
                        </div>
                        <SidebarContent />
                    </div>
                </div>
            )}

            {/* Desktop Sidebar */}
            <div className={`hidden lg:block fixed left-0 top-0 bottom-0 w-64 ${darkMode ? 'bg-zinc-900 border-zinc-700' : 'bg-white border-gray-200'} border-r overflow-y-auto`}>
                <SidebarContent />
            </div>

            {/* Main Content - Outlet renders nested routes */}
            <div className="lg:ml-64 pt-16 lg:pt-0">
                <div className="p-6">
                    <Outlet />
                </div>
            </div>
        </div>
    );
}
