import { useSellerAdminStyles } from './useSellerAdminStyles';
import { pastOrders } from '~/data/seller-demo-data';

export function PastOrdersTab() {
    const { darkMode, cardClasses, textPrimary, textSecondary } = useSellerAdminStyles();

    return (
        <div className="space-y-6">
            <h2 className={`text-2xl font-bold ${textPrimary}`}>Past Orders</h2>

            <div className={cardClasses}>
                <div className="overflow-x-auto">
                    <table className="w-full">
                        <thead className={`border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                            <tr>
                                <th className={`text-left p-4 font-medium ${textSecondary}`}>Order ID</th>
                                <th className={`text-left p-4 font-medium ${textSecondary}`}>Customer</th>
                                <th className={`text-left p-4 font-medium ${textSecondary}`}>Product</th>
                                <th className={`text-left p-4 font-medium ${textSecondary}`}>Type</th>
                                <th className={`text-left p-4 font-medium ${textSecondary}`}>Price</th>
                                <th className={`text-left p-4 font-medium ${textSecondary}`}>Date</th>
                                <th className={`text-left p-4 font-medium ${textSecondary}`}>Status</th>
                            </tr>
                        </thead>
                        <tbody className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                            {pastOrders.map((order) => (
                                <tr key={order.id}>
                                    <td className={`p-4 ${textPrimary}`}>{order.id}</td>
                                    <td className={`p-4 ${textPrimary}`}>{order.customer}</td>
                                    <td className={`p-4 ${textPrimary}`}>{order.product}</td>
                                    <td className="p-4">
                                        <span className={`px-2 py-1 rounded text-xs font-medium ${
                                            order.type === 'tune' ? 'bg-red-500/20 text-red-500' :
                                            order.type === 'part' ? 'bg-orange-500/20 text-orange-500' :
                                            'bg-green-500/20 text-green-500'
                                        }`}>{order.type}</span>
                                    </td>
                                    <td className={`p-4 ${textPrimary}`}>${order.price}</td>
                                    <td className={`p-4 ${textSecondary}`}>{order.date}</td>
                                    <td className="p-4">
                                        <span className={`px-2 py-1 rounded text-xs font-medium ${
                                            order.status === 'completed' ? 'bg-green-500/20 text-green-500' :
                                            'bg-red-500/20 text-red-500'
                                        }`}>{order.status}</span>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
}
