import { MetaFunction, useLoaderData, useSubmit, Link } from '@remix-run/react';
import { DataFunctionArgs } from '@remix-run/server-runtime';
import { withZod } from '@remix-validated-form/with-zod';
import { useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { ValidatedForm } from 'remix-validated-form';
import { Breadcrumbs } from '~/components/Breadcrumbs';
import { CollectionCard } from '~/components/collections/CollectionCard';
import { FacetFilterTracker } from '~/components/facet-filter/facet-filter-tracker';
import { FiltersButton } from '~/components/FiltersButton';
import { FilterableProductGrid } from '~/components/products/FilterableProductGrid';
import { APP_META_TITLE } from '~/constants';
import { filteredSearchLoaderFromPagination } from '~/utils/filtered-search-loader';
import { sdk } from '../graphqlWrapper';
import { useDarkMode } from '~/root';
import {
    Squares2X2Icon,
    FunnelIcon,
    ChevronRightIcon,
} from '@heroicons/react/24/solid';

export const meta: MetaFunction = ({ data }) => {
  return [
    {
      title: data?.collection
        ? `${data.collection?.name} - ${APP_META_TITLE}`
        : APP_META_TITLE,
    },
  ];
};

const paginationLimitMinimumDefault = 25;
const allowedPaginationLimits = new Set<number>([
  paginationLimitMinimumDefault,
  50,
  100,
]);
const { validator, filteredSearchLoader } = filteredSearchLoaderFromPagination(
  allowedPaginationLimits,
  paginationLimitMinimumDefault,
);

export async function loader({ params, request, context }: DataFunctionArgs) {
  const {
    result,
    resultWithoutFacetValueFilters,
    facetValueIds,
    appliedPaginationLimit,
    appliedPaginationPage,
    term,
  } = await filteredSearchLoader({
    params,
    request,
    context,
  });
  const collection = (await sdk.collection({ slug: params.slug })).collection;
  if (!collection?.id || !collection?.name) {
    throw new Response('Not Found', {
      status: 404,
    });
  }

  return {
    term,
    collection,
    result,
    resultWithoutFacetValueFilters,
    facetValueIds,
    appliedPaginationLimit,
    appliedPaginationPage,
  };
}

export default function CollectionSlug() {
  const loaderData = useLoaderData<typeof loader>();
  const { collection, result, resultWithoutFacetValueFilters, facetValueIds } =
    loaderData;
  const [mobileFiltersOpen, setMobileFiltersOpen] = useState(false);
  const facetValuesTracker = useRef(new FacetFilterTracker());
  facetValuesTracker.current.update(
    result,
    resultWithoutFacetValueFilters,
    facetValueIds,
  );
  const submit = useSubmit();
  const { t } = useTranslation();
  const { darkMode } = useDarkMode();

  const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
  const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
  const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

  return (
    <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
      {/* Hero Header */}
      <div className="relative overflow-hidden">
        <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
        <div className="absolute inset-0 bg-gradient-to-r from-yellow-500/10 via-red-500/10 to-blue-500/10 animate-pulse" />

        <div className="relative max-w-6xl mx-auto py-16 px-6">
          {/* Breadcrumbs */}
          <nav className="flex items-center gap-2 text-sm mb-6">
            <Link to="/collections" className="text-gray-400 hover:text-white transition-colors">
              Collections
            </Link>
            {collection.breadcrumbs?.slice(1).map((crumb, index) => (
              <span key={crumb.id} className="flex items-center gap-2">
                <ChevronRightIcon className="w-4 h-4 text-gray-600" />
                {index === collection.breadcrumbs.length - 2 ? (
                  <span className="text-white font-medium">{crumb.name}</span>
                ) : (
                  <Link
                    to={`/collections/${crumb.slug}`}
                    className="text-gray-400 hover:text-white transition-colors"
                  >
                    {crumb.name}
                  </Link>
                )}
              </span>
            ))}
          </nav>

          <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
            <div>
              <h1 className="text-3xl sm:text-4xl lg:text-5xl font-black tracking-tight">
                <span className="text-transparent bg-clip-text bg-gradient-to-r from-yellow-400 via-red-500 to-blue-500">
                  {collection.name}
                </span>
              </h1>
              {result.totalItems > 0 && (
                <p className="mt-2 text-gray-400">
                  {result.totalItems} {result.totalItems === 1 ? 'product' : 'products'} available
                </p>
              )}
            </div>

            <button
              onClick={() => setMobileFiltersOpen(true)}
              className={`flex items-center gap-2 px-4 py-2 rounded-lg font-medium transition-all ${
                darkMode
                  ? 'bg-zinc-800 text-white hover:bg-zinc-700'
                  : 'bg-white text-gray-900 hover:bg-gray-50 shadow'
              }`}
            >
              <FunnelIcon className="w-5 h-5" />
              Filters
              {facetValueIds.length > 0 && (
                <span className="px-2 py-0.5 text-xs rounded-full bg-red-500 text-white">
                  {facetValueIds.length}
                </span>
              )}
            </button>
          </div>
        </div>

        <div className={`absolute bottom-0 left-0 right-0 h-16 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
      </div>

      <div className="max-w-6xl mx-auto px-6">
        {/* Sub-Collections */}
        {collection.children?.length ? (
          <div className="py-8">
            <h2 className={`text-xl font-bold mb-6 ${textPrimary}`}>
              {t('product.collections')}
            </h2>
            <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 gap-4">
              {collection.children.map((child) => (
                <Link
                  key={child.id}
                  to={`/collections/${child.slug}`}
                  prefetch="intent"
                  className={`${cardClasses} overflow-hidden group hover:shadow-xl hover:shadow-red-500/10 transition-all duration-300`}
                >
                  <div className="relative h-32 overflow-hidden">
                    {child.featuredAsset?.preview ? (
                      <img
                        src={`${child.featuredAsset.preview}?w=300&h=200`}
                        alt={child.name}
                        className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-500"
                      />
                    ) : (
                      <div className="w-full h-full bg-gradient-to-br from-yellow-500/50 via-red-500/50 to-blue-500/50 flex items-center justify-center">
                        <Squares2X2Icon className="w-8 h-8 text-white/50" />
                      </div>
                    )}
                    <div className="absolute inset-0 bg-gradient-to-t from-black/70 to-transparent" />
                    <div className="absolute bottom-0 left-0 right-0 p-3">
                      <h3 className="text-sm font-semibold text-white truncate">{child.name}</h3>
                    </div>
                  </div>
                </Link>
              ))}
            </div>
          </div>
        ) : null}

        {/* Products Grid */}
        <ValidatedForm
          validator={withZod(validator)}
          method="get"
          onChange={(e) => submit(e.currentTarget, { preventScrollReset: true })}
        >
          <FilterableProductGrid
            allowedPaginationLimits={allowedPaginationLimits}
            mobileFiltersOpen={mobileFiltersOpen}
            setMobileFiltersOpen={setMobileFiltersOpen}
            {...loaderData}
          />
        </ValidatedForm>

        {/* Empty State */}
        {result.totalItems === 0 && !collection.children?.length && (
          <div className={`${cardClasses} p-12 text-center mt-8`}>
            <Squares2X2Icon className={`w-16 h-16 mx-auto mb-4 ${darkMode ? 'text-zinc-600' : 'text-gray-300'}`} />
            <h3 className={`text-xl font-bold mb-2 ${textPrimary}`}>No Products Found</h3>
            <p className={`mb-6 ${textSecondary}`}>
              This collection doesn't have any products yet. Check back later or browse other collections.
            </p>
            <Link
              to="/collections"
              className="inline-block px-6 py-3 bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500 text-white font-bold rounded-xl hover:shadow-lg transition-all"
            >
              Browse All Collections
            </Link>
          </div>
        )}
      </div>
    </div>
  );
}

export function CatchBoundary() {
  const { t } = useTranslation();
  const { darkMode } = useDarkMode();

  const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
  const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
  const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

  return (
    <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
      <div className="max-w-6xl mx-auto px-6 py-16">
        <div className={`${cardClasses} p-12 text-center`}>
          <Squares2X2Icon className={`w-16 h-16 mx-auto mb-4 ${darkMode ? 'text-zinc-600' : 'text-gray-300'}`} />
          <h2 className={`text-3xl font-bold mb-4 ${textPrimary}`}>
            {t('product.collectionNotFound')}
          </h2>
          <p className={`mb-8 ${textSecondary}`}>
            The collection you're looking for doesn't exist or has been removed.
          </p>
          <Link
            to="/collections"
            className="inline-block px-8 py-4 bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500 text-white font-bold rounded-xl hover:shadow-lg hover:shadow-red-500/25 transition-all"
          >
            Browse All Collections
          </Link>
        </div>
      </div>
    </div>
  );
}
