import { useState } from 'react';
import {
    ShoppingCartIcon,
    CpuChipIcon,
    WrenchScrewdriverIcon,
    CalendarDaysIcon,
    MagnifyingGlassIcon,
} from '@heroicons/react/24/solid';
import { useBuyerAdminStyles } from './useBuyerAdminStyles';
import { buyerOrders } from '~/data/buyer-demo-data';

export function OrdersTab() {
    const { darkMode, cardClasses, inputClasses, textPrimary, textSecondary } = useBuyerAdminStyles();
    const [filter, setFilter] = useState<'all' | 'tune' | 'parts' | 'service'>('all');
    const [selectedOrder, setSelectedOrder] = useState<string | null>(null);

    const filteredOrders = filter === 'all'
        ? buyerOrders
        : buyerOrders.filter(o => o.type === filter);

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <h2 className={`text-2xl font-bold ${textPrimary}`}>My Orders</h2>
                <div className="relative">
                    <MagnifyingGlassIcon className={`w-5 h-5 absolute left-3 top-1/2 -translate-y-1/2 ${textSecondary}`} />
                    <input
                        type="text"
                        placeholder="Search orders..."
                        className={`${inputClasses} pl-10 w-64`}
                    />
                </div>
            </div>

            {/* Filter Tabs */}
            <div className="flex gap-2">
                {(['all', 'tune', 'parts', 'service'] as const).map((tab) => (
                    <button
                        key={tab}
                        onClick={() => setFilter(tab)}
                        className={`px-4 py-2 rounded-lg capitalize transition-colors ${
                            filter === tab
                                ? 'bg-blue-500 text-white'
                                : `${darkMode ? 'bg-zinc-800 text-gray-300 hover:bg-zinc-700' : 'bg-gray-100 text-gray-700 hover:bg-gray-200'}`
                        }`}
                    >
                        {tab === 'all' ? 'All Orders' : tab}
                    </button>
                ))}
            </div>

            <div className="grid lg:grid-cols-3 gap-6">
                {/* Orders List */}
                <div className={`${cardClasses} lg:col-span-1`}>
                    <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                        <h3 className={`font-semibold ${textPrimary}`}>{filteredOrders.length} Orders</h3>
                    </div>
                    <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'} max-h-[600px] overflow-y-auto`}>
                        {filteredOrders.map((order) => (
                            <div
                                key={order.id}
                                onClick={() => setSelectedOrder(order.id)}
                                className={`p-4 cursor-pointer transition-colors ${
                                    selectedOrder === order.id
                                        ? darkMode ? 'bg-zinc-700' : 'bg-gray-100'
                                        : `${darkMode ? 'hover:bg-zinc-700/50' : 'hover:bg-gray-50'}`
                                }`}
                            >
                                <div className="flex items-center justify-between mb-2">
                                    <span className={`font-medium ${textPrimary}`}>{order.id}</span>
                                    <span className={`text-xs px-2 py-0.5 rounded ${
                                        order.status === 'completed' ? 'bg-green-500/20 text-green-500' :
                                        order.status === 'shipped' ? 'bg-blue-500/20 text-blue-500' :
                                        order.status === 'processing' ? 'bg-yellow-500/20 text-yellow-500' :
                                        'bg-purple-500/20 text-purple-500'
                                    }`}>{order.status}</span>
                                </div>
                                <p className={`text-sm ${textPrimary}`}>{order.product}</p>
                                <p className={`text-xs ${textSecondary}`}>{order.seller.name} • ${order.price}</p>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Order Details */}
                <div className={`${cardClasses} lg:col-span-2`}>
                    {selectedOrder ? (() => {
                        const order = buyerOrders.find(o => o.id === selectedOrder)!;
                        return (
                            <>
                                <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                                    <div className="flex items-center justify-between">
                                        <div className="flex items-center gap-3">
                                            <div className={`w-10 h-10 rounded-lg flex items-center justify-center ${
                                                order.type === 'tune' ? 'bg-purple-500/20' :
                                                order.type === 'parts' ? 'bg-orange-500/20' :
                                                'bg-green-500/20'
                                            }`}>
                                                {order.type === 'tune' ? (
                                                    <CpuChipIcon className="w-5 h-5 text-purple-500" />
                                                ) : order.type === 'parts' ? (
                                                    <WrenchScrewdriverIcon className="w-5 h-5 text-orange-500" />
                                                ) : (
                                                    <CalendarDaysIcon className="w-5 h-5 text-green-500" />
                                                )}
                                            </div>
                                            <div>
                                                <h3 className={`font-semibold ${textPrimary}`}>{order.id}</h3>
                                                <p className={`text-sm ${textSecondary}`}>{order.date}</p>
                                            </div>
                                        </div>
                                        <span className={`text-lg font-bold ${textPrimary}`}>${order.price}</span>
                                    </div>
                                </div>
                                <div className="p-4 space-y-4">
                                    <div>
                                        <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Product</h4>
                                        <div className={`p-3 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'}`}>
                                            <p className={textPrimary}>{order.product}</p>
                                            <p className={`text-sm ${textSecondary}`}>For: {order.vehicle}</p>
                                        </div>
                                    </div>

                                    <div>
                                        <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Seller</h4>
                                        <div className="flex items-center gap-3">
                                            <div className={`w-10 h-10 rounded-full bg-blue-500 flex items-center justify-center text-white font-medium`}>
                                                {order.seller.initials}
                                            </div>
                                            <div>
                                                <p className={textPrimary}>{order.seller.name}</p>
                                                <button className="text-sm text-blue-500 hover:text-blue-400">View Profile</button>
                                            </div>
                                        </div>
                                    </div>

                                    {order.tracking && (
                                        <div>
                                            <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Tracking</h4>
                                            <div className={`p-3 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'}`}>
                                                <p className={textPrimary}>{order.tracking}</p>
                                            </div>
                                        </div>
                                    )}

                                    <div className="flex gap-3 pt-4">
                                        {order.downloadUrl && (
                                            <button className="px-4 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded-lg transition-colors">
                                                Download Tune
                                            </button>
                                        )}
                                        {order.tracking && (
                                            <button className="px-4 py-2 bg-green-500 hover:bg-green-600 text-white rounded-lg transition-colors">
                                                Track Package
                                            </button>
                                        )}
                                        <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                                            Contact Seller
                                        </button>
                                    </div>
                                </div>
                            </>
                        );
                    })() : (
                        <div className="flex-1 flex items-center justify-center p-12">
                            <div className="text-center">
                                <ShoppingCartIcon className={`w-16 h-16 mx-auto mb-4 ${textSecondary}`} />
                                <p className={textSecondary}>Select an order to view details</p>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
