import { useState } from 'react';
import {
    ChatBubbleLeftRightIcon,
    PaperAirplaneIcon,
} from '@heroicons/react/24/solid';
import { useBuyerAdminStyles } from './useBuyerAdminStyles';
import { buyerMessages } from '~/data/buyer-demo-data';

export function MessagesTab() {
    const { darkMode, cardClasses, inputClasses, textPrimary, textSecondary } = useBuyerAdminStyles();
    const [selectedConversation, setSelectedConversation] = useState<string | null>(null);
    const [newMessage, setNewMessage] = useState('');

    return (
        <div className="space-y-6">
            <h2 className={`text-2xl font-bold ${textPrimary}`}>Messages</h2>

            <div className="grid lg:grid-cols-3 gap-6">
                {/* Conversations List */}
                <div className={`${cardClasses} lg:col-span-1`}>
                    <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                        <h3 className={`font-semibold ${textPrimary}`}>Conversations</h3>
                    </div>
                    <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'} max-h-[600px] overflow-y-auto`}>
                        {buyerMessages.map((conversation) => (
                            <div
                                key={conversation.id}
                                onClick={() => setSelectedConversation(conversation.id)}
                                className={`p-4 cursor-pointer transition-colors ${
                                    selectedConversation === conversation.id
                                        ? darkMode ? 'bg-zinc-700' : 'bg-gray-100'
                                        : `${darkMode ? 'hover:bg-zinc-700/50' : 'hover:bg-gray-50'}`
                                }`}
                            >
                                <div className="flex items-center gap-3">
                                    <div className="relative">
                                        <div className={`w-10 h-10 rounded-full bg-blue-500 flex items-center justify-center text-white font-medium`}>
                                            {conversation.seller.initials}
                                        </div>
                                        {conversation.unread && (
                                            <div className="absolute -top-1 -right-1 w-3 h-3 bg-blue-500 rounded-full border-2 border-zinc-800" />
                                        )}
                                    </div>
                                    <div className="flex-1 min-w-0">
                                        <div className="flex items-center justify-between">
                                            <span className={`font-medium ${textPrimary}`}>{conversation.seller.name}</span>
                                            <span className={`text-xs ${textSecondary}`}>{conversation.timestamp.split(' ')[0]}</span>
                                        </div>
                                        <p className={`text-sm ${textSecondary} truncate`}>{conversation.lastMessage}</p>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Conversation View */}
                <div className={`${cardClasses} lg:col-span-2 flex flex-col`}>
                    {selectedConversation ? (() => {
                        const conversation = buyerMessages.find(c => c.id === selectedConversation)!;
                        return (
                            <>
                                <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                                    <div className="flex items-center gap-3">
                                        <div className={`w-10 h-10 rounded-full bg-blue-500 flex items-center justify-center text-white font-medium`}>
                                            {conversation.seller.initials}
                                        </div>
                                        <div>
                                            <h3 className={`font-semibold ${textPrimary}`}>{conversation.seller.name}</h3>
                                            <p className={`text-sm ${textSecondary}`}>{conversation.subject}</p>
                                        </div>
                                    </div>
                                </div>

                                {/* Messages */}
                                <div className="flex-1 p-4 space-y-4 overflow-y-auto max-h-[400px]">
                                    {conversation.messages.map((message) => (
                                        <div
                                            key={message.id}
                                            className={`flex ${message.sender === 'buyer' ? 'justify-end' : 'justify-start'}`}
                                        >
                                            <div className={`max-w-[70%] p-3 rounded-lg ${
                                                message.sender === 'buyer'
                                                    ? 'bg-blue-500 text-white'
                                                    : darkMode ? 'bg-zinc-700 text-white' : 'bg-gray-100 text-gray-900'
                                            }`}>
                                                <p>{message.text}</p>
                                                <p className={`text-xs mt-1 ${
                                                    message.sender === 'buyer' ? 'text-blue-200' : textSecondary
                                                }`}>{message.timestamp}</p>
                                            </div>
                                        </div>
                                    ))}
                                </div>

                                {/* Message Input */}
                                <div className={`p-4 border-t ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                                    <div className="flex gap-2">
                                        <input
                                            type="text"
                                            value={newMessage}
                                            onChange={(e) => setNewMessage(e.target.value)}
                                            placeholder="Type a message..."
                                            className={`${inputClasses} flex-1`}
                                        />
                                        <button className="px-4 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded-lg transition-colors">
                                            <PaperAirplaneIcon className="w-5 h-5" />
                                        </button>
                                    </div>
                                </div>
                            </>
                        );
                    })() : (
                        <div className="flex-1 flex items-center justify-center p-12">
                            <div className="text-center">
                                <ChatBubbleLeftRightIcon className={`w-16 h-16 mx-auto mb-4 ${textSecondary}`} />
                                <p className={textSecondary}>Select a conversation to view messages</p>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
