import { useState } from 'react';
import { TruckIcon, MapPinIcon } from '@heroicons/react/24/solid';
import { useSellerAdminStyles } from './useSellerAdminStyles';
import { partsOrders } from '~/data/seller-demo-data';

export function PartsOrdersTab() {
    const { darkMode, cardClasses, textPrimary, textSecondary } = useSellerAdminStyles();
    const [selectedOrder, setSelectedOrder] = useState<string | null>(null);

    return (
        <div className="space-y-6">
            <h2 className={`text-2xl font-bold ${textPrimary}`}>Parts Orders</h2>

            <div className="grid lg:grid-cols-3 gap-6">
                {/* Orders List */}
                <div className={`${cardClasses} lg:col-span-1`}>
                    <div className="p-4 border-b border-zinc-700">
                        <h3 className={`font-semibold ${textPrimary}`}>Active Orders</h3>
                    </div>
                    <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                        {partsOrders.map((order) => (
                            <div
                                key={order.id}
                                onClick={() => setSelectedOrder(order.id)}
                                className={`p-4 cursor-pointer transition-colors ${
                                    selectedOrder === order.id
                                        ? darkMode ? 'bg-zinc-700' : 'bg-gray-100'
                                        : `hover:${darkMode ? 'bg-zinc-700/50' : 'bg-gray-50'}`
                                }`}
                            >
                                <div className="flex items-center justify-between mb-2">
                                    <span className={`font-medium ${textPrimary}`}>{order.customer.name}</span>
                                    <span className={`text-xs px-2 py-0.5 rounded ${
                                        order.status === 'shipped' ? 'bg-green-500/20 text-green-500' :
                                        order.status === 'processing' ? 'bg-blue-500/20 text-blue-500' :
                                        'bg-yellow-500/20 text-yellow-500'
                                    }`}>{order.status}</span>
                                </div>
                                <p className={`text-sm ${textSecondary}`}>{order.product}</p>
                                <p className={`text-xs ${textSecondary}`}>${order.price} • {order.date}</p>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Order Details */}
                <div className={`${cardClasses} lg:col-span-2`}>
                    {selectedOrder ? (() => {
                        const order = partsOrders.find(o => o.id === selectedOrder)!;
                        return (
                            <>
                                <div className="p-4 border-b border-zinc-700">
                                    <div className="flex items-center justify-between">
                                        <div>
                                            <h3 className={`font-semibold ${textPrimary}`}>{order.id}</h3>
                                            <p className={`text-sm ${textSecondary}`}>{order.product} x{order.quantity}</p>
                                        </div>
                                        <span className={`text-lg font-bold ${textPrimary}`}>${order.price}</span>
                                    </div>
                                </div>
                                <div className="p-4 space-y-4">
                                    <div>
                                        <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Customer</h4>
                                        <div className="flex items-center gap-3">
                                            <div className={`w-10 h-10 rounded-full bg-orange-500 flex items-center justify-center text-white font-medium`}>
                                                {order.customer.initials}
                                            </div>
                                            <div>
                                                <p className={textPrimary}>{order.customer.name}</p>
                                                <p className={`text-sm ${textSecondary}`}>{order.customer.email}</p>
                                            </div>
                                        </div>
                                    </div>

                                    <div>
                                        <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Shipping Address</h4>
                                        <div className={`p-3 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'}`}>
                                            <div className="flex items-start gap-2">
                                                <MapPinIcon className="w-4 h-4 text-orange-500 mt-0.5" />
                                                <p className={textPrimary}>{order.shippingAddress}</p>
                                            </div>
                                        </div>
                                    </div>

                                    {order.tracking && (
                                        <div>
                                            <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Tracking</h4>
                                            <div className={`p-3 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'}`}>
                                                <p className={textPrimary}>{order.tracking}</p>
                                            </div>
                                        </div>
                                    )}

                                    <div className="flex gap-3 pt-4">
                                        {order.status === 'pending' && (
                                            <button className="px-4 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded-lg transition-colors">
                                                Process Order
                                            </button>
                                        )}
                                        {order.status === 'processing' && (
                                            <button className="px-4 py-2 bg-green-500 hover:bg-green-600 text-white rounded-lg transition-colors">
                                                Mark as Shipped
                                            </button>
                                        )}
                                        <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                                            Print Label
                                        </button>
                                        <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                                            Contact Customer
                                        </button>
                                    </div>
                                </div>
                            </>
                        );
                    })() : (
                        <div className="flex-1 flex items-center justify-center p-12">
                            <div className="text-center">
                                <TruckIcon className={`w-16 h-16 mx-auto mb-4 ${textSecondary}`} />
                                <p className={textSecondary}>Select an order to view details</p>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
