import { Link, useFetcher, useSearchParams } from '@remix-run/react';
import { DataFunctionArgs, json, redirect } from '@remix-run/server-runtime';
import { login } from '~/providers/account/account';
import { ErrorResult } from '~/generated/graphql';
import { XCircleIcon } from '@heroicons/react/24/solid';
import { Button } from '~/components/Button';
import { ArrowPathIcon } from '@heroicons/react/24/solid';
import { useTranslation } from 'react-i18next';

export async function action({ params, request }: DataFunctionArgs) {
  const body = await request.formData();
  const email = body.get('email');
  const password = body.get('password');
  if (typeof email === 'string' && typeof password === 'string') {
    const rememberMe = !!body.get('rememberMe');
    const redirectTo = (body.get('redirectTo') || '/buyeradmin') as string;
    const result = await login(email, password, rememberMe, { request });
    if (result.__typename === 'CurrentUser') {
      return redirect(redirectTo, { headers: result._headers });
    } else {
      return json(result, {
        status: 401,
      });
    }
  }
}

export default function SignInPage() {
  const [searchParams] = useSearchParams();
  const login = useFetcher<ErrorResult>();
  const { t } = useTranslation();

  return (
    <div className="min-h-screen bg-gray-900 pb-24">
      {/* Hero Header */}
      <header className="py-24 px-6 text-center bg-gradient-to-r from-yellow-400 via-red-500 to-blue-500">
        <h1 className="text-5xl font-extrabold text-white mb-4">
          {t('account.signInTitle')}
        </h1>
        <p className="text-lg text-white/90">
          {t('common.or')}{' '}
          <Link
            to="/sign-up"
            className="font-medium text-white underline hover:text-yellow-200"
          >
            {t('account.register')}
          </Link>
        </p>
      </header>

      {/* Form Card */}
      <div className="max-w-md mx-auto -mt-16 relative z-10 px-4">
        <div className="bg-zinc-800 rounded-2xl shadow-2xl p-8">
          <login.Form method="post">
            <fieldset disabled={login.state !== 'idle'} className="space-y-6">
              <input
                type="hidden"
                name="redirectTo"
                value={searchParams.get('redirectTo') ?? undefined}
              />
              <div>
                <label
                  htmlFor="email"
                  className="block text-sm font-medium text-gray-300"
                >
                  {t('account.emailAddress')}
                </label>
                <div className="mt-1">
                  <input
                    id="email"
                    name="email"
                    type="email"
                    autoComplete="email"
                    required
                    placeholder={t('account.emailAddress')}
                    className="appearance-none block w-full px-4 py-3 bg-zinc-700 border border-zinc-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-red-500 focus:border-transparent sm:text-sm disabled:text-gray-500 disabled:bg-zinc-800 disabled:cursor-not-allowed"
                  />
                </div>
              </div>

              <div>
                <label
                  htmlFor="password"
                  className="block text-sm font-medium text-gray-300"
                >
                  {t('account.password')}
                </label>
                <div className="mt-1">
                  <input
                    id="password"
                    name="password"
                    type="password"
                    autoComplete="current-password"
                    required
                    placeholder={t('account.password')}
                    className="appearance-none block w-full px-4 py-3 bg-zinc-700 border border-zinc-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-red-500 focus:border-transparent sm:text-sm disabled:text-gray-500 disabled:bg-zinc-800 disabled:cursor-not-allowed"
                  />
                </div>
              </div>

              <div className="flex items-center justify-between">
                <div className="flex items-center">
                  <input
                    id="rememberMe"
                    name="rememberMe"
                    type="checkbox"
                    className="h-4 w-4 text-red-500 focus:ring-red-500 bg-zinc-700 border-zinc-600 rounded disabled:bg-zinc-800 disabled:cursor-not-allowed"
                    defaultChecked
                  />
                  <label
                    htmlFor="rememberMe"
                    className="ml-2 block text-sm text-gray-300"
                  >
                    {t('account.rememberMe')}
                  </label>
                </div>

                <div className="text-sm">
                  <a
                    href="#"
                    className="font-medium text-red-400 hover:text-red-300"
                  >
                    {t('account.forgotPassword')}
                  </a>
                </div>
              </div>

              {login.data && login.state === 'idle' && (
                <div className="rounded-lg bg-red-900/50 border border-red-700 p-4">
                  <div className="flex">
                    <div className="flex-shrink-0">
                      <XCircleIcon
                        className="h-5 w-5 text-red-400"
                        aria-hidden="true"
                      />
                    </div>
                    <div className="ml-3">
                      <h3 className="text-sm font-medium text-red-300">
                        {t('account.errorSignIn')}
                      </h3>
                      <p className="text-sm text-red-400 mt-2">
                        {login.data.message}
                      </p>
                    </div>
                  </div>
                </div>
              )}

              <div>
                <button
                  type="submit"
                  className="w-full flex justify-center py-3 px-4 border border-transparent rounded-lg shadow-lg text-sm font-semibold text-white bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500 hover:from-yellow-400 hover:via-red-400 hover:to-blue-400 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 focus:ring-offset-zinc-800 transition-all"
                >
                  <span className="flex gap-4 items-center">
                    {login.state !== 'idle' && (
                      <ArrowPathIcon className="animate-spin h-5 w-5 text-white" />
                    )}
                    {t('account.signIn')}
                  </span>
                </button>
              </div>
            </fieldset>
          </login.Form>

          <div className="mt-6 text-center">
            <p className="text-sm text-gray-400">
              Don't have an account?{' '}
              <Link
                to="/sign-up"
                className="font-medium text-red-400 hover:text-red-300"
              >
                Sign up here
              </Link>
            </p>
          </div>
        </div>

        {/* Tuner sign-in link */}
        <div className="mt-6 text-center p-4 rounded-xl bg-zinc-800/50 border border-zinc-700">
          <p className="text-sm text-gray-400">
            This sign in is for customers.{' '}
            <Link
              to="/tuner-sign-in"
              className="font-medium text-blue-400 hover:text-blue-300"
            >
              Seller / Tuner sign in →
            </Link>
          </p>
        </div>
      </div>
    </div>
  );
}
