/**
 * Vendure Fallback Utility
 *
 * Tries to fetch data from Vendure API, falls back to demo data if unavailable.
 * Returns isDemo flag so UI can show an indicator.
 */

export interface FallbackResult<T> {
    data: T;
    isDemo: boolean;
    error?: string;
}

/**
 * Wraps a Vendure API call with demo data fallback
 *
 * @param vendureFetch - Async function that fetches from Vendure
 * @param demoData - Fallback data to use if Vendure is unavailable
 * @returns Object with data, isDemo flag, and optional error message
 */
export async function withDemoFallback<T>(
    vendureFetch: () => Promise<T>,
    demoData: T
): Promise<FallbackResult<T>> {
    try {
        const data = await vendureFetch();
        return { data, isDemo: false };
    } catch (error) {
        const errorMessage = error instanceof Error ? error.message : 'Unknown error';
        console.warn(`Vendure unavailable (${errorMessage}), using demo data`);
        return {
            data: demoData,
            isDemo: true,
            error: errorMessage
        };
    }
}

/**
 * Creates a demo-only result (for when we don't have a Vendure query yet)
 */
export function demoOnly<T>(demoData: T): FallbackResult<T> {
    return {
        data: demoData,
        isDemo: true,
        error: 'Vendure query not implemented'
    };
}

/**
 * Combines multiple fallback results into one
 * isDemo is true if ANY of the results are demo data
 */
export function combineFallbackResults<T extends Record<string, unknown>>(
    results: { [K in keyof T]: FallbackResult<T[K]> }
): FallbackResult<T> {
    const data = {} as T;
    let isDemo = false;
    const errors: string[] = [];

    for (const key in results) {
        data[key] = results[key].data;
        if (results[key].isDemo) {
            isDemo = true;
        }
        if (results[key].error) {
            errors.push(`${key}: ${results[key].error}`);
        }
    }

    return {
        data,
        isDemo,
        error: errors.length > 0 ? errors.join('; ') : undefined
    };
}
