import { useState } from 'react';
import { DocumentArrowUpIcon, PaperAirplaneIcon } from '@heroicons/react/24/solid';
import { ChatBubbleLeftIcon } from '@heroicons/react/24/outline';
import { useSellerAdminStyles } from './useSellerAdminStyles';
import { tuneOrders } from '~/data/seller-demo-data';

export function TuneOrdersTab() {
    const { darkMode, cardClasses, inputClasses, textPrimary, textSecondary } = useSellerAdminStyles();
    const [selectedTuneOrder, setSelectedTuneOrder] = useState<string | null>(null);
    const [newMessage, setNewMessage] = useState('');

    const selectedOrder = tuneOrders.find(o => o.id === selectedTuneOrder);

    return (
        <div className="space-y-6">
            <h2 className={`text-2xl font-bold ${textPrimary}`}>Tune Orders</h2>

            <div className="grid lg:grid-cols-3 gap-6">
                {/* Order List */}
                <div className={`${cardClasses} lg:col-span-1`}>
                    <div className="p-4 border-b border-zinc-700">
                        <h3 className={`font-semibold ${textPrimary}`}>Active Orders</h3>
                    </div>
                    <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                        {tuneOrders.map((order) => (
                            <div
                                key={order.id}
                                onClick={() => setSelectedTuneOrder(order.id)}
                                className={`p-4 cursor-pointer transition-colors ${
                                    selectedTuneOrder === order.id
                                        ? darkMode ? 'bg-zinc-700' : 'bg-gray-100'
                                        : `hover:${darkMode ? 'bg-zinc-700/50' : 'bg-gray-50'}`
                                }`}
                            >
                                <div className="flex items-center justify-between mb-2">
                                    <span className={`font-medium ${textPrimary}`}>{order.customer.name}</span>
                                    <span className={`text-xs px-2 py-0.5 rounded ${
                                        order.status === 'awaiting_file' ? 'bg-yellow-500/20 text-yellow-500' :
                                        order.status === 'revision_requested' ? 'bg-orange-500/20 text-orange-500' :
                                        'bg-green-500/20 text-green-500'
                                    }`}>{order.status.replace('_', ' ')}</span>
                                </div>
                                <p className={`text-sm ${textSecondary}`}>{order.product}</p>
                                <p className={`text-xs ${textSecondary}`}>{order.vehicle}</p>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Chat Window */}
                <div className={`${cardClasses} lg:col-span-2 flex flex-col`} style={{ minHeight: '500px' }}>
                    {selectedOrder ? (
                        <>
                            {/* Header */}
                            <div className="p-4 border-b border-zinc-700">
                                <div className="flex items-center justify-between">
                                    <div>
                                        <h3 className={`font-semibold ${textPrimary}`}>{selectedOrder.customer.name}</h3>
                                        <p className={`text-sm ${textSecondary}`}>{selectedOrder.product} • {selectedOrder.vehicle}</p>
                                    </div>
                                    <span className={`text-lg font-bold ${textPrimary}`}>${selectedOrder.price}</span>
                                </div>
                                <div className={`mt-2 text-sm ${textSecondary}`}>
                                    <span>Mods: {selectedOrder.mods}</span>
                                </div>
                            </div>

                            {/* Messages */}
                            <div className="flex-1 p-4 overflow-y-auto space-y-4">
                                {selectedOrder.messages.map((msg, idx) => (
                                    <div key={idx} className={`flex ${msg.from === 'seller' ? 'justify-end' : 'justify-start'}`}>
                                        <div className={`max-w-[80%] p-3 rounded-xl ${
                                            msg.from === 'seller'
                                                ? 'bg-red-500 text-white'
                                                : darkMode ? 'bg-zinc-700 text-white' : 'bg-gray-100 text-gray-900'
                                        }`}>
                                            <p className="text-sm">{msg.text}</p>
                                            <p className={`text-xs mt-1 ${msg.from === 'seller' ? 'text-red-200' : textSecondary}`}>{msg.time}</p>
                                        </div>
                                    </div>
                                ))}
                            </div>

                            {/* Files */}
                            {selectedOrder.files.length > 0 && (
                                <div className="px-4 py-2 border-t border-zinc-700">
                                    <p className={`text-xs font-medium mb-2 ${textSecondary}`}>Attached Files</p>
                                    <div className="flex flex-wrap gap-2">
                                        {selectedOrder.files.map((file, idx) => (
                                            <div key={idx} className={`flex items-center gap-2 px-3 py-1 rounded ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'}`}>
                                                <DocumentArrowUpIcon className="w-4 h-4 text-blue-500" />
                                                <span className={`text-sm ${textPrimary}`}>{file.name}</span>
                                                <span className={`text-xs ${textSecondary}`}>{file.size}</span>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            )}

                            {/* Input */}
                            <div className="p-4 border-t border-zinc-700">
                                <div className="flex items-center gap-2">
                                    <button className={`p-2 rounded-lg ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} transition-colors`}>
                                        <DocumentArrowUpIcon className="w-5 h-5 text-blue-500" />
                                    </button>
                                    <input
                                        type="text"
                                        placeholder="Type a message..."
                                        value={newMessage}
                                        onChange={(e) => setNewMessage(e.target.value)}
                                        className={`${inputClasses} flex-1`}
                                    />
                                    <button className="p-2 bg-red-500 hover:bg-red-600 text-white rounded-lg transition-colors">
                                        <PaperAirplaneIcon className="w-5 h-5" />
                                    </button>
                                </div>
                            </div>
                        </>
                    ) : (
                        <div className="flex-1 flex items-center justify-center">
                            <div className="text-center">
                                <ChatBubbleLeftIcon className={`w-16 h-16 mx-auto mb-4 ${textSecondary}`} />
                                <p className={textSecondary}>Select an order to view conversation</p>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
