import { useDarkMode } from '~/root';
import { RocketLaunchIcon, CheckCircleIcon, ClockIcon, BeakerIcon } from '@heroicons/react/24/solid';
import { Link } from '@remix-run/react';

export default function RoadmapPage() {
    const { darkMode } = useDarkMode();

    const phases = [
        {
            phase: 'Phase 1',
            title: 'Foundation',
            status: 'completed',
            timeline: 'Q4 2025',
            items: [
                { title: 'Core Marketplace', description: 'Browse and purchase tunes, parts, and services', done: true },
                { title: 'User Authentication', description: 'Sign up, sign in, and account management', done: true },
                { title: 'Seller Profiles', description: 'Detailed tuner profiles with ratings and reviews', done: true },
                { title: 'Buyer Dashboard', description: 'Order history, garage, and saved items', done: true },
                { title: 'Seller Dashboard', description: 'Order management and product listings', done: true },
                { title: 'Dark Mode', description: 'Full site dark/light theme support', done: true },
            ],
        },
        {
            phase: 'Phase 2',
            title: 'Community & Content',
            status: 'in-progress',
            timeline: 'Q1 2026',
            items: [
                { title: 'Forums', description: 'Community discussion boards by category', done: true },
                { title: 'Blog', description: 'News, tutorials, and featured content', done: false },
                { title: 'Messaging System', description: 'Direct messaging between buyers and sellers', done: false },
                { title: 'Review System', description: 'Verified purchase reviews with photos', done: false },
                { title: 'Notifications', description: 'Real-time alerts for orders and messages', done: false },
            ],
        },
        {
            phase: 'Phase 3',
            title: 'Advanced Features',
            status: 'planned',
            timeline: 'Q2 2026',
            items: [
                { title: 'Tune File Delivery', description: 'Secure digital file delivery system', done: false },
                { title: 'Subscription Plans', description: 'Recurring support and update packages', done: false },
                { title: 'Analytics Dashboard', description: 'Sales, traffic, and performance insights', done: false },
                { title: 'Affiliate Program', description: 'Referral rewards for tuners and buyers', done: false },
                { title: 'Mobile App', description: 'iOS and Android companion apps', done: false },
            ],
        },
        {
            phase: 'Phase 4',
            title: 'Ecosystem Expansion',
            status: 'planned',
            timeline: 'Q3-Q4 2026',
            items: [
                { title: 'API for Partners', description: 'Public API for third-party integrations', done: false },
                { title: 'Dyno Verification', description: 'Official dyno result verification badges', done: false },
                { title: 'Live Tuning Sessions', description: 'Video call integration for remote tuning', done: false },
                { title: 'Event Listings', description: 'Car meets, dyno days, and track events', done: false },
                { title: 'Sponsorship Program', description: 'Featured placement for top tuners', done: false },
            ],
        },
    ];

    const getStatusColor = (status: string) => {
        switch (status) {
            case 'completed': return 'bg-green-500';
            case 'in-progress': return 'bg-yellow-500';
            default: return 'bg-gray-500';
        }
    };

    const getStatusLabel = (status: string) => {
        switch (status) {
            case 'completed': return 'Completed';
            case 'in-progress': return 'In Progress';
            default: return 'Planned';
        }
    };

    const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-purple-500/10 via-blue-500/10 to-green-500/10 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-32 px-6 flex flex-col items-center text-center">
                    <RocketLaunchIcon className="w-16 h-16 text-purple-500 mb-6" />
                    <h1 className="text-5xl sm:text-6xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-purple-400 via-blue-500 to-green-500">
                            Roadmap
                        </span>
                    </h1>
                    <p className="mt-6 text-xl text-gray-300 max-w-2xl">
                        See what we're building and what's coming next for TunerSwap.
                    </p>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            {/* Legend */}
            <div className="max-w-4xl mx-auto px-6 -mt-8 relative z-10">
                <div className={`${cardClasses} p-4`}>
                    <div className="flex flex-wrap items-center justify-center gap-6">
                        <div className="flex items-center gap-2">
                            <CheckCircleIcon className="w-5 h-5 text-green-500" />
                            <span className={textSecondary}>Completed</span>
                        </div>
                        <div className="flex items-center gap-2">
                            <BeakerIcon className="w-5 h-5 text-yellow-500" />
                            <span className={textSecondary}>In Progress</span>
                        </div>
                        <div className="flex items-center gap-2">
                            <ClockIcon className="w-5 h-5 text-gray-500" />
                            <span className={textSecondary}>Planned</span>
                        </div>
                    </div>
                </div>
            </div>

            {/* Roadmap Timeline */}
            <main className="max-w-4xl mx-auto px-6 py-16">
                <div className="space-y-8">
                    {phases.map((phase, index) => (
                        <div key={phase.phase} className={`${cardClasses} p-8 hover:shadow-2xl hover:shadow-purple-500/10 transition-all duration-300`}>
                            {/* Phase Header */}
                            <div className={`flex flex-wrap items-center gap-4 mb-6 pb-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                                <span className="px-4 py-2 rounded-full bg-gradient-to-r from-purple-500 to-blue-500 text-white text-sm font-bold">
                                    {phase.phase}
                                </span>
                                <h2 className={`text-2xl font-bold ${textPrimary}`}>{phase.title}</h2>
                                <span className={`px-3 py-1 rounded-full text-xs font-semibold ${
                                    phase.status === 'completed' ? 'bg-green-500/20 text-green-400' :
                                    phase.status === 'in-progress' ? 'bg-yellow-500/20 text-yellow-400' :
                                    'bg-gray-500/20 text-gray-400'
                                }`}>
                                    {getStatusLabel(phase.status)}
                                </span>
                                <span className={`ml-auto text-sm font-medium ${textSecondary}`}>{phase.timeline}</span>
                            </div>

                            {/* Phase Items */}
                            <ul className="space-y-4">
                                {phase.items.map((item) => (
                                    <li key={item.title} className={`flex items-start gap-4 p-3 rounded-xl ${darkMode ? 'bg-zinc-800/50' : 'bg-gray-50'}`}>
                                        {item.done ? (
                                            <CheckCircleIcon className="w-6 h-6 text-green-500 flex-shrink-0 mt-0.5" />
                                        ) : (
                                            <div className={`w-6 h-6 rounded-full border-2 flex-shrink-0 mt-0.5 ${darkMode ? 'border-zinc-600' : 'border-gray-300'}`} />
                                        )}
                                        <div className="flex-1">
                                            <div className={`font-semibold ${item.done ? 'text-green-400' : textPrimary}`}>
                                                {item.title}
                                            </div>
                                            <div className={`text-sm mt-1 ${textSecondary}`}>{item.description}</div>
                                        </div>
                                    </li>
                                ))}
                            </ul>
                        </div>
                    ))}
                </div>

                {/* Feedback CTA */}
                <div className="mt-16">
                    <div className={`${cardClasses} p-8 text-center`}>
                        <RocketLaunchIcon className={`w-12 h-12 mx-auto mb-4 text-purple-500`} />
                        <h3 className={`text-xl font-bold mb-2 ${textPrimary}`}>Have a Feature Request?</h3>
                        <p className={`${textSecondary} max-w-xl mx-auto mb-4`}>
                            We're building TunerSwap for the community. Let us know what features would help you most.
                        </p>
                        <Link
                            to="/contact"
                            className="inline-block px-6 py-3 bg-gradient-to-r from-purple-500 to-blue-500 text-white font-bold rounded-xl hover:shadow-lg hover:shadow-purple-500/25 transition-all"
                        >
                            Submit Feedback
                        </Link>
                    </div>
                </div>
            </main>
        </div>
    );
}
