import {
    CurrencyDollarIcon,
    ChartBarIcon,
    ShoppingCartIcon,
    StarIcon,
    EyeIcon,
    CpuChipIcon,
    WrenchScrewdriverIcon,
    BuildingStorefrontIcon,
} from '@heroicons/react/24/solid';
import { useSellerAdminStyles } from './useSellerAdminStyles';
import { statsData as demoStats, openOrders as demoOrders } from '~/data/seller-demo-data';
import type { StatsData, Order } from '~/data/seller-demo-data';

interface OverviewTabProps {
    stats?: StatsData;
    orders?: Order[];
}

export function OverviewTab({ stats, orders }: OverviewTabProps) {
    const { cardClasses, textPrimary, textSecondary } = useSellerAdminStyles();

    // Use provided data or fall back to demo data
    const statsData = stats || demoStats;
    const openOrders = orders || demoOrders;

    return (
        <div className="space-y-6">
            <h2 className={`text-2xl font-bold ${textPrimary}`}>Dashboard Overview</h2>

            {/* Stats Grid */}
            <div className="grid grid-cols-2 lg:grid-cols-4 gap-4">
                <div className={`${cardClasses} p-4`}>
                    <div className="flex items-center justify-between">
                        <div>
                            <p className={`text-sm ${textSecondary}`}>Total Revenue</p>
                            <p className={`text-2xl font-bold ${textPrimary}`}>${statsData.totalRevenue.toLocaleString()}</p>
                        </div>
                        <CurrencyDollarIcon className="w-10 h-10 text-green-500" />
                    </div>
                </div>
                <div className={`${cardClasses} p-4`}>
                    <div className="flex items-center justify-between">
                        <div>
                            <p className={`text-sm ${textSecondary}`}>This Month</p>
                            <p className={`text-2xl font-bold ${textPrimary}`}>${statsData.monthlyRevenue.toLocaleString()}</p>
                        </div>
                        <ChartBarIcon className="w-10 h-10 text-blue-500" />
                    </div>
                </div>
                <div className={`${cardClasses} p-4`}>
                    <div className="flex items-center justify-between">
                        <div>
                            <p className={`text-sm ${textSecondary}`}>Pending Orders</p>
                            <p className={`text-2xl font-bold ${textPrimary}`}>{statsData.pendingOrders}</p>
                        </div>
                        <ShoppingCartIcon className="w-10 h-10 text-orange-500" />
                    </div>
                </div>
                <div className={`${cardClasses} p-4`}>
                    <div className="flex items-center justify-between">
                        <div>
                            <p className={`text-sm ${textSecondary}`}>Avg Rating</p>
                            <p className={`text-2xl font-bold ${textPrimary}`}>{statsData.avgRating}</p>
                        </div>
                        <StarIcon className="w-10 h-10 text-yellow-500" />
                    </div>
                </div>
            </div>

            {/* Quick Stats */}
            <div className="grid md:grid-cols-3 gap-4">
                <div className={`${cardClasses} p-4`}>
                    <h3 className={`font-semibold mb-3 ${textPrimary}`}>Products</h3>
                    <div className="space-y-2">
                        <div className="flex justify-between">
                            <span className={textSecondary}>Tunes</span>
                            <span className={textPrimary}>{statsData.totalTunes}</span>
                        </div>
                        <div className="flex justify-between">
                            <span className={textSecondary}>Parts</span>
                            <span className={textPrimary}>{statsData.totalParts}</span>
                        </div>
                        <div className="flex justify-between">
                            <span className={textSecondary}>Services</span>
                            <span className={textPrimary}>{statsData.totalServices}</span>
                        </div>
                    </div>
                </div>
                <div className={`${cardClasses} p-4`}>
                    <h3 className={`font-semibold mb-3 ${textPrimary}`}>Recent Activity</h3>
                    <div className="space-y-2">
                        {openOrders.slice(0, 3).map((order) => (
                            <div key={order.id} className="flex items-center justify-between text-sm">
                                <span className={textSecondary}>{order.customer}</span>
                                <span className={`px-2 py-0.5 rounded text-xs ${
                                    order.status === 'pending' ? 'bg-yellow-500/20 text-yellow-500' :
                                    order.status === 'in_progress' ? 'bg-blue-500/20 text-blue-500' :
                                    'bg-green-500/20 text-green-500'
                                }`}>{order.status}</span>
                            </div>
                        ))}
                    </div>
                </div>
                <div className={`${cardClasses} p-4`}>
                    <h3 className={`font-semibold mb-3 ${textPrimary}`}>Profile Views</h3>
                    <div className="flex items-center gap-4">
                        <EyeIcon className="w-12 h-12 text-purple-500" />
                        <div>
                            <p className={`text-3xl font-bold ${textPrimary}`}>{statsData.views.toLocaleString()}</p>
                            <p className={`text-sm ${textSecondary}`}>All time</p>
                        </div>
                    </div>
                </div>
            </div>

            {/* Recent Orders */}
            <div className={cardClasses}>
                <div className="p-4 border-b border-zinc-700">
                    <h3 className={`font-semibold ${textPrimary}`}>Recent Orders</h3>
                </div>
                <div className="divide-y divide-zinc-700">
                    {openOrders.map((order) => (
                        <div key={order.id} className="p-4 flex items-center justify-between">
                            <div className="flex items-center gap-4">
                                <div className={`w-10 h-10 rounded-full flex items-center justify-center ${
                                    order.type === 'tune' ? 'bg-red-500/20 text-red-500' :
                                    order.type === 'part' ? 'bg-orange-500/20 text-orange-500' :
                                    'bg-green-500/20 text-green-500'
                                }`}>
                                    {order.type === 'tune' ? <CpuChipIcon className="w-5 h-5" /> :
                                     order.type === 'part' ? <WrenchScrewdriverIcon className="w-5 h-5" /> :
                                     <BuildingStorefrontIcon className="w-5 h-5" />}
                                </div>
                                <div>
                                    <p className={`font-medium ${textPrimary}`}>{order.product}</p>
                                    <p className={`text-sm ${textSecondary}`}>{order.customer} • {order.id}</p>
                                </div>
                            </div>
                            <div className="text-right">
                                <p className={`font-medium ${textPrimary}`}>${order.price}</p>
                                <p className={`text-sm ${textSecondary}`}>{order.date}</p>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}
