import { useDarkMode } from '~/root';
import { Link } from '@remix-run/react';
import {
    WrenchScrewdriverIcon,
    CurrencyDollarIcon,
    ChartBarIcon,
    CloudArrowUpIcon,
    ShieldCheckIcon,
    UserGroupIcon,
    RocketLaunchIcon,
    CheckCircleIcon,
} from '@heroicons/react/24/solid';

export default function TunersPage() {
    const { darkMode } = useDarkMode();

    const benefits = [
        {
            icon: CurrencyDollarIcon,
            title: "Earn Passive Income",
            desc: "Upload your tunes once and generate revenue from every sale, automatically. Set your own prices and keep the majority of earnings.",
            color: "text-green-500"
        },
        {
            icon: CloudArrowUpIcon,
            title: "Unlimited Uploads",
            desc: "List as many tunes as you want with no limits. Support multiple platforms, vehicles, and configurations.",
            color: "text-blue-500"
        },
        {
            icon: ChartBarIcon,
            title: "Analytics Dashboard",
            desc: "Track your sales, downloads, and revenue in real-time. Understand your customers and optimize your offerings.",
            color: "text-yellow-500"
        },
        {
            icon: ShieldCheckIcon,
            title: "Secure Delivery",
            desc: "We handle file protection and secure delivery so your work is protected and customers get their files safely.",
            color: "text-red-500"
        },
        {
            icon: UserGroupIcon,
            title: "Build Your Brand",
            desc: "Create a professional tuner profile, showcase your expertise, and build a loyal customer base.",
            color: "text-purple-500"
        },
        {
            icon: WrenchScrewdriverIcon,
            title: "Focus on Tuning",
            desc: "We handle payments, delivery, and customer management so you can focus on what you do best — tuning.",
            color: "text-orange-500"
        },
    ];

    const steps = [
        { num: 1, title: "Create Your Account", desc: "Sign up and complete your tuner profile with your experience and specialties." },
        { num: 2, title: "Upload Your Tunes", desc: "Add your calibration files with detailed descriptions, vehicle compatibility, and pricing." },
        { num: 3, title: "Start Selling", desc: "Your tunes go live and customers can purchase them instantly. You get paid automatically." },
    ];

    const features = [
        "Set your own prices",
        "Instant payouts available",
        "Customer messaging system",
        "Review and rating system",
        "Promotional tools",
        "Bulk upload support",
        "Version control for updates",
        "Analytics and insights",
    ];

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-blue-500/20 via-purple-500/10 to-blue-500/20 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-32 px-6 flex flex-col items-center text-center">
                    <WrenchScrewdriverIcon className="w-20 h-20 text-blue-500 mb-6" />
                    <h1 className="text-5xl sm:text-6xl lg:text-7xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-blue-400 via-purple-500 to-blue-500">
                            For Tuners
                        </span>
                    </h1>
                    <p className="mt-6 text-xl sm:text-2xl text-gray-300 max-w-3xl">
                        Turn your tuning expertise into a thriving business. Join the leading marketplace for automotive calibration files.
                    </p>
                    <div className="mt-10 flex flex-col sm:flex-row gap-4">
                        <Link
                            to="/tunersignup"
                            className="px-8 py-4 bg-gradient-to-r from-blue-500 via-purple-500 to-blue-600 text-white font-bold rounded-xl shadow-lg hover:shadow-blue-500/25 hover:shadow-2xl transform hover:-translate-y-1 transition-all duration-300"
                        >
                            Apply to Become a Tuner
                        </Link>
                        <Link
                            to="/how"
                            className="px-8 py-4 bg-white/10 backdrop-blur-sm border border-white/20 text-white font-bold rounded-xl hover:bg-white/20 transition-all duration-300"
                        >
                            Learn How It Works
                        </Link>
                    </div>

                    {/* Stats */}
                    <div className="mt-16 grid grid-cols-3 gap-8 sm:gap-16">
                        <div className="text-center">
                            <div className="text-3xl sm:text-4xl font-bold text-white">$50K+</div>
                            <div className="text-gray-400 text-sm sm:text-base">Paid to Tuners</div>
                        </div>
                        <div className="text-center">
                            <div className="text-3xl sm:text-4xl font-bold text-white">50+</div>
                            <div className="text-gray-400 text-sm sm:text-base">Active Tuners</div>
                        </div>
                        <div className="text-center">
                            <div className="text-3xl sm:text-4xl font-bold text-white">85%</div>
                            <div className="text-gray-400 text-sm sm:text-base">Revenue Share</div>
                        </div>
                    </div>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            {/* Benefits Section */}
            <section className="max-w-6xl mx-auto px-6 py-20">
                <div className="text-center mb-16">
                    <h2 className={`text-3xl sm:text-4xl font-bold ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                        Why Sell on TunerSwap?
                    </h2>
                    <p className={`mt-4 text-lg ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                        Everything you need to monetize your tuning skills
                    </p>
                </div>

                <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
                    {benefits.map((benefit) => (
                        <div
                            key={benefit.title}
                            className={`${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl p-8 hover:shadow-2xl hover:shadow-blue-500/10 transition-all duration-300 hover:-translate-y-2`}
                        >
                            <benefit.icon className={`w-12 h-12 mb-4 ${benefit.color}`} />
                            <h3 className={`font-bold text-xl mb-3 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                                {benefit.title}
                            </h3>
                            <p className={`${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                                {benefit.desc}
                            </p>
                        </div>
                    ))}
                </div>
            </section>

            {/* How It Works */}
            <section className={`py-20 ${darkMode ? 'bg-zinc-800/30' : 'bg-gray-50'}`}>
                <div className="max-w-6xl mx-auto px-6">
                    <div className="text-center mb-16">
                        <h2 className={`text-3xl sm:text-4xl font-bold ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                            Getting Started is Easy
                        </h2>
                        <p className={`mt-4 text-lg ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                            Three simple steps to start earning
                        </p>
                    </div>

                    <div className="grid md:grid-cols-3 gap-8">
                        {steps.map((step) => (
                            <div key={step.num} className="relative text-center">
                                <div className="w-16 h-16 mx-auto mb-6 rounded-full bg-gradient-to-br from-blue-500 via-purple-500 to-blue-600 flex items-center justify-center text-white font-bold text-2xl shadow-lg">
                                    {step.num}
                                </div>
                                <h3 className={`font-bold text-xl mb-3 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                                    {step.title}
                                </h3>
                                <p className={`${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                                    {step.desc}
                                </p>
                            </div>
                        ))}
                    </div>
                </div>
            </section>

            {/* Features List */}
            <section className="max-w-6xl mx-auto px-6 py-20">
                <div className="grid md:grid-cols-2 gap-12 items-center">
                    <div>
                        <h2 className={`text-3xl sm:text-4xl font-bold mb-6 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                            Everything You Need to Succeed
                        </h2>
                        <p className={`text-lg mb-8 ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                            We've built the tools and infrastructure so you can focus on creating great tunes.
                        </p>
                        <div className="grid grid-cols-2 gap-4">
                            {features.map((feature) => (
                                <div key={feature} className="flex items-center gap-2">
                                    <CheckCircleIcon className="w-5 h-5 text-green-500 flex-shrink-0" />
                                    <span className={`${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>{feature}</span>
                                </div>
                            ))}
                        </div>
                    </div>
                    <div className="flex justify-center">
                        <div className="w-64 h-64 rounded-3xl bg-gradient-to-br from-blue-500 via-purple-500 to-blue-600 flex items-center justify-center shadow-2xl">
                            <RocketLaunchIcon className="w-32 h-32 text-white" />
                        </div>
                    </div>
                </div>
            </section>

            {/* CTA Section */}
            <section className="max-w-6xl mx-auto px-6 pb-20">
                <div className="relative overflow-hidden rounded-3xl bg-gradient-to-r from-blue-500 via-purple-500 to-blue-600 p-1">
                    <div className={`${darkMode ? 'bg-gray-900' : 'bg-gray-100'} rounded-3xl py-16 px-6 text-center`}>
                        <WrenchScrewdriverIcon className="w-16 h-16 mx-auto mb-6 text-blue-500" />
                        <h2 className={`text-3xl sm:text-4xl font-bold mb-4 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                            Ready to Start Selling?
                        </h2>
                        <p className={`mb-8 max-w-2xl mx-auto text-lg ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                            Join our community of professional tuners and start earning from your expertise today.
                        </p>
                        <Link
                            to="/tunersignup"
                            className="inline-block px-10 py-4 bg-gradient-to-r from-blue-500 via-purple-500 to-blue-600 text-white font-bold rounded-xl shadow-lg hover:shadow-2xl hover:shadow-blue-500/30 transform hover:-translate-y-1 transition-all duration-300"
                        >
                            Apply Now — It's Free
                        </Link>
                    </div>
                </div>
            </section>
        </div>
    );
}
