import {
    CpuChipIcon,
    WrenchScrewdriverIcon,
    BuildingStorefrontIcon,
    PlusIcon,
    PencilIcon,
    TrashIcon,
} from '@heroicons/react/24/solid';
import { useSellerAdminStyles } from './useSellerAdminStyles';
import { activeProducts } from '~/data/seller-demo-data';

interface ActiveProductsTabProps {
    onNavigate: (tab: string) => void;
}

export function ActiveProductsTab({ onNavigate }: ActiveProductsTabProps) {
    const { darkMode, cardClasses, textPrimary, textSecondary } = useSellerAdminStyles();

    return (
        <div className="space-y-6">
            <h2 className={`text-2xl font-bold ${textPrimary}`}>Active Products</h2>

            {/* Tunes */}
            <div className={cardClasses}>
                <div className="p-4 border-b border-zinc-700 flex items-center justify-between">
                    <h3 className={`font-semibold ${textPrimary} flex items-center gap-2`}>
                        <CpuChipIcon className="w-5 h-5 text-red-500" />
                        Tunes ({activeProducts.tunes.length})
                    </h3>
                    <button onClick={() => onNavigate('list-tune')} className="text-red-500 hover:text-red-400 text-sm flex items-center gap-1">
                        <PlusIcon className="w-4 h-4" /> Add New
                    </button>
                </div>
                <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                    {activeProducts.tunes.map((tune) => (
                        <div key={tune.id} className="p-4 flex items-center justify-between">
                            <div className="flex items-center gap-4">
                                <div className="w-12 h-12 rounded-lg bg-red-500/20 flex items-center justify-center">
                                    <CpuChipIcon className="w-6 h-6 text-red-500" />
                                </div>
                                <div>
                                    <p className={`font-medium ${textPrimary}`}>{tune.name}</p>
                                    <p className={`text-sm ${textSecondary}`}>${tune.price} • {tune.sales} sales • {tune.rating} rating</p>
                                </div>
                            </div>
                            <div className="flex items-center gap-2">
                                <span className={`px-2 py-1 rounded text-xs ${
                                    tune.status === 'active' ? 'bg-green-500/20 text-green-500' : 'bg-yellow-500/20 text-yellow-500'
                                }`}>{tune.status}</span>
                                <button className={`p-2 ${darkMode ? 'hover:bg-zinc-700' : 'hover:bg-gray-100'} rounded`}>
                                    <PencilIcon className="w-4 h-4 text-blue-500" />
                                </button>
                                <button className={`p-2 ${darkMode ? 'hover:bg-zinc-700' : 'hover:bg-gray-100'} rounded`}>
                                    <TrashIcon className="w-4 h-4 text-red-500" />
                                </button>
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Parts */}
            <div className={cardClasses}>
                <div className="p-4 border-b border-zinc-700 flex items-center justify-between">
                    <h3 className={`font-semibold ${textPrimary} flex items-center gap-2`}>
                        <WrenchScrewdriverIcon className="w-5 h-5 text-orange-500" />
                        Parts ({activeProducts.parts.length})
                    </h3>
                    <button onClick={() => onNavigate('list-parts')} className="text-orange-500 hover:text-orange-400 text-sm flex items-center gap-1">
                        <PlusIcon className="w-4 h-4" /> Add New
                    </button>
                </div>
                <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                    {activeProducts.parts.map((part) => (
                        <div key={part.id} className="p-4 flex items-center justify-between">
                            <div className="flex items-center gap-4">
                                <div className="w-12 h-12 rounded-lg bg-orange-500/20 flex items-center justify-center">
                                    <WrenchScrewdriverIcon className="w-6 h-6 text-orange-500" />
                                </div>
                                <div>
                                    <p className={`font-medium ${textPrimary}`}>{part.name}</p>
                                    <p className={`text-sm ${textSecondary}`}>${part.price} • {part.stock} in stock • {part.sales} sales</p>
                                </div>
                            </div>
                            <div className="flex items-center gap-2">
                                <span className={`px-2 py-1 rounded text-xs ${
                                    part.status === 'active' ? 'bg-green-500/20 text-green-500' :
                                    part.status === 'low_stock' ? 'bg-yellow-500/20 text-yellow-500' :
                                    'bg-red-500/20 text-red-500'
                                }`}>{part.status.replace('_', ' ')}</span>
                                <button className={`p-2 ${darkMode ? 'hover:bg-zinc-700' : 'hover:bg-gray-100'} rounded`}>
                                    <PencilIcon className="w-4 h-4 text-blue-500" />
                                </button>
                                <button className={`p-2 ${darkMode ? 'hover:bg-zinc-700' : 'hover:bg-gray-100'} rounded`}>
                                    <TrashIcon className="w-4 h-4 text-red-500" />
                                </button>
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Services */}
            <div className={cardClasses}>
                <div className="p-4 border-b border-zinc-700 flex items-center justify-between">
                    <h3 className={`font-semibold ${textPrimary} flex items-center gap-2`}>
                        <BuildingStorefrontIcon className="w-5 h-5 text-green-500" />
                        Services ({activeProducts.services.length})
                    </h3>
                    <button onClick={() => onNavigate('list-service')} className="text-green-500 hover:text-green-400 text-sm flex items-center gap-1">
                        <PlusIcon className="w-4 h-4" /> Add New
                    </button>
                </div>
                <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                    {activeProducts.services.map((service) => (
                        <div key={service.id} className="p-4 flex items-center justify-between">
                            <div className="flex items-center gap-4">
                                <div className="w-12 h-12 rounded-lg bg-green-500/20 flex items-center justify-center">
                                    <BuildingStorefrontIcon className="w-6 h-6 text-green-500" />
                                </div>
                                <div>
                                    <p className={`font-medium ${textPrimary}`}>{service.name}</p>
                                    <p className={`text-sm ${textSecondary}`}>${service.price} • {service.bookings} bookings • {service.rating} rating</p>
                                </div>
                            </div>
                            <div className="flex items-center gap-2">
                                <span className="px-2 py-1 rounded text-xs bg-green-500/20 text-green-500">{service.status}</span>
                                <button className={`p-2 ${darkMode ? 'hover:bg-zinc-700' : 'hover:bg-gray-100'} rounded`}>
                                    <PencilIcon className="w-4 h-4 text-blue-500" />
                                </button>
                                <button className={`p-2 ${darkMode ? 'hover:bg-zinc-700' : 'hover:bg-gray-100'} rounded`}>
                                    <TrashIcon className="w-4 h-4 text-red-500" />
                                </button>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}
