import { useState } from 'react';
import {
    BuildingStorefrontIcon,
    CalendarDaysIcon,
    MapPinIcon,
    ClockIcon,
} from '@heroicons/react/24/solid';
import { useBuyerAdminStyles } from './useBuyerAdminStyles';
import { buyerServices } from '~/data/buyer-demo-data';

export function ServicesTab() {
    const { darkMode, cardClasses, textPrimary, textSecondary } = useBuyerAdminStyles();
    const [selectedService, setSelectedService] = useState<string | null>(null);

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <h2 className={`text-2xl font-bold ${textPrimary}`}>My Services</h2>
                <button className="px-4 py-2 bg-green-500 hover:bg-green-600 text-white rounded-lg transition-colors">
                    Book Service
                </button>
            </div>

            <div className="grid lg:grid-cols-3 gap-6">
                {/* Services List */}
                <div className={`${cardClasses} lg:col-span-1`}>
                    <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                        <h3 className={`font-semibold ${textPrimary}`}>Appointments</h3>
                    </div>
                    <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                        {buyerServices.map((service) => (
                            <div
                                key={service.id}
                                onClick={() => setSelectedService(service.id)}
                                className={`p-4 cursor-pointer transition-colors ${
                                    selectedService === service.id
                                        ? darkMode ? 'bg-zinc-700' : 'bg-gray-100'
                                        : `${darkMode ? 'hover:bg-zinc-700/50' : 'hover:bg-gray-50'}`
                                }`}
                            >
                                <div className="flex items-center justify-between mb-2">
                                    <span className={`font-medium ${textPrimary}`}>{service.service}</span>
                                    <span className={`text-xs px-2 py-0.5 rounded ${
                                        service.status === 'completed' ? 'bg-gray-500/20 text-gray-500' :
                                        service.status === 'scheduled' ? 'bg-green-500/20 text-green-500' :
                                        'bg-red-500/20 text-red-500'
                                    }`}>{service.status}</span>
                                </div>
                                <p className={`text-sm ${textSecondary}`}>{service.seller.name}</p>
                                <p className={`text-xs ${textSecondary}`}>{service.date} at {service.time}</p>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Service Details */}
                <div className={`${cardClasses} lg:col-span-2`}>
                    {selectedService ? (() => {
                        const service = buyerServices.find(s => s.id === selectedService)!;
                        return (
                            <>
                                <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                                    <div className="flex items-center justify-between">
                                        <div className="flex items-center gap-3">
                                            <div className="w-12 h-12 rounded-lg bg-green-500/20 flex items-center justify-center">
                                                <BuildingStorefrontIcon className="w-6 h-6 text-green-500" />
                                            </div>
                                            <div>
                                                <h3 className={`font-semibold ${textPrimary}`}>{service.service}</h3>
                                                <p className={`text-sm ${textSecondary}`}>{service.vehicle}</p>
                                            </div>
                                        </div>
                                        <span className={`text-lg font-bold ${textPrimary}`}>${service.price}</span>
                                    </div>
                                </div>
                                <div className="p-4 space-y-4">
                                    {/* Shop Info */}
                                    <div>
                                        <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Shop</h4>
                                        <div className="flex items-center gap-3">
                                            <div className={`w-10 h-10 rounded-full bg-green-500 flex items-center justify-center text-white font-medium`}>
                                                {service.seller.initials}
                                            </div>
                                            <div>
                                                <p className={textPrimary}>{service.seller.name}</p>
                                                <div className="flex items-center gap-1 text-sm">
                                                    <MapPinIcon className="w-4 h-4 text-green-500" />
                                                    <span className={textSecondary}>{service.seller.location}</span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                    {/* Appointment Time */}
                                    <div>
                                        <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Appointment</h4>
                                        <div className={`p-3 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'}`}>
                                            <div className="flex items-center gap-4">
                                                <div className="flex items-center gap-2">
                                                    <CalendarDaysIcon className="w-5 h-5 text-green-500" />
                                                    <p className={textPrimary}>{service.date}</p>
                                                </div>
                                                <div className="flex items-center gap-2">
                                                    <ClockIcon className="w-5 h-5 text-green-500" />
                                                    <p className={textPrimary}>{service.time}</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                    {/* Notes */}
                                    {service.notes && (
                                        <div>
                                            <h4 className={`text-sm font-medium mb-2 ${textSecondary}`}>Notes</h4>
                                            <div className={`p-3 rounded-lg ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'}`}>
                                                <p className={textPrimary}>{service.notes}</p>
                                            </div>
                                        </div>
                                    )}

                                    <div className="flex gap-3 pt-4">
                                        {service.status === 'scheduled' && (
                                            <>
                                                <button className="px-4 py-2 bg-green-500 hover:bg-green-600 text-white rounded-lg transition-colors">
                                                    Get Directions
                                                </button>
                                                <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                                                    Reschedule
                                                </button>
                                                <button className="px-4 py-2 bg-red-500/20 text-red-500 hover:bg-red-500/30 rounded-lg transition-colors">
                                                    Cancel
                                                </button>
                                            </>
                                        )}
                                        {service.status === 'completed' && (
                                            <>
                                                <button className="px-4 py-2 bg-green-500 hover:bg-green-600 text-white rounded-lg transition-colors">
                                                    Leave Review
                                                </button>
                                                <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                                                    Book Again
                                                </button>
                                            </>
                                        )}
                                        <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                                            Contact Shop
                                        </button>
                                    </div>
                                </div>
                            </>
                        );
                    })() : (
                        <div className="flex-1 flex items-center justify-center p-12">
                            <div className="text-center">
                                <BuildingStorefrontIcon className={`w-16 h-16 mx-auto mb-4 ${textSecondary}`} />
                                <p className={textSecondary}>Select a service to view details</p>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
