import {
  HashtagIcon,
  MapPinIcon,
  ShoppingBagIcon,
  UserCircleIcon,
  TruckIcon,
  WrenchScrewdriverIcon,
} from '@heroicons/react/24/solid';
import { Form, Link, Outlet, useLoaderData, useMatches } from '@remix-run/react';
import { DataFunctionArgs, json, redirect } from '@remix-run/server-runtime';
import { TabProps } from '~/components/tabs/Tab';
import { TabsContainer } from '~/components/tabs/TabsContainer';
import { getActiveCustomerDetails } from '~/providers/customer/customer';
import { useTranslation } from 'react-i18next';

export async function loader({ request }: DataFunctionArgs) {
  // Redirect to new buyeradmin dashboard
  return redirect('/buyeradmin');
}

export default function AccountDashboard() {
  const { activeCustomer } = useLoaderData<typeof loader>();
  const { firstName, lastName, emailAddress } = activeCustomer!;
  const { t } = useTranslation();

  // Get initials for avatar
  const initials = `${firstName?.[0] || ''}${lastName?.[0] || ''}`.toUpperCase();

  const tabs: TabProps[] = [
    {
      Icon: UserCircleIcon,
      text: t('account.details'),
      to: './',
    },
    {
      Icon: ShoppingBagIcon,
      text: t('account.purchaseHistory'),
      to: './history',
    },
    {
      Icon: MapPinIcon,
      text: t('account.addresses'),
      to: './addresses',
    },
    {
      Icon: HashtagIcon,
      text: t('account.password'),
      to: './password',
    },
  ];

  return (
    <div className="min-h-screen bg-black">
      <div className="max-w-6xl xl:mx-auto px-4 py-8">
        {/* Header Section */}
        <div className="bg-zinc-900 border border-zinc-800 rounded-xl p-6 mb-8">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              {/* Avatar */}
              <div className="w-16 h-16 rounded-full bg-gradient-to-br from-red-500 to-red-700 flex items-center justify-center text-white text-xl font-bold">
                {initials}
              </div>
              <div>
                <h2 className="text-2xl font-bold text-white">
                  {firstName} {lastName}
                </h2>
                <p className="text-zinc-400">{emailAddress}</p>
              </div>
            </div>
            <Form method="post" action="/api/logout">
              <button
                type="submit"
                className="bg-zinc-800 hover:bg-zinc-700 text-white px-4 py-2 rounded-lg transition-colors"
              >
                Sign Out
              </button>
            </Form>
          </div>
        </div>

        {/* Quick Stats */}
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
          <div className="bg-zinc-900 border border-zinc-800 rounded-xl p-4">
            <div className="flex items-center gap-3">
              <ShoppingBagIcon className="w-8 h-8 text-red-500" />
              <div>
                <p className="text-zinc-400 text-sm">Orders</p>
                <p className="text-white text-xl font-bold">0</p>
              </div>
            </div>
          </div>
          <div className="bg-zinc-900 border border-zinc-800 rounded-xl p-4">
            <div className="flex items-center gap-3">
              <WrenchScrewdriverIcon className="w-8 h-8 text-red-500" />
              <div>
                <p className="text-zinc-400 text-sm">Tunes</p>
                <p className="text-white text-xl font-bold">0</p>
              </div>
            </div>
          </div>
          <div className="bg-zinc-900 border border-zinc-800 rounded-xl p-4">
            <div className="flex items-center gap-3">
              <TruckIcon className="w-8 h-8 text-red-500" />
              <div>
                <p className="text-zinc-400 text-sm">Vehicles</p>
                <p className="text-white text-xl font-bold">0</p>
              </div>
            </div>
          </div>
          <div className="bg-zinc-900 border border-zinc-800 rounded-xl p-4">
            <div className="flex items-center gap-3">
              <MapPinIcon className="w-8 h-8 text-red-500" />
              <div>
                <p className="text-zinc-400 text-sm">Addresses</p>
                <p className="text-white text-xl font-bold">0</p>
              </div>
            </div>
          </div>
        </div>

        {/* Main Content with Tabs */}
        <div className="bg-zinc-900 border border-zinc-800 rounded-xl overflow-hidden">
          <TabsContainer tabs={tabs}>
            <Outlet />
          </TabsContainer>
        </div>
      </div>
    </div>
  );
}
