import { useSellerAdminStyles } from './useSellerAdminStyles';
import { messages } from '~/data/seller-demo-data';

export function MessagesTab() {
    const { darkMode, cardClasses, textPrimary, textSecondary } = useSellerAdminStyles();

    return (
        <div className="space-y-6">
            <h2 className={`text-2xl font-bold ${textPrimary}`}>Messages</h2>

            <div className={cardClasses}>
                <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                    {messages.map((msg) => (
                        <div key={msg.id} className={`p-4 flex items-start gap-4 cursor-pointer hover:${darkMode ? 'bg-zinc-700/50' : 'bg-gray-50'} transition-colors`}>
                            <div className={`w-10 h-10 rounded-full ${msg.unread ? 'bg-red-500' : darkMode ? 'bg-zinc-600' : 'bg-gray-300'} flex items-center justify-center text-white font-medium`}>
                                {msg.from.split(' ').map(n => n[0]).join('')}
                            </div>
                            <div className="flex-1 min-w-0">
                                <div className="flex items-center justify-between">
                                    <p className={`font-medium ${textPrimary} ${msg.unread ? 'font-bold' : ''}`}>{msg.from}</p>
                                    <span className={`text-sm ${textSecondary}`}>{msg.date}</span>
                                </div>
                                <p className={`font-medium text-sm ${textPrimary} ${msg.unread ? 'font-semibold' : ''}`}>{msg.subject}</p>
                                <p className={`text-sm truncate ${textSecondary}`}>{msg.preview}</p>
                            </div>
                            {msg.unread && (
                                <div className="w-3 h-3 bg-red-500 rounded-full" />
                            )}
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}
