/**
 * Get list of approved sellers
 */
export async function getSellers(
    options: QueryOptions,
    pagination?: { skip?: number; take?: number }
): Promise<{ items: SellerProfile[]; totalItems: number }> {
    const query = gql`
        query Sellers($skip: Int, $take: Int) {
            sellers(skip: $skip, take: $take) {
                items {
                    id
                    firstName
                    lastName
                    location
                    businessName
                    bio
                    experience
                    software
                    vehiclePlatforms
                    tuneTypes
                    status
                    verified
                    rating
                    reviewCount
                    tunesSold
                    slug
                }
                totalItems
            }
        }
    `;

    const response = await fetch(process.env.VENDURE_API_URL || 'http://localhost:3000/shop-api', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            query: query.loc?.source.body,
            variables: pagination,
        }),
    });

    const result = await response.json();
    return result.data?.sellers || { items: [], totalItems: 0 };
}

/**
 * Update the current seller's profile
 */
export async function updateSellerProfile(
    input: UpdateSellerProfileInput,
    options: QueryOptions
): Promise<SellerProfile> {
    const query = gql`
        mutation UpdateSellerProfile($input: UpdateSellerProfileInput!) {
            updateSellerProfile(input: $input) {
                id
                firstName
                lastName
                phone
                location
                address
                businessName
                website
                instagram
                facebook
                bio
                experience
                software
                vehiclePlatforms
                tuneTypes
                hasDyno
                hours
                status
                verified
                rating
                reviewCount
                tunesSold
                totalOrders
                slug
                createdAt
                updatedAt
            }
        }
    `;

    const response = await fetch(process.env.VENDURE_API_URL || 'http://localhost:3000/shop-api', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            ...(options.request.headers.get('Cookie') ? { Cookie: options.request.headers.get('Cookie')! } : {}),
        },
        body: JSON.stringify({
            query: query.loc?.source.body,
            variables: { input },
        }),
    });

    const result = await response.json();

    if (result.errors) {
        throw new Error(result.errors[0]?.message || 'Update failed');
    }

    return result.data.updateSellerProfile;
}
