import { useDarkMode } from '~/root';
import { useState } from 'react';
import { Link, useFetcher } from '@remix-run/react';
import { ActionFunctionArgs, json, redirect } from '@remix-run/server-runtime';
import {
    WrenchScrewdriverIcon,
    UserIcon,
    BuildingStorefrontIcon,
    CpuChipIcon,
    DocumentCheckIcon,
    CheckCircleIcon,
    ArrowLeftIcon,
    ArrowRightIcon,
    ClockIcon,
    RocketLaunchIcon,
    ExclamationTriangleIcon,
    ArrowPathIcon,
} from '@heroicons/react/24/solid';
import { registerSeller, RegisterSellerInput } from '~/providers/seller/seller';

// ============================================================================
// SIGNUP MODE CONFIGURATION
// Set this to 'form' for application form, 'direct' for direct signup flow
// ============================================================================
const SIGNUP_MODE: 'form' | 'direct' = 'direct';

// ============================================================================
// DEMO MODE - Set to false for production
// ============================================================================
const DEMO_MODE = false;

// ============================================================================
// SETUP MODE - 'immediate' skips approval, 'approval' requires admin approval
// ============================================================================
const SETUP_MODE: 'immediate' | 'approval' = 'immediate';

// Action to handle form submission
export async function action({ request }: ActionFunctionArgs) {
    const body = await request.formData();

    const input: RegisterSellerInput = {
        email: body.get('email') as string,
        password: body.get('password') as string,
        firstName: body.get('firstName') as string,
        lastName: body.get('lastName') as string,
        phone: body.get('phone') as string || undefined,
        location: body.get('location') as string || undefined,
        businessName: body.get('businessName') as string || undefined,
        website: body.get('website') as string || undefined,
        socialMedia: body.get('socialMedia') as string || undefined,
        bio: body.get('bio') as string || undefined,
        experience: body.get('experience') as string || undefined,
        software: body.getAll('software') as string[] || undefined,
        vehiclePlatforms: body.getAll('vehiclePlatforms') as string[] || undefined,
        tuneTypes: body.getAll('tuneTypes') as string[] || undefined,
        hasDyno: body.get('hasDyno') as string || undefined,
    };

    // Validate password match
    const confirmPassword = body.get('confirmPassword') as string;
    if (input.password !== confirmPassword) {
        return json({ success: false, message: 'Passwords do not match' }, { status: 400 });
    }

    // Validate required fields
    if (!input.email || !input.password || !input.firstName || !input.lastName) {
        return json({ success: false, message: 'Please fill in all required fields' }, { status: 400 });
    }

    try {
        const result = await registerSeller(input, { request });

        if (result.success) {
            // Registration successful - redirect based on setup mode
            return json({ success: true, message: result.message, sellerId: result.sellerId });
        } else {
            return json({ success: false, message: result.message || 'Registration failed' }, { status: 400 });
        }
    } catch (error: any) {
        return json({ success: false, message: error.message || 'Registration failed' }, { status: 500 });
    }
}

export default function TunerSignupPage() {
    const { darkMode } = useDarkMode();

    if (SIGNUP_MODE === 'form') {
        return <ApplicationForm darkMode={darkMode} />;
    }

    return <DirectSignupFlow darkMode={darkMode} />;
}

// ============================================================================
// DIRECT SIGNUP FLOW - Multi-step wizard
// ============================================================================
interface DirectSignupFlowProps {
    darkMode: boolean;
}

function DirectSignupFlow({ darkMode }: DirectSignupFlowProps) {
    const fetcher = useFetcher<{ success: boolean; message?: string; sellerId?: string }>();
    const [currentStep, setCurrentStep] = useState(1);
    const [formData, setFormData] = useState({
        // Step 1: Account
        email: '',
        password: '',
        confirmPassword: '',
        // Step 2: Personal
        firstName: '',
        lastName: '',
        phone: '',
        location: '',
        // Step 3: Business
        businessName: '',
        website: '',
        socialMedia: '',
        bio: '',
        // Step 4: Tuning
        experience: '',
        software: [] as string[],
        vehiclePlatforms: [] as string[],
        tuneTypes: [] as string[],
        hasDyno: '',
    });
    const [error, setError] = useState<string | null>(null);

    const totalSteps = 5;
    const isSubmitting = fetcher.state !== 'idle';

    const steps = [
        { number: 1, title: 'Account', icon: UserIcon },
        { number: 2, title: 'Personal', icon: UserIcon },
        { number: 3, title: 'Business', icon: BuildingStorefrontIcon },
        { number: 4, title: 'Tuning', icon: CpuChipIcon },
        { number: 5, title: 'Review', icon: DocumentCheckIcon },
    ];

    const updateFormData = (field: string, value: string | string[]) => {
        setFormData(prev => ({ ...prev, [field]: value }));
    };

    const toggleArrayValue = (field: string, value: string) => {
        setFormData(prev => {
            const current = prev[field as keyof typeof prev] as string[];
            if (current.includes(value)) {
                return { ...prev, [field]: current.filter(v => v !== value) };
            }
            return { ...prev, [field]: [...current, value] };
        });
    };

    const nextStep = () => setCurrentStep(prev => Math.min(prev + 1, totalSteps + 1));
    const prevStep = () => setCurrentStep(prev => Math.max(prev - 1, 1));

    // Handle form submission on final step
    const handleSubmit = () => {
        setError(null);

        // Validate passwords match
        if (formData.password !== formData.confirmPassword) {
            setError('Passwords do not match');
            return;
        }

        // Validate required fields
        if (!formData.email || !formData.password || !formData.firstName || !formData.lastName) {
            setError('Please fill in all required fields');
            return;
        }

        // Submit the form
        const submitData = new FormData();
        submitData.append('email', formData.email);
        submitData.append('password', formData.password);
        submitData.append('confirmPassword', formData.confirmPassword);
        submitData.append('firstName', formData.firstName);
        submitData.append('lastName', formData.lastName);
        if (formData.phone) submitData.append('phone', formData.phone);
        if (formData.location) submitData.append('location', formData.location);
        if (formData.businessName) submitData.append('businessName', formData.businessName);
        if (formData.website) submitData.append('website', formData.website);
        if (formData.socialMedia) submitData.append('socialMedia', formData.socialMedia);
        if (formData.bio) submitData.append('bio', formData.bio);
        if (formData.experience) submitData.append('experience', formData.experience);
        formData.software.forEach(s => submitData.append('software', s));
        formData.vehiclePlatforms.forEach(p => submitData.append('vehiclePlatforms', p));
        formData.tuneTypes.forEach(t => submitData.append('tuneTypes', t));
        if (formData.hasDyno) submitData.append('hasDyno', formData.hasDyno);

        fetcher.submit(submitData, { method: 'post' });
    };

    const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

    // Check for successful registration
    if (fetcher.data?.success) {
        if (SETUP_MODE === 'immediate') {
            return <ImmediateSetupComplete darkMode={darkMode} />;
        }
        return <ApprovalPending darkMode={darkMode} />;
    }

    // Show error from API if present
    const displayError = error || (fetcher.data && !fetcher.data.success ? fetcher.data.message : null);

    // Final completion screens (for demo mode or after successful submission)
    if (currentStep > totalSteps && !isSubmitting) {
        // In non-demo mode, submission should have happened
        // This is a fallback for demo mode
        if (DEMO_MODE) {
            if (SETUP_MODE === 'immediate') {
                return <ImmediateSetupComplete darkMode={darkMode} />;
            }
            return <ApprovalPending darkMode={darkMode} />;
        }
    }

    return (
        <div className={`min-h-screen ${darkMode ? 'bg-gray-900' : 'bg-gray-50'}`}>
            {/* Header */}
            <div className={`${darkMode ? 'bg-zinc-900 border-zinc-800' : 'bg-white border-gray-200'} border-b sticky top-0 z-50`}>
                <div className="max-w-4xl mx-auto px-6 py-4">
                    <div className="flex items-center justify-between">
                        <Link
                            to="/"
                            className={`flex items-center gap-2 text-sm ${textSecondary} hover:text-red-500 transition-colors`}
                        >
                            <ArrowLeftIcon className="w-4 h-4" />
                            Back to TunerSwap
                        </Link>
                        <div className={`text-sm ${textSecondary}`}>
                            Step {currentStep} of {totalSteps}
                        </div>
                    </div>
                </div>
            </div>

            {/* Progress Steps */}
            <div className={`${darkMode ? 'bg-zinc-900/50' : 'bg-gray-100'} py-6`}>
                <div className="max-w-4xl mx-auto px-6">
                    <div className="flex items-center justify-between">
                        {steps.map((step, index) => (
                            <div key={step.number} className="flex items-center">
                                <div className="flex flex-col items-center">
                                    <div
                                        className={`w-10 h-10 rounded-full flex items-center justify-center transition-all ${
                                            currentStep >= step.number
                                                ? 'bg-gradient-to-r from-blue-500 to-purple-500 text-white'
                                                : darkMode
                                                    ? 'bg-zinc-700 text-gray-400'
                                                    : 'bg-gray-200 text-gray-500'
                                        }`}
                                    >
                                        {currentStep > step.number ? (
                                            <CheckCircleIcon className="w-6 h-6" />
                                        ) : (
                                            <step.icon className="w-5 h-5" />
                                        )}
                                    </div>
                                    <span className={`text-xs mt-2 hidden sm:block ${
                                        currentStep >= step.number ? textPrimary : textSecondary
                                    }`}>
                                        {step.title}
                                    </span>
                                </div>
                                {index < steps.length - 1 && (
                                    <div
                                        className={`w-12 sm:w-24 h-1 mx-2 rounded ${
                                            currentStep > step.number
                                                ? 'bg-gradient-to-r from-blue-500 to-purple-500'
                                                : darkMode
                                                    ? 'bg-zinc-700'
                                                    : 'bg-gray-200'
                                        }`}
                                    />
                                )}
                            </div>
                        ))}
                    </div>
                </div>
            </div>

            {/* Main Content */}
            <main className="max-w-2xl mx-auto px-6 py-12">
                {currentStep === 1 && (
                    <StepAccount
                        darkMode={darkMode}
                        formData={formData}
                        updateFormData={updateFormData}
                    />
                )}
                {currentStep === 2 && (
                    <StepPersonal
                        darkMode={darkMode}
                        formData={formData}
                        updateFormData={updateFormData}
                    />
                )}
                {currentStep === 3 && (
                    <StepBusiness
                        darkMode={darkMode}
                        formData={formData}
                        updateFormData={updateFormData}
                    />
                )}
                {currentStep === 4 && (
                    <StepTuning
                        darkMode={darkMode}
                        formData={formData}
                        updateFormData={updateFormData}
                        toggleArrayValue={toggleArrayValue}
                    />
                )}
                {currentStep === 5 && (
                    <StepReview
                        darkMode={darkMode}
                        formData={formData}
                    />
                )}

                {/* Error Display */}
                {displayError && (
                    <div className={`mt-6 p-4 rounded-xl ${darkMode ? 'bg-red-500/10 border-red-500/20' : 'bg-red-50 border-red-200'} border`}>
                        <div className="flex items-center gap-2">
                            <ExclamationTriangleIcon className="w-5 h-5 text-red-500" />
                            <span className={`text-sm font-medium ${darkMode ? 'text-red-400' : 'text-red-700'}`}>
                                {displayError}
                            </span>
                        </div>
                    </div>
                )}

                {/* Navigation Buttons */}
                <div className="flex items-center justify-between mt-12">
                    <button
                        onClick={prevStep}
                        disabled={currentStep === 1 || isSubmitting}
                        className={`flex items-center gap-2 px-6 py-3 rounded-xl font-semibold transition-all ${
                            currentStep === 1 || isSubmitting
                                ? 'opacity-50 cursor-not-allowed'
                                : darkMode
                                    ? 'bg-zinc-700 hover:bg-zinc-600 text-white'
                                    : 'bg-gray-200 hover:bg-gray-300 text-gray-900'
                        }`}
                    >
                        <ArrowLeftIcon className="w-4 h-4" />
                        Back
                    </button>

                    <button
                        onClick={currentStep === totalSteps ? handleSubmit : nextStep}
                        disabled={isSubmitting}
                        className={`flex items-center gap-2 px-8 py-3 bg-gradient-to-r from-blue-500 to-purple-500 text-white font-semibold rounded-xl hover:shadow-lg hover:shadow-blue-500/25 transition-all ${isSubmitting ? 'opacity-75 cursor-not-allowed' : ''}`}
                    >
                        {isSubmitting && <ArrowPathIcon className="w-4 h-4 animate-spin" />}
                        {currentStep === totalSteps ? (isSubmitting ? 'Creating Account...' : 'Complete Signup') : 'Continue'}
                        {!isSubmitting && <ArrowRightIcon className="w-4 h-4" />}
                    </button>
                </div>

                {/* Demo Navigation */}
                {DEMO_MODE && (
                    <div className={`mt-8 p-4 rounded-xl ${darkMode ? 'bg-yellow-500/10 border-yellow-500/20' : 'bg-yellow-50 border-yellow-200'} border`}>
                        <div className="flex items-center gap-2 mb-3">
                            <ExclamationTriangleIcon className="w-5 h-5 text-yellow-500" />
                            <span className={`text-sm font-semibold ${darkMode ? 'text-yellow-400' : 'text-yellow-700'}`}>
                                Demo Mode
                            </span>
                        </div>
                        <div className="flex flex-wrap gap-2">
                            {steps.map(step => (
                                <button
                                    key={step.number}
                                    onClick={() => setCurrentStep(step.number)}
                                    className={`px-3 py-1.5 text-xs rounded-lg font-medium transition-colors ${
                                        currentStep === step.number
                                            ? 'bg-yellow-500 text-white'
                                            : darkMode
                                                ? 'bg-zinc-700 text-gray-300 hover:bg-zinc-600'
                                                : 'bg-white text-gray-700 hover:bg-gray-100'
                                    }`}
                                >
                                    {step.number}. {step.title}
                                </button>
                            ))}
                            <button
                                onClick={() => setCurrentStep(totalSteps + 1)}
                                className={`px-3 py-1.5 text-xs rounded-lg font-medium ${
                                    darkMode
                                        ? 'bg-green-500/20 text-green-400 hover:bg-green-500/30'
                                        : 'bg-green-100 text-green-700 hover:bg-green-200'
                                }`}
                            >
                                Complete
                            </button>
                        </div>
                    </div>
                )}
            </main>
        </div>
    );
}

// ============================================================================
// STEP 1: Account Creation
// ============================================================================
interface StepProps {
    darkMode: boolean;
    formData: any;
    updateFormData: (field: string, value: string | string[]) => void;
}

function StepAccount({ darkMode, formData, updateFormData }: StepProps) {
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';
    const inputClasses = `w-full rounded-xl px-4 py-3 focus:ring-2 focus:ring-blue-500 focus:outline-none transition-colors ${
        darkMode
            ? 'bg-zinc-700 border-zinc-600 text-white placeholder-gray-400'
            : 'bg-white border-gray-300 text-gray-900 placeholder-gray-500'
    } border`;
    const labelClasses = `block text-sm font-semibold mb-2 ${darkMode ? 'text-gray-300' : 'text-gray-700'}`;

    return (
        <div className="space-y-6">
            <div className="text-center mb-8">
                <div className="w-16 h-16 rounded-2xl bg-gradient-to-br from-blue-500/20 to-purple-500/20 flex items-center justify-center mx-auto mb-4">
                    <UserIcon className="w-8 h-8 text-blue-500" />
                </div>
                <h1 className={`text-3xl font-bold ${textPrimary}`}>Create Your Account</h1>
                <p className={`mt-2 ${textSecondary}`}>
                    Start by setting up your login credentials
                </p>
            </div>

            <div className="space-y-4">
                <div>
                    <label className={labelClasses}>Email Address</label>
                    <input
                        type="email"
                        value={formData.email}
                        onChange={(e) => updateFormData('email', e.target.value)}
                        placeholder="you@example.com"
                        className={inputClasses}
                    />
                </div>

                <div>
                    <label className={labelClasses}>Password</label>
                    <input
                        type="password"
                        value={formData.password}
                        onChange={(e) => updateFormData('password', e.target.value)}
                        placeholder="Create a strong password"
                        className={inputClasses}
                    />
                    <p className={`text-xs mt-2 ${textSecondary}`}>
                        At least 8 characters with a mix of letters, numbers, and symbols
                    </p>
                </div>

                <div>
                    <label className={labelClasses}>Confirm Password</label>
                    <input
                        type="password"
                        value={formData.confirmPassword}
                        onChange={(e) => updateFormData('confirmPassword', e.target.value)}
                        placeholder="Confirm your password"
                        className={inputClasses}
                    />
                </div>
            </div>
        </div>
    );
}

