// ============================================================================
// STEP 5: Review
// ============================================================================
interface StepReviewProps {
    darkMode: boolean;
    formData: any;
}

function StepReview({ darkMode, formData }: StepReviewProps) {
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';
    const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-gray-50 border-gray-200'} border rounded-xl p-4`;

    const ReviewSection = ({ title, children }: { title: string; children: React.ReactNode }) => (
        <div className={cardClasses}>
            <h3 className={`text-sm font-semibold mb-3 ${textSecondary}`}>{title}</h3>
            {children}
        </div>
    );

    const ReviewItem = ({ label, value }: { label: string; value: string | string[] }) => (
        <div className="flex justify-between py-1.5">
            <span className={textSecondary}>{label}</span>
            <span className={`${textPrimary} font-medium text-right`}>
                {Array.isArray(value) ? value.join(', ') || '—' : value || '—'}
            </span>
        </div>
    );

    return (
        <div className="space-y-6">
            <div className="text-center mb-8">
                <div className="w-16 h-16 rounded-2xl bg-gradient-to-br from-green-500/20 to-emerald-500/20 flex items-center justify-center mx-auto mb-4">
                    <DocumentCheckIcon className="w-8 h-8 text-green-500" />
                </div>
                <h1 className={`text-3xl font-bold ${textPrimary}`}>Review Your Information</h1>
                <p className={`mt-2 ${textSecondary}`}>
                    Make sure everything looks correct before completing signup
                </p>
            </div>

            <div className="space-y-4">
                <ReviewSection title="Account">
                    <ReviewItem label="Email" value={formData.email} />
                </ReviewSection>

                <ReviewSection title="Personal">
                    <ReviewItem label="Name" value={`${formData.firstName} ${formData.lastName}`.trim()} />
                    <ReviewItem label="Phone" value={formData.phone} />
                    <ReviewItem label="Location" value={formData.location} />
                </ReviewSection>

                <ReviewSection title="Business">
                    <ReviewItem label="Business Name" value={formData.businessName} />
                    <ReviewItem label="Website" value={formData.website} />
                    <ReviewItem label="Social Media" value={formData.socialMedia} />
                </ReviewSection>

                <ReviewSection title="Tuning">
                    <ReviewItem label="Experience" value={formData.experience} />
                    <ReviewItem label="Software" value={formData.software} />
                    <ReviewItem label="Platforms" value={formData.vehiclePlatforms} />
                    <ReviewItem label="Services" value={formData.tuneTypes} />
                    <ReviewItem label="Dyno Access" value={formData.hasDyno} />
                </ReviewSection>
            </div>

            <div className={`p-4 rounded-xl ${darkMode ? 'bg-blue-500/10 border-blue-500/20' : 'bg-blue-50 border-blue-200'} border`}>
                <p className={`text-sm ${darkMode ? 'text-blue-300' : 'text-blue-700'}`}>
                    By completing signup, you agree to our <Link to="/terms" className="underline">Terms of Service</Link> and <Link to="/disclaimer" className="underline">Tuning Disclaimer</Link>.
                </p>
            </div>
        </div>
    );
}

