import { Link } from '@remix-run/react';
import {
    WrenchScrewdriverIcon,
    UserCircleIcon,
    ShoppingBagIcon,
    BeakerIcon,
    ServerStackIcon,
    InboxIcon,
    CommandLineIcon,
    CircleStackIcon,
    UserPlusIcon,
    ArrowRightOnRectangleIcon,
} from '@heroicons/react/24/solid';

/**
 * DEV TOOLS HEADER
 *
 * This component provides quick access to development/staging tools.
 * REMOVE THIS COMPONENT FROM PRODUCTION BUILDS.
 *
 * To disable: Simply don't render <DevToolsHeader /> in your layout.
 */
export function DevToolsHeader() {
    return (
        <div className="bg-gradient-to-r from-yellow-500 via-orange-500 to-red-500 text-white text-xs py-1.5 px-4">
            <div className="max-w-6xl mx-auto flex items-center justify-between">
                <div className="flex items-center gap-1">
                    <BeakerIcon className="w-4 h-4" />
                    <span className="font-bold">DEV MODE</span>
                </div>

                <div className="flex items-center gap-4">
                    {/* Admin Panels */}
                    <Link
                        to="/selleradmin"
                        className="flex items-center gap-1 hover:text-yellow-200 transition-colors"
                    >
                        <UserCircleIcon className="w-3.5 h-3.5" />
                        Seller Panel
                    </Link>

                    <Link
                        to="/buyeradmin"
                        className="flex items-center gap-1 hover:text-yellow-200 transition-colors"
                    >
                        <ShoppingBagIcon className="w-3.5 h-3.5" />
                        Buyer Panel
                    </Link>

                    <span className="text-white/30">|</span>

                    {/* Tuner Auth */}
                    <Link
                        to="/tunersignup"
                        className="flex items-center gap-1 hover:text-yellow-200 transition-colors"
                    >
                        <UserPlusIcon className="w-3.5 h-3.5" />
                        Tuner Signup
                    </Link>

                    <Link
                        to="/tuner-sign-in"
                        className="flex items-center gap-1 hover:text-yellow-200 transition-colors"
                    >
                        <ArrowRightOnRectangleIcon className="w-3.5 h-3.5" />
                        Tuner Sign In
                    </Link>

                    <span className="text-white/30">|</span>

                    {/* Vendure Links */}
                    <a
                        href="http://localhost:3000/dashboard"
                        target="_blank"
                        rel="noopener noreferrer"
                        className="flex items-center gap-1 hover:text-yellow-200 transition-colors"
                    >
                        <WrenchScrewdriverIcon className="w-3.5 h-3.5" />
                        Dashboard
                    </a>

                    <a
                        href="http://localhost:3000/graphiql/admin"
                        target="_blank"
                        rel="noopener noreferrer"
                        className="flex items-center gap-1 hover:text-yellow-200 transition-colors"
                    >
                        <CommandLineIcon className="w-3.5 h-3.5" />
                        Admin API
                    </a>

                    <a
                        href="http://localhost:3000/graphiql/shop"
                        target="_blank"
                        rel="noopener noreferrer"
                        className="flex items-center gap-1 hover:text-yellow-200 transition-colors"
                    >
                        <ServerStackIcon className="w-3.5 h-3.5" />
                        Shop API
                    </a>

                    <a
                        href="http://localhost:3000/mailbox"
                        target="_blank"
                        rel="noopener noreferrer"
                        className="flex items-center gap-1 hover:text-yellow-200 transition-colors"
                    >
                        <InboxIcon className="w-3.5 h-3.5" />
                        Mailbox
                    </a>

                    <a
                        href="http://localhost:3000/assets"
                        target="_blank"
                        rel="noopener noreferrer"
                        className="flex items-center gap-1 hover:text-yellow-200 transition-colors"
                    >
                        <CircleStackIcon className="w-3.5 h-3.5" />
                        Assets
                    </a>
                </div>
            </div>
        </div>
    );
}
