import { useDarkMode } from '~/root';
import { Link, useNavigate } from '@remix-run/react';
import { useState, useRef, useEffect } from 'react';
import {
    BookOpenIcon,
    CodeBracketIcon,
    CpuChipIcon,
    ShoppingCartIcon,
    UserGroupIcon,
    CogIcon,
    MagnifyingGlassIcon,
    ExclamationTriangleIcon,
    ArrowLeftIcon,
} from '@heroicons/react/24/solid';

// Navigation structure for search
const sections = [
    {
        title: 'Getting Started',
        icon: BookOpenIcon,
        color: 'text-blue-500',
        bg: 'bg-blue-500/20',
        slug: 'getting-started',
        items: [
            { title: 'Platform Overview', description: 'Learn what TunerSwap is and how it works', slug: 'platform-overview' },
            { title: 'Creating an Account', description: 'Sign up as a buyer or seller', slug: 'creating-account' },
            { title: 'Navigating the Marketplace', description: 'Find tunes, parts, and services', slug: 'navigating-marketplace' },
        ],
    },
    {
        title: 'For Buyers',
        icon: ShoppingCartIcon,
        color: 'text-green-500',
        bg: 'bg-green-500/20',
        slug: 'buyers',
        items: [
            { title: 'Browsing Tunes', description: 'Search and filter available tunes', slug: 'browsing-tunes' },
            { title: 'Purchasing a Tune', description: 'Checkout process and payment options', slug: 'purchasing-tune' },
            { title: 'Installing Your Tune', description: 'How to apply purchased calibrations', slug: 'installing-tune' },
            { title: 'Buyer Dashboard', description: 'Manage orders, garage, and messages', slug: 'buyer-dashboard' },
        ],
    },
    {
        title: 'For Sellers',
        icon: UserGroupIcon,
        color: 'text-purple-500',
        bg: 'bg-purple-500/20',
        slug: 'sellers',
        items: [
            { title: 'Becoming a Tuner', description: 'Application and verification process', slug: 'becoming-tuner' },
            { title: 'Listing Your Tunes', description: 'Create and manage tune listings', slug: 'listing-tunes' },
            { title: 'Selling Parts & Services', description: 'Expand your offerings', slug: 'selling-parts-services' },
            { title: 'Seller Dashboard', description: 'Track orders, revenue, and analytics', slug: 'seller-dashboard' },
        ],
    },
    {
        title: 'Technical Reference',
        icon: CpuChipIcon,
        color: 'text-orange-500',
        bg: 'bg-orange-500/20',
        slug: 'technical',
        items: [
            { title: 'Supported Platforms', description: 'GM, Ford, Mopar, and more', slug: 'supported-platforms' },
            { title: 'Tuning Software', description: 'HP Tuners, EFI Live, SCT compatibility', slug: 'tuning-software' },
            { title: 'File Formats', description: 'Accepted calibration file types', slug: 'file-formats' },
        ],
    },
    {
        title: 'API Documentation',
        icon: CodeBracketIcon,
        color: 'text-red-500',
        bg: 'bg-red-500/20',
        slug: 'api',
        items: [
            { title: 'REST API Overview', description: 'Endpoints and authentication', slug: 'rest-api-overview' },
            { title: 'Webhooks', description: 'Real-time event notifications', slug: 'webhooks' },
            { title: 'Rate Limits', description: 'API usage guidelines', slug: 'rate-limits' },
        ],
    },
    {
        title: 'Account & Settings',
        icon: CogIcon,
        color: 'text-yellow-500',
        bg: 'bg-yellow-500/20',
        slug: 'account',
        items: [
            { title: 'Profile Settings', description: 'Update your information', slug: 'profile-settings' },
            { title: 'Security', description: 'Password and two-factor auth', slug: 'security' },
            { title: 'Notifications', description: 'Email and alert preferences', slug: 'notifications' },
        ],
    },
];

// Searchable index with keywords
const searchIndex = sections.flatMap(section =>
    section.items.map(item => ({
        title: item.title,
        section: section.title,
        sectionSlug: section.slug,
        slug: item.slug,
        path: `/dev/docs/${section.slug}/${item.slug}`,
        keywords: getKeywordsForDoc(section.slug, item.slug),
    }))
);

function getKeywordsForDoc(sectionSlug: string, itemSlug: string): string[] {
    const keywordMap: Record<string, string[]> = {
        'getting-started/platform-overview': ['tunerswap', 'what is', 'introduction', 'overview', 'marketplace', 'how it works', 'features'],
        'getting-started/creating-account': ['sign up', 'register', 'account', 'create', 'login', 'buyer', 'seller', 'verification'],
        'getting-started/navigating-marketplace': ['browse', 'search', 'filter', 'find', 'navigate', 'tuners', 'products'],
        'buyers/browsing-tunes': ['browse', 'search', 'find', 'tunes', 'filter', 'shop', 'catalog'],
        'buyers/purchasing-tune': ['buy', 'purchase', 'checkout', 'payment', 'cart', 'order'],
        'buyers/installing-tune': ['install', 'flash', 'apply', 'calibration', 'ecu', 'upload'],
        'buyers/buyer-dashboard': ['dashboard', 'orders', 'garage', 'messages', 'downloads', 'account'],
        'sellers/becoming-tuner': ['become', 'seller', 'tuner', 'apply', 'verification', 'requirements'],
        'sellers/listing-tunes': ['list', 'create', 'sell', 'upload', 'product', 'pricing'],
        'sellers/selling-parts-services': ['parts', 'services', 'dyno', 'hardware', 'installation'],
        'sellers/seller-dashboard': ['dashboard', 'analytics', 'orders', 'revenue', 'sales'],
        'technical/supported-platforms': ['gm', 'ford', 'mopar', 'dodge', 'chevy', 'vehicles', 'cars', 'trucks'],
        'technical/tuning-software': ['hp tuners', 'efi live', 'sct', 'software', 'vcm', 'interface'],
        'technical/file-formats': ['file', 'format', 'hpt', 'tun', 'bin', 'calibration'],
        'api/rest-api-overview': ['api', 'rest', 'endpoints', 'authentication', 'integration', 'developer'],
        'api/webhooks': ['webhook', 'events', 'notifications', 'real-time', 'callback'],
        'api/rate-limits': ['rate', 'limit', 'throttle', 'api', 'requests'],
        'account/profile-settings': ['profile', 'settings', 'update', 'information', 'avatar'],
        'account/security': ['security', 'password', '2fa', 'authentication', 'protect'],
        'account/notifications': ['notifications', 'email', 'alerts', 'preferences', 'subscribe'],
    };
    return keywordMap[`${sectionSlug}/${itemSlug}`] || [];
}

export default function DocsIndexPage() {
    const { darkMode } = useDarkMode();
    const navigate = useNavigate();
    const [searchQuery, setSearchQuery] = useState('');
    const [searchResults, setSearchResults] = useState<typeof searchIndex>([]);
    const [showSearchDropdown, setShowSearchDropdown] = useState(false);
    const [selectedSearchIndex, setSelectedSearchIndex] = useState(0);
    const searchInputRef = useRef<HTMLInputElement>(null);
    const searchDropdownRef = useRef<HTMLDivElement>(null);

    // Search function
    const handleSearch = (query: string) => {
        setSearchQuery(query);
        if (query.trim().length === 0) {
            setSearchResults([]);
            setShowSearchDropdown(false);
            return;
        }

        const lowerQuery = query.toLowerCase();
        const results = searchIndex.filter(item => {
            const titleMatch = item.title.toLowerCase().includes(lowerQuery);
            const sectionMatch = item.section.toLowerCase().includes(lowerQuery);
            const keywordMatch = item.keywords.some(k => k.toLowerCase().includes(lowerQuery));
            return titleMatch || sectionMatch || keywordMatch;
        });

        setSearchResults(results);
        setShowSearchDropdown(true);
        setSelectedSearchIndex(0);
    };

    // Handle keyboard navigation in search
    const handleSearchKeyDown = (e: React.KeyboardEvent) => {
        if (!showSearchDropdown || searchResults.length === 0) return;

        if (e.key === 'ArrowDown') {
            e.preventDefault();
            setSelectedSearchIndex(prev => Math.min(prev + 1, searchResults.length - 1));
        } else if (e.key === 'ArrowUp') {
            e.preventDefault();
            setSelectedSearchIndex(prev => Math.max(prev - 1, 0));
        } else if (e.key === 'Enter') {
            e.preventDefault();
            const selected = searchResults[selectedSearchIndex];
            if (selected) {
                navigate(selected.path);
                setShowSearchDropdown(false);
                setSearchQuery('');
            }
        } else if (e.key === 'Escape') {
            setShowSearchDropdown(false);
        }
    };

    // Close dropdown when clicking outside
    useEffect(() => {
        const handleClickOutside = (e: MouseEvent) => {
            if (
                searchDropdownRef.current &&
                !searchDropdownRef.current.contains(e.target as Node) &&
                searchInputRef.current &&
                !searchInputRef.current.contains(e.target as Node)
            ) {
                setShowSearchDropdown(false);
            }
        };
        document.addEventListener('mousedown', handleClickOutside);
        return () => document.removeEventListener('mousedown', handleClickOutside);
    }, []);

    const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';
    const borderColor = darkMode ? 'border-zinc-700' : 'border-gray-200';

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-50'}`}>
            {/* Back to Site Button */}
            <div className={`${darkMode ? 'bg-zinc-900 border-zinc-800' : 'bg-white border-gray-200'} border-b`}>
                <div className="max-w-6xl mx-auto px-6 py-3">
                    <Link
                        to="/"
                        className={`inline-flex items-center gap-2 text-sm ${textSecondary} hover:text-red-500 transition-colors`}
                    >
                        <ArrowLeftIcon className="w-4 h-4" />
                        Back to TunerSwap
                    </Link>
                </div>
            </div>

            {/* Hero Section */}
            <div className="relative overflow-visible">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-red-500/10 via-orange-500/10 to-yellow-500/10 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-24 px-6 flex flex-col items-center text-center">
                    <BookOpenIcon className="w-16 h-16 text-red-500 mb-6" />
                    <h1 className="text-5xl sm:text-6xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-yellow-400 via-red-500 to-blue-500">
                            Documentation
                        </span>
                    </h1>
                    <p className="mt-6 text-xl text-gray-300 max-w-2xl">
                        Everything you need to know about using TunerSwap as a buyer, seller, or developer.
                    </p>

                    {/* Search Bar */}
                    <div className="mt-8 w-full max-w-xl relative z-10">
                        <div className="relative">
                            <MagnifyingGlassIcon className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400 z-10" />
                            <input
                                ref={searchInputRef}
                                type="text"
                                value={searchQuery}
                                onChange={(e) => handleSearch(e.target.value)}
                                onKeyDown={handleSearchKeyDown}
                                onFocus={() => searchQuery && setShowSearchDropdown(true)}
                                placeholder="Search documentation..."
                                className={`w-full pl-12 pr-4 py-4 rounded-xl ${
                                    darkMode
                                        ? 'bg-zinc-800 border-zinc-700 text-white placeholder-gray-500'
                                        : 'bg-white border-gray-200 text-gray-900 placeholder-gray-400'
                                } border focus:ring-2 focus:ring-red-500 focus:outline-none`}
                            />
                            {/* Search Dropdown */}
                            {showSearchDropdown && searchResults.length > 0 && (
                                <div
                                    ref={searchDropdownRef}
                                    className={`absolute top-full left-0 right-0 mt-2 ${darkMode ? 'bg-zinc-800 border-zinc-700' : 'bg-white border-gray-200'} border rounded-xl shadow-2xl z-50`}
                                >
                                    <div className="overflow-y-auto" style={{ maxHeight: '400px' }}>
                                        {searchResults.slice(0, 10).map((result, index) => (
                                            <Link
                                                key={result.path}
                                                to={result.path}
                                                onClick={() => {
                                                    setShowSearchDropdown(false);
                                                    setSearchQuery('');
                                                }}
                                                className={`block px-4 py-4 ${
                                                    index === selectedSearchIndex
                                                        ? 'bg-red-500/20'
                                                        : darkMode ? 'hover:bg-zinc-700' : 'hover:bg-gray-100'
                                                } transition-colors ${index < searchResults.slice(0, 10).length - 1 ? `border-b ${borderColor}` : ''}`}
                                            >
                                                <div className={`font-medium ${index === selectedSearchIndex ? 'text-red-500' : textPrimary}`}>
                                                    {result.title}
                                                </div>
                                                <div className={`text-sm ${textSecondary}`}>
                                                    {result.section}
                                                </div>
                                            </Link>
                                        ))}
                                    </div>
                                    {searchResults.length > 10 && (
                                        <div className={`px-4 py-2 text-sm ${textSecondary} border-t ${borderColor}`}>
                                            +{searchResults.length - 10} more results
                                        </div>
                                    )}
                                </div>
                            )}
                            {showSearchDropdown && searchQuery && searchResults.length === 0 && (
                                <div
                                    ref={searchDropdownRef}
                                    className={`absolute top-full left-0 right-0 mt-2 ${darkMode ? 'bg-zinc-800 border-zinc-700' : 'bg-white border-gray-200'} border rounded-xl shadow-2xl p-4 z-50`}
                                >
                                    <div className={`${textSecondary} text-center`}>
                                        No results found for "{searchQuery}"
                                    </div>
                                </div>
                            )}
                        </div>
                    </div>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-50'} to-transparent`} />
            </div>

            {/* Quick Links Bar */}
            <div className="max-w-6xl mx-auto px-6 -mt-8 relative z-[5]">
                <div className={`${cardClasses} p-4`}>
                    <div className="flex flex-wrap items-center justify-center gap-4">
                        <span className={textSecondary}>Quick links:</span>
                        <Link to="/how" className="text-red-500 hover:text-red-400 text-sm">How It Works</Link>
                        <Link to="/dev/changelog" className="text-red-500 hover:text-red-400 text-sm">Changelog</Link>
                        <Link to="/dev/roadmap" className="text-red-500 hover:text-red-400 text-sm">Roadmap</Link>
                        <Link to="/contact" className="text-red-500 hover:text-red-400 text-sm">Get Help</Link>
                    </div>
                </div>
            </div>

            <main className="max-w-6xl mx-auto px-6 py-16">
                {/* Documentation Sections with all links */}
                <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
                    {sections.map((section) => (
                        <div key={section.title} className={`${cardClasses} p-6 hover:shadow-2xl hover:shadow-red-500/10 transition-all duration-300`}>
                            <div className="flex items-center gap-3 mb-4">
                                <div className={`w-10 h-10 rounded-lg ${section.bg} flex items-center justify-center`}>
                                    <section.icon className={`w-5 h-5 ${section.color}`} />
                                </div>
                                <h2 className={`text-xl font-bold ${textPrimary}`}>{section.title}</h2>
                            </div>
                            <ul className="space-y-3">
                                {section.items.map((item) => (
                                    <Link
                                        key={item.title}
                                        to={`/dev/docs/${section.slug}/${item.slug}`}
                                        className="block group"
                                    >
                                        <div className={`font-medium ${textPrimary} group-hover:text-red-500 transition-colors`}>
                                            {item.title}
                                        </div>
                                        <div className={`text-sm ${textSecondary}`}>{item.description}</div>
                                    </Link>
                                ))}
                            </ul>
                        </div>
                    ))}
                </div>

                {/* Disclaimer */}
                <div className="mt-12">
                    <div className={`${cardClasses} p-8`}>
                        <div className="flex items-start gap-4">
                            <div className="w-12 h-12 rounded-xl bg-yellow-500/20 flex items-center justify-center flex-shrink-0">
                                <ExclamationTriangleIcon className="w-6 h-6 text-yellow-500" />
                            </div>
                            <div>
                                <h3 className={`text-xl font-bold mb-2 ${textPrimary}`}>Documentation In Progress</h3>
                                <p className={`${textSecondary}`}>
                                    We're actively building out our documentation. Check back soon for detailed guides,
                                    tutorials, and API references. Some pages may be incomplete or subject to change as we continue development.
                                </p>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Need Help Section */}
                <div className="mt-8">
                    <div className={`${cardClasses} p-8 text-center`}>
                        <h3 className={`text-xl font-bold mb-4 ${textPrimary}`}>Need Help?</h3>
                        <p className={`${textSecondary} max-w-xl mx-auto mb-6`}>
                            Can't find what you're looking for? Our team is here to help.
                        </p>
                        <div className="flex flex-wrap justify-center gap-4">
                            <Link
                                to="/contact"
                                className="px-6 py-3 bg-gradient-to-r from-red-500 to-orange-500 text-white font-bold rounded-xl hover:shadow-lg hover:shadow-red-500/25 transition-all"
                            >
                                Contact Support
                            </Link>
                            <Link
                                to="/community"
                                className={`px-6 py-3 rounded-xl font-bold ${darkMode ? 'bg-zinc-700 text-white hover:bg-zinc-600' : 'bg-gray-200 text-gray-900 hover:bg-gray-300'} transition-all`}
                            >
                                Community Forums
                            </Link>
                        </div>
                    </div>
                </div>
            </main>
        </div>
    );
}
