import { useDarkMode } from '~/root';
import { Link } from '@remix-run/react';
import {
    AcademicCapIcon,
    QuestionMarkCircleIcon,
    ComputerDesktopIcon,
    WrenchScrewdriverIcon,
    ShieldExclamationIcon,
    BuildingOffice2Icon,
    BookOpenIcon,
    PlayCircleIcon
} from '@heroicons/react/24/solid';

export default function LearnHubPage() {
    const { darkMode } = useDarkMode();

    const learnLinks = [
        {
            title: 'How It Works',
            description: 'New to TunerSwap? Learn how to buy tunes, find tuners, and get your vehicle dialed in.',
            icon: QuestionMarkCircleIcon,
            color: 'text-blue-500',
            bg: 'from-blue-500/20 to-cyan-500/20',
            href: '/how',
        },
        {
            title: 'Recommended Software',
            description: 'The best tuning software for your platform - HP Tuners, EFI Live, SCT, and more.',
            icon: ComputerDesktopIcon,
            color: 'text-green-500',
            bg: 'from-green-500/20 to-emerald-500/20',
            href: '/software',
        },
        {
            title: 'Recommended Hardware',
            description: 'Tuning interfaces, wideband sensors, and diagnostic tools you need.',
            icon: WrenchScrewdriverIcon,
            color: 'text-orange-500',
            bg: 'from-orange-500/20 to-amber-500/20',
            href: '/hardware',
        },
        {
            title: 'Tuning Disclaimer',
            description: 'Important information about risks, warranties, and legal considerations.',
            icon: ShieldExclamationIcon,
            color: 'text-red-500',
            bg: 'from-red-500/20 to-pink-500/20',
            href: '/disclaimer',
        },
        {
            title: 'About Us',
            description: 'Meet the team behind TunerSwap and learn about our mission.',
            icon: BuildingOffice2Icon,
            color: 'text-purple-500',
            bg: 'from-purple-500/20 to-violet-500/20',
            href: '/about',
        },
    ];

    const quickTips = [
        'Always get a baseline tune before modifications',
        'Log your data during pulls for your tuner',
        'Start with a reputable tuner with verified reviews',
        'Make sure your hardware is compatible with your vehicle',
        'Keep your stock tune backed up safely',
    ];

    const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-yellow-500/10 via-orange-500/10 to-red-500/10 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-32 px-6 flex flex-col items-center text-center">
                    <AcademicCapIcon className="w-16 h-16 text-yellow-500 mb-6" />
                    <h1 className="text-5xl sm:text-6xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-yellow-400 via-orange-500 to-red-500">
                            Learning Hub
                        </span>
                    </h1>
                    <p className="mt-6 text-xl text-gray-300 max-w-2xl">
                        Everything you need to know about tuning, from getting started to choosing the right tools.
                    </p>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            <main className="max-w-4xl mx-auto px-6 py-16">
                {/* Main Learn Links */}
                <div className="grid sm:grid-cols-2 gap-6 mb-12">
                    {learnLinks.map((link) => (
                        <Link
                            key={link.title}
                            to={link.href}
                            className={`${cardClasses} p-6 group hover:shadow-2xl hover:shadow-yellow-500/10 transition-all duration-300 hover:-translate-y-1`}
                        >
                            <div className={`w-12 h-12 rounded-xl bg-gradient-to-br ${link.bg} flex items-center justify-center mb-4`}>
                                <link.icon className={`w-6 h-6 ${link.color}`} />
                            </div>
                            <h2 className={`text-lg font-bold mb-2 ${textPrimary} group-hover:text-yellow-400 transition-colors`}>
                                {link.title}
                            </h2>
                            <p className={`text-sm ${textSecondary}`}>{link.description}</p>
                            <div className="mt-3 text-yellow-500 text-sm font-medium group-hover:translate-x-2 transition-transform">
                                Learn more &rarr;
                            </div>
                        </Link>
                    ))}
                </div>

                {/* Quick Tips */}
                <div className={`${cardClasses} p-8 mb-12`}>
                    <h3 className={`text-lg font-bold mb-6 ${textPrimary} flex items-center gap-2`}>
                        <BookOpenIcon className="w-5 h-5 text-yellow-500" />
                        Quick Tips for New Tuners
                    </h3>
                    <ul className="space-y-3">
                        {quickTips.map((tip, index) => (
                            <li key={index} className="flex items-start gap-3">
                                <span className="flex-shrink-0 w-6 h-6 rounded-full bg-gradient-to-r from-yellow-500 to-orange-500 flex items-center justify-center text-white text-xs font-bold">
                                    {index + 1}
                                </span>
                                <span className={textSecondary}>{tip}</span>
                            </li>
                        ))}
                    </ul>
                </div>

                {/* Video Tutorials Coming Soon */}
                <div className={`${cardClasses} p-8`}>
                    <div className="flex items-center gap-4 mb-4">
                        <PlayCircleIcon className="w-10 h-10 text-yellow-500" />
                        <div>
                            <h3 className={`text-lg font-bold ${textPrimary}`}>Video Tutorials</h3>
                            <p className={`text-sm ${textSecondary}`}>Coming Soon</p>
                        </div>
                    </div>
                    <p className={textSecondary}>
                        We're working on video guides covering everything from basic tuning concepts to advanced techniques. Stay tuned!
                    </p>
                </div>

                {/* CTA */}
                <div className="mt-12 text-center">
                    <div className={`${cardClasses} p-8`}>
                        <AcademicCapIcon className="w-12 h-12 mx-auto mb-4 text-yellow-500" />
                        <h3 className={`text-xl font-bold mb-4 ${textPrimary}`}>Ready to Get Started?</h3>
                        <p className={`${textSecondary} max-w-xl mx-auto mb-6`}>
                            Browse our marketplace to find the perfect tune for your vehicle.
                        </p>
                        <div className="flex flex-wrap justify-center gap-4">
                            <Link
                                to="/tunes"
                                className="inline-block px-6 py-3 bg-gradient-to-r from-yellow-500 to-orange-500 text-white font-bold rounded-xl hover:shadow-lg hover:shadow-yellow-500/25 transition-all"
                            >
                                Browse Tunes
                            </Link>
                            <Link
                                to="/sellers"
                                className={`px-6 py-3 rounded-xl font-bold ${darkMode ? 'bg-zinc-700 text-white hover:bg-zinc-600' : 'bg-gray-200 text-gray-900 hover:bg-gray-300'} transition-all`}
                            >
                                Find a Tuner
                            </Link>
                        </div>
                    </div>
                </div>
            </main>
        </div>
    );
}
