import { useDarkMode } from '~/root';
import { Link } from '@remix-run/react';
import {
    UserGroupIcon,
    ChatBubbleLeftRightIcon,
    NewspaperIcon,
    TrophyIcon,
    CalendarDaysIcon,
    FireIcon
} from '@heroicons/react/24/solid';

export default function CommunityHubPage() {
    const { darkMode } = useDarkMode();

    const communityLinks = [
        {
            title: 'Forums',
            description: 'Discuss tuning, share builds, ask questions, and connect with fellow enthusiasts.',
            icon: ChatBubbleLeftRightIcon,
            color: 'text-blue-500',
            bg: 'from-blue-500/20 to-cyan-500/20',
            href: '/forums',
        },
        {
            title: 'Blog',
            description: 'News, tutorials, featured builds, and updates from the TunerSwap team.',
            icon: NewspaperIcon,
            color: 'text-purple-500',
            bg: 'from-purple-500/20 to-pink-500/20',
            href: '/blog',
        },
    ];

    const upcomingFeatures = [
        {
            title: 'Build Showcases',
            description: 'Share your build with photos, specs, and mods.',
            icon: TrophyIcon,
            color: 'text-yellow-500',
        },
        {
            title: 'Events Calendar',
            description: 'Car meets, dyno days, and track events near you.',
            icon: CalendarDaysIcon,
            color: 'text-green-500',
        },
        {
            title: 'Hot Builds',
            description: 'Trending builds and most popular tunes this week.',
            icon: FireIcon,
            color: 'text-orange-500',
        },
    ];

    const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-blue-500/10 via-purple-500/10 to-pink-500/10 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-32 px-6 flex flex-col items-center text-center">
                    <UserGroupIcon className="w-16 h-16 text-blue-500 mb-6" />
                    <h1 className="text-5xl sm:text-6xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-blue-400 via-purple-500 to-pink-500">
                            Community Hub
                        </span>
                    </h1>
                    <p className="mt-6 text-xl text-gray-300 max-w-2xl">
                        Connect with tuners, share your builds, and be part of the TunerSwap community.
                    </p>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            <main className="max-w-4xl mx-auto px-6 py-16">
                {/* Main Community Links */}
                <div className="grid sm:grid-cols-2 gap-6 mb-12">
                    {communityLinks.map((link) => (
                        <Link
                            key={link.title}
                            to={link.href}
                            className={`${cardClasses} p-8 group hover:shadow-2xl hover:shadow-blue-500/10 transition-all duration-300 hover:-translate-y-1`}
                        >
                            <div className={`w-14 h-14 rounded-2xl bg-gradient-to-br ${link.bg} flex items-center justify-center mb-4`}>
                                <link.icon className={`w-7 h-7 ${link.color}`} />
                            </div>
                            <h2 className={`text-xl font-bold mb-2 ${textPrimary} group-hover:text-blue-400 transition-colors`}>
                                {link.title}
                            </h2>
                            <p className={textSecondary}>{link.description}</p>
                            <div className="mt-4 text-blue-500 text-sm font-medium group-hover:translate-x-2 transition-transform">
                                Explore &rarr;
                            </div>
                        </Link>
                    ))}
                </div>

                {/* Community Stats */}
                <div className={`${cardClasses} p-8 mb-12`}>
                    <h3 className={`text-lg font-bold mb-6 ${textPrimary}`}>Community Stats</h3>
                    <div className="grid grid-cols-2 sm:grid-cols-4 gap-6 text-center">
                        <div>
                            <div className="text-3xl font-black text-blue-500">500+</div>
                            <div className={`text-sm ${textSecondary}`}>Members</div>
                        </div>
                        <div>
                            <div className="text-3xl font-black text-purple-500">1.2K</div>
                            <div className={`text-sm ${textSecondary}`}>Forum Posts</div>
                        </div>
                        <div>
                            <div className="text-3xl font-black text-pink-500">50+</div>
                            <div className={`text-sm ${textSecondary}`}>Verified Tuners</div>
                        </div>
                        <div>
                            <div className="text-3xl font-black text-green-500">Growing</div>
                            <div className={`text-sm ${textSecondary}`}>Every Day</div>
                        </div>
                    </div>
                </div>

                {/* Coming Soon */}
                <div className={`${cardClasses} p-8`}>
                    <h3 className={`text-lg font-bold mb-6 ${textPrimary}`}>Coming Soon</h3>
                    <div className="grid sm:grid-cols-3 gap-6">
                        {upcomingFeatures.map((feature) => (
                            <div key={feature.title} className={`p-4 rounded-xl ${darkMode ? 'bg-zinc-800' : 'bg-gray-50'}`}>
                                <feature.icon className={`w-8 h-8 ${feature.color} mb-3`} />
                                <h4 className={`font-bold mb-1 ${textPrimary}`}>{feature.title}</h4>
                                <p className={`text-sm ${textSecondary}`}>{feature.description}</p>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Join CTA */}
                <div className="mt-12 text-center">
                    <div className={`${cardClasses} p-8`}>
                        <UserGroupIcon className="w-12 h-12 mx-auto mb-4 text-blue-500" />
                        <h3 className={`text-xl font-bold mb-4 ${textPrimary}`}>Join the Conversation</h3>
                        <p className={`${textSecondary} max-w-xl mx-auto mb-6`}>
                            Whether you're a seasoned tuner or just getting started, there's a place for you here.
                        </p>
                        <Link
                            to="/forums"
                            className="inline-block px-6 py-3 bg-gradient-to-r from-blue-500 to-purple-500 text-white font-bold rounded-xl hover:shadow-lg hover:shadow-blue-500/25 transition-all"
                        >
                            Visit Forums
                        </Link>
                    </div>
                </div>
            </main>
        </div>
    );
}
