import { CheckCircleIcon } from '@heroicons/react/24/solid';
import { useSellerAdminStyles } from './useSellerAdminStyles';
import { sellerData } from '~/data/seller-demo-data';

export function ConnectionsTab() {
    const { darkMode, cardClasses, textPrimary, textSecondary } = useSellerAdminStyles();

    return (
        <div className="space-y-6">
            <h2 className={`text-2xl font-bold ${textPrimary}`}>Connections</h2>

            {/* Payment Processors */}
            <div className={cardClasses}>
                <div className="p-4 border-b border-zinc-700">
                    <h3 className={`font-semibold ${textPrimary}`}>Payment Processors</h3>
                </div>
                <div className="p-4 space-y-4">
                    <div className="flex items-center justify-between p-4 rounded-xl border border-zinc-700">
                        <div className="flex items-center gap-4">
                            <div className="w-12 h-12 bg-purple-500 rounded-lg flex items-center justify-center text-white font-bold">
                                S
                            </div>
                            <div>
                                <p className={`font-medium ${textPrimary}`}>Stripe</p>
                                <p className={`text-sm ${textSecondary}`}>Accept credit cards and more</p>
                            </div>
                        </div>
                        {sellerData.stripeConnected ? (
                            <div className="flex items-center gap-2">
                                <CheckCircleIcon className="w-5 h-5 text-green-500" />
                                <span className="text-green-500">Connected</span>
                                <button className="ml-4 text-red-500 hover:text-red-400 text-sm">Disconnect</button>
                            </div>
                        ) : (
                            <button className="px-4 py-2 bg-purple-500 hover:bg-purple-600 text-white rounded-lg transition-colors">
                                Connect
                            </button>
                        )}
                    </div>

                    <div className="flex items-center justify-between p-4 rounded-xl border border-zinc-700">
                        <div className="flex items-center gap-4">
                            <div className="w-12 h-12 bg-blue-500 rounded-lg flex items-center justify-center text-white font-bold">
                                P
                            </div>
                            <div>
                                <p className={`font-medium ${textPrimary}`}>PayPal</p>
                                <p className={`text-sm ${textSecondary}`}>Accept PayPal payments</p>
                            </div>
                        </div>
                        {sellerData.paypalConnected ? (
                            <div className="flex items-center gap-2">
                                <CheckCircleIcon className="w-5 h-5 text-green-500" />
                                <span className="text-green-500">Connected</span>
                                <button className="ml-4 text-red-500 hover:text-red-400 text-sm">Disconnect</button>
                            </div>
                        ) : (
                            <button className="px-4 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded-lg transition-colors">
                                Connect
                            </button>
                        )}
                    </div>
                </div>
            </div>

            {/* Tuning Software */}
            <div className={cardClasses}>
                <div className="p-4 border-b border-zinc-700">
                    <h3 className={`font-semibold ${textPrimary}`}>Tuning Software Integrations</h3>
                </div>
                <div className="p-4 space-y-4">
                    <div className="flex items-center justify-between p-4 rounded-xl border border-zinc-700">
                        <div className="flex items-center gap-4">
                            <div className="w-12 h-12 bg-red-500 rounded-lg flex items-center justify-center text-white font-bold">
                                HP
                            </div>
                            <div>
                                <p className={`font-medium ${textPrimary}`}>HP Tuners</p>
                                <p className={`text-sm ${textSecondary}`}>VCM Suite integration</p>
                            </div>
                        </div>
                        <button className="px-4 py-2 bg-red-500 hover:bg-red-600 text-white rounded-lg transition-colors">
                            Configure
                        </button>
                    </div>

                    <div className="flex items-center justify-between p-4 rounded-xl border border-zinc-700">
                        <div className="flex items-center gap-4">
                            <div className="w-12 h-12 bg-green-500 rounded-lg flex items-center justify-center text-white font-bold">
                                EFI
                            </div>
                            <div>
                                <p className={`font-medium ${textPrimary}`}>EFI Live</p>
                                <p className={`text-sm ${textSecondary}`}>AutoCal integration</p>
                            </div>
                        </div>
                        <button className="px-4 py-2 bg-green-500 hover:bg-green-600 text-white rounded-lg transition-colors">
                            Configure
                        </button>
                    </div>
                </div>
            </div>

            {/* Social Media */}
            <div className={cardClasses}>
                <div className="p-4 border-b border-zinc-700">
                    <h3 className={`font-semibold ${textPrimary}`}>Social Media</h3>
                </div>
                <div className="p-4 space-y-4">
                    <div className="flex items-center justify-between p-4 rounded-xl border border-zinc-700">
                        <div className="flex items-center gap-4">
                            <div className="w-12 h-12 bg-gradient-to-br from-purple-500 via-pink-500 to-orange-500 rounded-lg flex items-center justify-center text-white font-bold">
                                IG
                            </div>
                            <div>
                                <p className={`font-medium ${textPrimary}`}>Instagram</p>
                                <p className={`text-sm ${textSecondary}`}>Sync your posts</p>
                            </div>
                        </div>
                        <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                            Connect
                        </button>
                    </div>

                    <div className="flex items-center justify-between p-4 rounded-xl border border-zinc-700">
                        <div className="flex items-center gap-4">
                            <div className="w-12 h-12 bg-blue-600 rounded-lg flex items-center justify-center text-white font-bold">
                                FB
                            </div>
                            <div>
                                <p className={`font-medium ${textPrimary}`}>Facebook</p>
                                <p className={`text-sm ${textSecondary}`}>Connect your page</p>
                            </div>
                        </div>
                        <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                            Connect
                        </button>
                    </div>

                    <div className="flex items-center justify-between p-4 rounded-xl border border-zinc-700">
                        <div className="flex items-center gap-4">
                            <div className="w-12 h-12 bg-red-600 rounded-lg flex items-center justify-center text-white font-bold">
                                YT
                            </div>
                            <div>
                                <p className={`font-medium ${textPrimary}`}>YouTube</p>
                                <p className={`text-sm ${textSecondary}`}>Link your channel</p>
                            </div>
                        </div>
                        <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                            Connect
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
