import type { LoaderFunctionArgs, MetaFunction } from '@remix-run/node';
import { json, redirect } from '@remix-run/node';
import { useLoaderData } from '@remix-run/react';
import { ListTuneTab } from '~/components/seller-admin';
import { getActiveCustomerDetails } from '~/providers/customer/customer';
import { getSellerProfile } from '~/providers/seller';

export const meta: MetaFunction = () => {
    return [{ title: 'List Tune | Seller Dashboard | TunerSwap' }];
};

export async function loader({ request }: LoaderFunctionArgs) {
    // Check if user is logged in
    const { activeCustomer } = await getActiveCustomerDetails({ request });
    if (!activeCustomer) {
        return redirect('/tuner-sign-in?redirectTo=/selleradmin/list/tune');
    }

    // Check if user has a seller profile
    const profileResult = await getSellerProfile(request);

    return json({
        isDemo: profileResult.isDemo
    });
}

export default function SellerListTuneRoute() {
    const { isDemo } = useLoaderData<typeof loader>();

    return (
        <>
            {isDemo && (
                <div className="mb-4 px-4 py-2 bg-yellow-500/20 border border-yellow-500/50 rounded-lg text-yellow-500 text-sm">
                    Demo Mode: Some features may use sample data
                </div>
            )}
            <ListTuneTab />
        </>
    );
}
