import { useDarkMode } from '~/root';
import { Link } from '@remix-run/react';
import {
    BoltIcon,
    ShieldCheckIcon,
    MagnifyingGlassIcon,
    CreditCardIcon,
    DocumentArrowDownIcon,
    ChatBubbleLeftRightIcon,
    StarIcon,
    CheckCircleIcon,
    TruckIcon,
} from '@heroicons/react/24/solid';

export default function DriversPage() {
    const { darkMode } = useDarkMode();

    const benefits = [
        {
            icon: MagnifyingGlassIcon,
            title: "Find the Perfect Tune",
            desc: "Browse tunes by make, model, year, and modifications. Filter by tuner ratings, price, and performance gains.",
            color: "text-red-500"
        },
        {
            icon: ShieldCheckIcon,
            title: "Verified Tuners",
            desc: "Every tuner on our platform is vetted. Read reviews from real customers before you buy.",
            color: "text-green-500"
        },
        {
            icon: CreditCardIcon,
            title: "Secure Payments",
            desc: "Pay safely with credit card, PayPal, or crypto. Your payment info is always protected.",
            color: "text-blue-500"
        },
        {
            icon: DocumentArrowDownIcon,
            title: "Instant Downloads",
            desc: "Get your tune files immediately after purchase. No waiting, no hassle.",
            color: "text-yellow-500"
        },
        {
            icon: ChatBubbleLeftRightIcon,
            title: "Direct Tuner Support",
            desc: "Message tuners directly with questions. Get help with installation and compatibility.",
            color: "text-purple-500"
        },
        {
            icon: StarIcon,
            title: "Quality Guaranteed",
            desc: "All tunes come with detailed instructions. Not satisfied? We'll help make it right.",
            color: "text-orange-500"
        },
    ];

    const steps = [
        { num: 1, title: "Find Your Tune", desc: "Search for tunes compatible with your vehicle and modifications." },
        { num: 2, title: "Purchase Securely", desc: "Complete your order with our secure checkout. Multiple payment options available." },
        { num: 3, title: "Download & Install", desc: "Instantly download your tune file and follow the provided instructions." },
    ];

    const platforms = [
        "GM / Chevy / LS",
        "Ford / Coyote",
        "Dodge / HEMI",
        "BMW / Audi / VW",
        "Subaru / Nissan",
        "Diesel Trucks",
    ];

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-red-500/20 via-orange-500/10 to-red-500/20 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-32 px-6 flex flex-col items-center text-center">
                    <TruckIcon className="w-20 h-20 text-red-500 mb-6" />
                    <h1 className="text-5xl sm:text-6xl lg:text-7xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-red-400 via-orange-500 to-red-500">
                            For Drivers
                        </span>
                    </h1>
                    <p className="mt-6 text-xl sm:text-2xl text-gray-300 max-w-3xl">
                        Unlock your vehicle's full potential. Find professional-grade tunes from verified tuners, delivered instantly.
                    </p>
                    <div className="mt-10 flex flex-col sm:flex-row gap-4">
                        <Link
                            to="/collections"
                            className="px-8 py-4 bg-gradient-to-r from-red-500 via-orange-500 to-red-600 text-white font-bold rounded-xl shadow-lg hover:shadow-red-500/25 hover:shadow-2xl transform hover:-translate-y-1 transition-all duration-300"
                        >
                            Browse Tunes
                        </Link>
                        <Link
                            to="/sign-up"
                            className="px-8 py-4 bg-white/10 backdrop-blur-sm border border-white/20 text-white font-bold rounded-xl hover:bg-white/20 transition-all duration-300"
                        >
                            Create Free Account
                        </Link>
                    </div>

                    {/* Stats */}
                    <div className="mt-16 grid grid-cols-3 gap-8 sm:gap-16">
                        <div className="text-center">
                            <div className="text-3xl sm:text-4xl font-bold text-white">500+</div>
                            <div className="text-gray-400 text-sm sm:text-base">Tunes Available</div>
                        </div>
                        <div className="text-center">
                            <div className="text-3xl sm:text-4xl font-bold text-white">1000+</div>
                            <div className="text-gray-400 text-sm sm:text-base">Happy Drivers</div>
                        </div>
                        <div className="text-center">
                            <div className="text-3xl sm:text-4xl font-bold text-white">4.9</div>
                            <div className="text-gray-400 text-sm sm:text-base">Avg. Rating</div>
                        </div>
                    </div>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            {/* Benefits Section */}
            <section className="max-w-6xl mx-auto px-6 py-20">
                <div className="text-center mb-16">
                    <h2 className={`text-3xl sm:text-4xl font-bold ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                        Why Buy on TunerSwap?
                    </h2>
                    <p className={`mt-4 text-lg ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                        The safest and easiest way to get professional tunes
                    </p>
                </div>

                <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
                    {benefits.map((benefit) => (
                        <div
                            key={benefit.title}
                            className={`${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl p-8 hover:shadow-2xl hover:shadow-red-500/10 transition-all duration-300 hover:-translate-y-2`}
                        >
                            <benefit.icon className={`w-12 h-12 mb-4 ${benefit.color}`} />
                            <h3 className={`font-bold text-xl mb-3 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                                {benefit.title}
                            </h3>
                            <p className={`${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                                {benefit.desc}
                            </p>
                        </div>
                    ))}
                </div>
            </section>

            {/* How It Works */}
            <section className={`py-20 ${darkMode ? 'bg-zinc-800/30' : 'bg-gray-50'}`}>
                <div className="max-w-6xl mx-auto px-6">
                    <div className="text-center mb-16">
                        <h2 className={`text-3xl sm:text-4xl font-bold ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                            How It Works
                        </h2>
                        <p className={`mt-4 text-lg ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                            Get your tune in three simple steps
                        </p>
                    </div>

                    <div className="grid md:grid-cols-3 gap-8">
                        {steps.map((step) => (
                            <div key={step.num} className="relative text-center">
                                <div className="w-16 h-16 mx-auto mb-6 rounded-full bg-gradient-to-br from-red-500 via-orange-500 to-red-600 flex items-center justify-center text-white font-bold text-2xl shadow-lg">
                                    {step.num}
                                </div>
                                <h3 className={`font-bold text-xl mb-3 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                                    {step.title}
                                </h3>
                                <p className={`${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                                    {step.desc}
                                </p>
                            </div>
                        ))}
                    </div>
                </div>
            </section>

            {/* Supported Platforms */}
            <section className="max-w-6xl mx-auto px-6 py-20">
                <div className="text-center mb-12">
                    <h2 className={`text-3xl sm:text-4xl font-bold ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                        Supported Platforms
                    </h2>
                    <p className={`mt-4 text-lg ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                        Tunes available for all major makes and models
                    </p>
                </div>

                <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
                    {platforms.map((platform) => (
                        <div
                            key={platform}
                            className={`${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-xl p-6 text-center hover:shadow-lg transition-all duration-300`}
                        >
                            <CheckCircleIcon className="w-8 h-8 mx-auto mb-3 text-green-500" />
                            <span className={`font-semibold ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                                {platform}
                            </span>
                        </div>
                    ))}
                </div>

                <div className="text-center mt-8">
                    <Link
                        to="/collections"
                        className={`inline-flex items-center gap-2 ${darkMode ? 'text-red-400 hover:text-red-300' : 'text-red-600 hover:text-red-500'} font-semibold`}
                    >
                        View all supported vehicles
                        <span>&rarr;</span>
                    </Link>
                </div>
            </section>

            {/* CTA Section */}
            <section className="max-w-6xl mx-auto px-6 pb-20">
                <div className="relative overflow-hidden rounded-3xl bg-gradient-to-r from-red-500 via-orange-500 to-red-600 p-1">
                    <div className={`${darkMode ? 'bg-gray-900' : 'bg-gray-100'} rounded-3xl py-16 px-6 text-center`}>
                        <BoltIcon className="w-16 h-16 mx-auto mb-6 text-red-500" />
                        <h2 className={`text-3xl sm:text-4xl font-bold mb-4 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                            Ready to Unlock Performance?
                        </h2>
                        <p className={`mb-8 max-w-2xl mx-auto text-lg ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                            Create a free account and start browsing professional tunes for your vehicle today.
                        </p>
                        <div className="flex flex-col sm:flex-row gap-4 justify-center">
                            <Link
                                to="/sign-up"
                                className="px-10 py-4 bg-gradient-to-r from-red-500 via-orange-500 to-red-600 text-white font-bold rounded-xl shadow-lg hover:shadow-2xl hover:shadow-red-500/30 transform hover:-translate-y-1 transition-all duration-300"
                            >
                                Create Free Account
                            </Link>
                            <Link
                                to="/collections"
                                className={`px-10 py-4 ${darkMode ? 'bg-zinc-800 text-white hover:bg-zinc-700' : 'bg-white text-gray-900 hover:bg-gray-50'} font-bold rounded-xl shadow-lg transition-all duration-300`}
                            >
                                Browse Tunes
                            </Link>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
}
