import { useEffect, useRef } from 'react';
import { Link, useLoaderData, useSearchParams } from '@remix-run/react';
import { ActionFunctionArgs, redirect, json } from '@remix-run/server-runtime';
import { CheckCircleIcon, XCircleIcon } from '@heroicons/react/24/outline';
import { verifyCustomerAccount } from '~/providers/account/account';
import { useTranslation } from 'react-i18next';
import { getFixedT } from '~/i18next.server';
import { LoaderFunctionArgs } from '@remix-run/router';

type LoaderReturnType = {
  success: boolean;
  error?: string;
  headersJson?: string;
};

export async function loader({
  request,
}: LoaderFunctionArgs): Promise<LoaderReturnType> {
  const url = new URL(request.url);
  const token = url.searchParams.get('token');
  if (!token) {
    return {
      success: false,
      error: 'No verification token provided. Please check your email link.',
    };
  }

  try {
    const result = await verifyCustomerAccount({ request }, token);
    if (result.__typename !== 'CurrentUser') {
      return { success: false, error: result.message };
    }

    const headersJson = JSON.stringify(Object.fromEntries(result._headers));
    return { success: true, headersJson };
  } catch (error: any) {
    return { success: false, error: error.message || 'Verification failed' };
  }
}

export async function action({ request }: ActionFunctionArgs) {
  const body = await request.formData();
  const headersJson = body.get('headers') as string;
  const redirectTarget = body.get('redirect') as string;

  if (!headersJson) {
    return null;
  }

  const headers = new Headers();
  const headerData = JSON.parse(headersJson);
  Object.keys(headerData).forEach((key) => {
    headers.set(key, headerData[key]);
  });

  return redirect(redirectTarget, { headers });
}

export default function VerifyTokenPage() {
  const [searchParams] = useSearchParams();
  const result = useLoaderData<LoaderReturnType>();
  const btnRef = useRef<HTMLButtonElement>(null);
  const { t } = useTranslation();

  useEffect(() => {
    if (!result.success || !btnRef.current) {
      return;
    }

    const submitBtn = btnRef.current;
    setTimeout(() => submitBtn.click(), 3000);
  }, [result]);

  return (
    <div className="min-h-screen bg-black flex flex-col justify-center py-12 sm:px-6 lg:px-8">
      <div className="sm:mx-auto sm:w-full sm:max-w-md">
        <h2 className="text-center text-3xl font-bold text-white mb-8">
          Email Verification
        </h2>
        <div className="bg-zinc-900 border border-zinc-800 py-8 px-6 shadow-xl rounded-xl">
          {result.success ? (
            <div className="rounded-lg bg-green-900/30 border border-green-500/50 p-6">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <CheckCircleIcon
                    className="h-8 w-8 text-green-500"
                    aria-hidden="true"
                  />
                </div>
                <div className="ml-4">
                  <h3 className="text-lg font-medium text-green-400">
                    Email Verified!
                  </h3>
                  <p className="text-sm text-green-300 mt-1">
                    Your email has been successfully verified. You will be redirected shortly...
                  </p>
                </div>
              </div>
              <form method="post" className="mt-6">
                <input
                  type="hidden"
                  name="redirect"
                  value={searchParams.get('redirectTo') || '/account'}
                />
                <input
                  type="hidden"
                  name="headers"
                  value={result.headersJson}
                />
                <button
                  ref={btnRef}
                  type="submit"
                  className="w-full bg-green-600 hover:bg-green-700 text-white font-medium py-3 px-4 rounded-lg transition-colors"
                >
                  Continue to Dashboard
                </button>
              </form>
            </div>
          ) : (
            <div className="rounded-lg bg-red-900/30 border border-red-500/50 p-6">
              <div className="flex items-start">
                <div className="flex-shrink-0">
                  <XCircleIcon
                    className="h-8 w-8 text-red-500"
                    aria-hidden="true"
                  />
                </div>
                <div className="ml-4">
                  <h3 className="text-lg font-medium text-red-400">
                    Verification Failed
                  </h3>
                  <p className="text-sm text-red-300 mt-1">{result.error}</p>
                </div>
              </div>
              <div className="mt-6 space-y-3">
                <Link
                  to="/sign-up"
                  className="block w-full bg-zinc-800 hover:bg-zinc-700 text-white text-center font-medium py-3 px-4 rounded-lg transition-colors"
                >
                  Try Signing Up Again
                </Link>
                <Link
                  to="/sign-in"
                  className="block w-full bg-red-600 hover:bg-red-700 text-white text-center font-medium py-3 px-4 rounded-lg transition-colors"
                >
                  Sign In Instead
                </Link>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
