        location: 'Dallas, TX',
        address: '9012 Truck Lane, Dallas, TX 75001',
        phone: '(214) 555-0789',
        rating: 4.8,
        reviewCount: 189,
        verified: true,
        featured: true,
        services: ['Diesel Tuning', 'EGR Delete', 'DPF Delete', 'Transmission Upgrades'],
