        slug: 'track-day-tuning',
        initials: 'TD',
        description: 'Race prep and track day specialists. Corner balancing, alignment, brake upgrades, and data logging.',
        location: 'Atlanta, GA',
        address: '6789 Lap Time Ln, Atlanta, GA 30301',
        phone: '(404) 555-0123',
        rating: 5.0,
        reviewCount: 76,
        verified: true,
        featured: false,
        services: ['Corner Balancing', 'Race Alignment', 'Brake Systems', 'Data Logging'],
