const serviceTypes = [
    'All Services',
    'Dyno Tuning',
    'Forced Induction',
    'Engine Builds',
    'Diesel Specialists',
    'European Imports',
    'Japanese Imports',
    'Track Prep',
];

export async function loader({ request }: DataFunctionArgs) {
    // TODO: Replace with Vendure services data when available
    return json({ services: demoServices });
}

export default function ServicesIndex() {
    const { services } = useLoaderData<typeof loader>();
    const { darkMode } = useDarkMode();
    const { t } = useTranslation();

    const featuredServices = services.filter(s => s.featured);
    const allServices = services;

    const cardClasses = `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-2xl`;
    const textPrimary = darkMode ? 'text-white' : 'text-gray-900';
    const textSecondary = darkMode ? 'text-gray-400' : 'text-gray-600';

    return (
        <div className={`min-h-screen pb-24 ${darkMode ? 'bg-gray-900' : 'bg-gray-100'}`}>
            {/* Hero Section */}
            <div className="relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-black via-gray-900/90 to-black" />
                <div className="absolute inset-0 bg-gradient-to-r from-green-500/10 via-teal-500/10 to-blue-500/10 animate-pulse" />

                <div className="relative max-w-6xl mx-auto py-24 px-6 flex flex-col items-center text-center">
                    <BuildingStorefrontIcon className="w-16 h-16 text-green-500 mb-6" />
                    <h1 className="text-4xl sm:text-5xl lg:text-6xl font-black tracking-tight">
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-green-400 via-teal-500 to-blue-500">
                            In-Person Services
                        </span>
                    </h1>
                    <p className="mt-6 text-xl text-gray-300 max-w-2xl">
                        Find verified performance shops near you. Dyno tuning, installs, engine builds, and more from trusted professionals.
                    </p>

                    {/* Search Bar */}
                    <div className="mt-8 w-full max-w-xl">
                        <div className="relative">
                            <MagnifyingGlassIcon className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
                            <input
                                type="text"
                                placeholder="Search by shop name, service, or location..."
                                className={`w-full pl-12 pr-4 py-4 rounded-xl ${
                                    darkMode
                                        ? 'bg-zinc-800 border-zinc-700 text-white placeholder-gray-500'
                                        : 'bg-white border-gray-200 text-gray-900 placeholder-gray-400'
                                } border focus:ring-2 focus:ring-green-500 focus:outline-none`}
                            />
                        </div>
                    </div>

                    {/* Location Filter */}
                    <div className="mt-4 flex flex-wrap justify-center gap-3">
                        <button className="px-4 py-2 bg-green-500 text-white rounded-full text-sm font-medium">
                            Near Me
                        </button>
                        <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-800 text-gray-300' : 'bg-white text-gray-700'} rounded-full text-sm font-medium`}>
                            Texas
                        </button>
                        <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-800 text-gray-300' : 'bg-white text-gray-700'} rounded-full text-sm font-medium`}>
                            California
                        </button>
                        <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-800 text-gray-300' : 'bg-white text-gray-700'} rounded-full text-sm font-medium`}>
                            Florida
                        </button>
                        <button className={`px-4 py-2 ${darkMode ? 'bg-zinc-800 text-gray-300' : 'bg-white text-gray-700'} rounded-full text-sm font-medium`}>
                            All States
                        </button>
                    </div>
                </div>

                <div className={`absolute bottom-0 left-0 right-0 h-24 bg-gradient-to-t ${darkMode ? 'from-gray-900' : 'from-gray-100'} to-transparent`} />
            </div>

            <div className="max-w-6xl mx-auto px-6 py-12">
                {/* Stats & Filters Bar */}
                <div className={`${cardClasses} p-4 mb-8`}>
                    <div className="flex flex-wrap items-center justify-between gap-4">
                        <div className={textSecondary}>
                            <span className={`font-bold ${textPrimary}`}>{allServices.length}</span> shops available
                        </div>
                        <div className="flex items-center gap-4">
                            <select className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 border-zinc-600 text-white' : 'bg-gray-50 border-gray-200 text-gray-900'} border text-sm`}>
                                {serviceTypes.map((type) => (
                                    <option key={type}>{type}</option>
                                ))}
                            </select>
                            <select className={`px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 border-zinc-600 text-white' : 'bg-gray-50 border-gray-200 text-gray-900'} border text-sm`}>
                                <option>Sort: Nearest</option>
                                <option>Top Rated</option>
                                <option>Most Reviews</option>
                                <option>Alphabetical</option>
                            </select>
                        </div>
                    </div>
                </div>

                {/* Featured Shops */}
                {featuredServices.length > 0 && (
                    <div className="mb-12">
                        <h2 className={`text-2xl font-bold mb-6 ${textPrimary} flex items-center gap-2`}>
                            <StarIcon className="w-6 h-6 text-yellow-500" />
                            Featured Shops
                        </h2>
                        <div className="grid lg:grid-cols-3 gap-6">
                            {featuredServices.map((shop) => (
                                <Link
                                    key={shop.id}
                                    to={`/services/${shop.slug}`}
                                    prefetch="intent"
                                    className={`${cardClasses} overflow-hidden group hover:shadow-2xl hover:shadow-green-500/10 transition-all duration-300 hover:-translate-y-1`}
                                >
                                    {/* Featured Badge */}
                                    <div className="bg-gradient-to-r from-green-500 via-teal-500 to-blue-500 text-white text-xs font-bold text-center py-1">
                                        FEATURED SHOP
                                    </div>

                                    <div className="p-6">
                                        <div className="flex items-start gap-4">
                                            <div className="w-16 h-16 rounded-xl bg-gradient-to-br from-green-400 via-teal-500 to-blue-500 flex items-center justify-center text-white font-bold text-xl shadow-lg flex-shrink-0">
                                                {shop.initials}
                                            </div>
                                            <div className="flex-1 min-w-0">
                                                <div className="flex items-center gap-2">
                                                    <h3 className={`font-bold text-lg truncate ${textPrimary}`}>{shop.name}</h3>
                                                    {shop.verified && (
                                                        <CheckBadgeIcon className="w-5 h-5 text-green-500 flex-shrink-0" />
                                                    )}
                                                </div>
                                                <div className="flex items-center mt-1">
                                                    {[...Array(5)].map((_, i) => (
                                                        <StarIcon key={i} className={`w-4 h-4 ${i < Math.floor(shop.rating) ? 'text-yellow-400' : 'text-gray-600'}`} />
                                                    ))}
                                                    <span className={`text-sm ml-2 ${textSecondary}`}>
                                                        {shop.rating} ({shop.reviewCount})
                                                    </span>
                                                </div>
                                            </div>
                                        </div>

                                        <p className={`mt-4 text-sm ${textSecondary} line-clamp-2`}>
                                            {shop.description}
                                        </p>

                                        <div className={`flex items-center text-sm mt-4 ${textSecondary}`}>
                                            <MapPinIcon className="w-4 h-4 mr-1 text-green-500" />
                                            {shop.location}
                                        </div>

                                        <div className={`flex items-center text-sm mt-2 ${textSecondary}`}>
                                            <ClockIcon className="w-4 h-4 mr-1" />
                                            {shop.hours}
                                        </div>

                                        <div className="flex flex-wrap gap-2 mt-4">
                                            {shop.services.slice(0, 3).map((service) => (
                                                <span
                                                    key={service}
                                                    className={`px-2 py-1 text-xs rounded-full ${darkMode ? 'bg-zinc-700 text-gray-300' : 'bg-gray-100 text-gray-700'}`}
                                                >
                                                    {service}
                                                </span>
                                            ))}
                                            {shop.services.length > 3 && (
                                                <span className={`px-2 py-1 text-xs rounded-full ${darkMode ? 'bg-zinc-700 text-gray-300' : 'bg-gray-100 text-gray-700'}`}>
                                                    +{shop.services.length - 3} more
                                                </span>
                                            )}
                                        </div>

                                        <div className={`mt-4 pt-4 border-t ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex justify-between items-center`}>
                                            <div className={`flex items-center text-sm ${textSecondary}`}>
                                                <PhoneIcon className="w-4 h-4 mr-1" />
                                                {shop.phone}
                                            </div>
                                            <span className="text-green-500 group-hover:translate-x-1 transition-transform">
                                                View Shop &rarr;
                                            </span>
                                        </div>
                                    </div>
                                </Link>
                            ))}
                        </div>
                    </div>
                )}

                {/* All Shops Grid */}
                <div>
                    <h2 className={`text-2xl font-bold mb-6 ${textPrimary}`}>
                        All Shops
                    </h2>
                    <div className="grid sm:grid-cols-2 gap-6">
                        {allServices.map((shop) => (
                            <Link
                                key={shop.id}
                                to={`/services/${shop.slug}`}
                                prefetch="intent"
                                className={`${cardClasses} p-6 group hover:shadow-2xl hover:shadow-green-500/10 transition-all duration-300 hover:-translate-y-1`}
                            >
                                <div className="flex items-start gap-4">
                                    <div className={`w-14 h-14 rounded-xl flex items-center justify-center text-white font-bold text-lg shadow-lg flex-shrink-0 ${
                                        shop.featured
                                            ? 'bg-gradient-to-br from-green-400 via-teal-500 to-blue-500'
                                            : darkMode ? 'bg-zinc-600' : 'bg-gray-400'
                                    }`}>
                                        {shop.initials}
                                    </div>
                                    <div className="flex-1 min-w-0">
                                        <div className="flex items-center gap-2">
                                            <h3 className={`font-bold truncate ${textPrimary}`}>{shop.name}</h3>
                                            {shop.verified && (
                                                <CheckBadgeIcon className="w-4 h-4 text-green-500 flex-shrink-0" />
                                            )}
                                        </div>
                                        <div className="flex items-center mt-1">
                                            {[...Array(5)].map((_, i) => (
                                                <StarIcon key={i} className={`w-3 h-3 ${i < Math.floor(shop.rating) ? 'text-yellow-400' : 'text-gray-600'}`} />
                                            ))}
                                            <span className={`text-xs ml-2 ${textSecondary}`}>
                                                ({shop.reviewCount})
                                            </span>
                                        </div>
                                    </div>
                                </div>

                                <p className={`mt-3 text-sm ${textSecondary} line-clamp-2`}>
                                    {shop.description}
                                </p>

                                <div className={`flex items-center text-sm mt-3 ${textSecondary}`}>
                                    <MapPinIcon className="w-4 h-4 mr-1" />
                                    {shop.location}
                                </div>

                                <div className="flex flex-wrap gap-1 mt-3">
                                    {shop.services.slice(0, 3).map((service) => (
                                        <span
                                            key={service}
                                            className={`px-2 py-0.5 text-xs rounded ${darkMode ? 'bg-zinc-700 text-gray-400' : 'bg-gray-100 text-gray-600'}`}
                                        >
                                            {service}
                                        </span>
                                    ))}
                                </div>

                                <div className={`mt-3 pt-3 border-t ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex justify-between text-sm`}>
                                    <span className={textSecondary}>{shop.phone}</span>
                                    <span className="text-green-500 group-hover:translate-x-1 transition-transform">
                                        &rarr;
                                    </span>
                                </div>
                            </Link>
                        ))}
                    </div>
                </div>

                {/* List Your Shop CTA */}
                <div className="mt-16">
                    <div className="relative overflow-hidden rounded-3xl bg-gradient-to-r from-green-500 via-teal-500 to-blue-500 p-1">
                        <div className={`${darkMode ? 'bg-gray-900' : 'bg-white'} rounded-3xl py-12 px-6 text-center`}>
                            <h2 className={`text-2xl sm:text-3xl font-bold mb-4 ${textPrimary}`}>
                                Own a Performance Shop?
                            </h2>
                            <p className={`mb-6 max-w-2xl mx-auto ${textSecondary}`}>
                                List your shop on TunerSwap and connect with enthusiasts in your area. Showcase your services and build your reputation.
                            </p>
                            <Link
                                to="/tunersignup"
                                className="inline-block px-8 py-3 bg-gradient-to-r from-green-500 via-teal-500 to-blue-500 text-white font-bold rounded-xl shadow-lg hover:shadow-2xl hover:shadow-green-500/25 transition-all duration-300"
                            >
                                List Your Shop
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
