import { CheckCircleIcon, EnvelopeIcon } from '@heroicons/react/24/outline';
import { Link } from '@remix-run/react';

export default function SuccessPage() {
  return (
    <div className="min-h-screen bg-black flex flex-col justify-center py-12 sm:px-6 lg:px-8">
      <div className="sm:mx-auto sm:w-full sm:max-w-md">
        <div className="bg-zinc-900 border border-zinc-800 py-8 px-6 shadow-xl rounded-xl">
          <div className="text-center">
            <div className="mx-auto w-16 h-16 rounded-full bg-green-500/20 flex items-center justify-center mb-4">
              <CheckCircleIcon className="w-10 h-10 text-green-500" />
            </div>
            <h2 className="text-2xl font-bold text-white mb-2">Account Created!</h2>
            <p className="text-zinc-400 mb-6">
              Your account has been created successfully.
            </p>
          </div>

          <div className="bg-zinc-800 rounded-lg p-4 mb-6">
            <div className="flex items-center gap-3 mb-2">
              <EnvelopeIcon className="w-5 h-5 text-blue-400" />
              <span className="text-white font-medium">Check Your Email</span>
            </div>
            <p className="text-zinc-400 text-sm">
              We've sent a verification link to your email address.
              Please click the link to verify your account before signing in.
            </p>
          </div>

          <div className="space-y-3">
            <Link
              to="/sign-in"
              className="w-full flex justify-center py-3 px-4 border border-transparent rounded-lg shadow-sm text-sm font-medium text-white bg-red-600 hover:bg-red-700 transition-colors"
            >
              Go to Sign In
            </Link>
            <Link
              to="/"
              className="w-full flex justify-center py-3 px-4 border border-zinc-700 rounded-lg shadow-sm text-sm font-medium text-zinc-300 hover:bg-zinc-800 transition-colors"
            >
              Return to Home
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}
