import { Form, Link, useActionData, useSearchParams } from '@remix-run/react';
import { ActionFunctionArgs, json, redirect } from '@remix-run/server-runtime';
import { registerCustomerAccount } from '~/providers/account/account';
import { XCircleIcon } from '@heroicons/react/24/solid';
import {
  extractRegistrationFormValues,
  RegisterValidationErrors,
  validateRegistrationForm,
} from '~/utils/registration-helper';
import { API_URL, DEMO_API_URL } from '~/constants';
import { useTranslation } from 'react-i18next';
import { getFixedT } from '~/i18next.server';

export async function action({ request }: ActionFunctionArgs) {
    const body = await request.formData();

    // Validate form
    const fieldErrors = validateRegistrationForm(body);
    if (Object.keys(fieldErrors).length !== 0) {
        return fieldErrors;
    }

    // Extract registration variables
    const variables = extractRegistrationFormValues(body);

    // Call Vendure shop API
    const result = await registerCustomerAccount({ request }, variables);

    if (result.__typename === 'Success') {
        return redirect('/sign-up/success');
    } else {
        const formError: RegisterValidationErrors = {
            form: result.errorCode,
        };
        return json(formError, { status: 401 });
    }
}


export default function SignUpPage() {
  const [searchParams] = useSearchParams();
  const formErrors = useActionData<RegisterValidationErrors>();
  const { t } = useTranslation();

  return (
    <div className="min-h-screen bg-gray-900 pb-24">
      {/* Hero Header */}
      <header className="py-24 px-6 text-center bg-gradient-to-r from-yellow-400 via-red-500 to-blue-500">
        <h1 className="text-5xl font-extrabold text-white mb-4">
          {t('account.create')}
        </h1>
        <p className="text-lg text-white/90">
          {t('common.or')}{' '}
          <Link
            to="/sign-in"
            className="font-medium text-white underline hover:text-yellow-200"
          >
            {t('account.login')}
          </Link>
        </p>
      </header>

      {/* Form Card */}
      <div className="max-w-md mx-auto -mt-16 relative z-10 px-4">
        <div className="bg-zinc-800 rounded-2xl shadow-2xl p-8">
          <Form className="space-y-6" method="post">
            <input
              type="hidden"
              name="redirectTo"
              value={searchParams.get('redirectTo') ?? undefined}
            />

            <div>
              <label
                htmlFor="email"
                className="block text-sm font-medium text-gray-300"
              >
                {t('account.emailAddress')}
              </label>
              <div className="mt-1">
                <input
                  id="email"
                  name="email"
                  type="email"
                  autoComplete="email"
                  className="appearance-none block w-full px-4 py-3 bg-zinc-700 border border-zinc-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-red-500 focus:border-transparent sm:text-sm"
                />
                {formErrors?.email && (
                  <div className="text-xs text-red-400 mt-1">
                    {formErrors.email}
                  </div>
                )}
              </div>
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div>
                <label
                  htmlFor="firstName"
                  className="block text-sm font-medium text-gray-300"
                >
                  {t('account.firstName')}
                </label>
                <div className="mt-1">
                  <input
                    id="firstName"
                    name="firstName"
                    type="text"
                    autoComplete="given-name"
                    className="appearance-none block w-full px-4 py-3 bg-zinc-700 border border-zinc-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-red-500 focus:border-transparent sm:text-sm"
                  />
                </div>
              </div>

              <div>
                <label
                  htmlFor="lastName"
                  className="block text-sm font-medium text-gray-300"
                >
                  {t('account.lastName')}
                </label>
                <div className="mt-1">
                  <input
                    id="lastName"
                    name="lastName"
                    type="text"
                    autoComplete="family-name"
                    className="appearance-none block w-full px-4 py-3 bg-zinc-700 border border-zinc-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-red-500 focus:border-transparent sm:text-sm"
                  />
                </div>
              </div>
            </div>

            <div>
              <label
                htmlFor="password"
                className="block text-sm font-medium text-gray-300"
              >
                {t('account.password')}
              </label>
              <div className="mt-1">
                <input
                  id="password"
                  name="password"
                  type="password"
                  autoComplete="new-password"
                  className="appearance-none block w-full px-4 py-3 bg-zinc-700 border border-zinc-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-red-500 focus:border-transparent sm:text-sm"
                />
                {formErrors?.password && (
                  <div className="text-xs text-red-400 mt-1">
                    {formErrors.password}
                  </div>
                )}
              </div>
            </div>

            <div>
              <label
                htmlFor="repeatPassword"
                className="block text-sm font-medium text-gray-300"
              >
                {t('account.repeatPassword')}
              </label>
              <div className="mt-1">
                <input
                  id="repeatPassword"
                  name="repeatPassword"
                  type="password"
                  autoComplete="new-password"
                  className="appearance-none block w-full px-4 py-3 bg-zinc-700 border border-zinc-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-red-500 focus:border-transparent sm:text-sm"
                />
                {formErrors?.repeatPassword && (
                  <div className="text-xs text-red-400 mt-1">
                    {formErrors.repeatPassword}
                  </div>
                )}
              </div>
            </div>

            {formErrors?.form && (
              <div className="rounded-lg bg-red-900/50 border border-red-700 p-4">
                <div className="flex">
                  <div className="flex-shrink-0">
                    <XCircleIcon
                      className="h-5 w-5 text-red-400"
                      aria-hidden="true"
                    />
                  </div>
                  <div className="ml-3">
                    <h3 className="text-sm font-medium text-red-300">
                      {t('account.createError')}
                    </h3>
                    <p className="text-sm text-red-400 mt-2">
                      {formErrors.form}
                    </p>
                  </div>
                </div>
              </div>
            )}

            <div>
              <button
                type="submit"
                className="w-full flex justify-center py-3 px-4 border border-transparent rounded-lg shadow-lg text-sm font-semibold text-white bg-gradient-to-r from-yellow-500 via-red-500 to-blue-500 hover:from-yellow-400 hover:via-red-400 hover:to-blue-400 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 focus:ring-offset-zinc-800 transition-all"
              >
                {t('account.signUp')}
              </button>
            </div>
          </Form>

          <div className="mt-6 text-center">
            <p className="text-sm text-gray-400">
              Already have an account?{' '}
              <Link
                to="/sign-in"
                className="font-medium text-red-400 hover:text-red-300"
              >
                Sign in here
              </Link>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
