import { useDarkMode } from '~/root';

export function useSellerAdminStyles() {
    const { darkMode } = useDarkMode();

    return {
        darkMode,
        cardClasses: `${darkMode ? 'bg-zinc-800/50 border-zinc-700' : 'bg-white border-gray-200'} border rounded-xl`,
        inputClasses: `w-full px-4 py-2 rounded-lg ${darkMode ? 'bg-zinc-700 border-zinc-600 text-white placeholder-gray-400' : 'bg-gray-50 border-gray-200 text-gray-900 placeholder-gray-500'} border focus:ring-2 focus:ring-red-500 focus:outline-none`,
        textPrimary: darkMode ? 'text-white' : 'text-gray-900',
        textSecondary: darkMode ? 'text-gray-400' : 'text-gray-600',
    };
}
