import { withDemoFallback, demoOnly, type FallbackResult } from '../utils/vendure-fallback';
import {
    sellerData,
    statsData,
    type SellerData,
    type StatsData
} from '~/data/seller-demo-data';
import gql from 'graphql-tag';
import { API_URL } from '~/constants';

export interface SellerProfileData {
    seller: SellerData;
    stats: StatsData;
}

/**
 * Fetch seller profile from Vendure marketplace API
 */
async function fetchSellerProfile(request?: Request): Promise<SellerProfileData> {
    const query = gql`
        query ActiveSellerProfile {
            activeSellerProfile {
                id
                firstName
                lastName
                phone
                location
                address
                businessName
                website
                instagram
                facebook
                bio
                experience
                software
                vehiclePlatforms
                tuneTypes
                hasDyno
                hours
                status
                verified
                rating
                reviewCount
                tunesSold
                totalOrders
                slug
                createdAt
            }
        }
    `;

    const headers: Record<string, string> = {
        'Content-Type': 'application/json',
    };

    // Get auth token from cookie if available
    if (request) {
        const cookie = request.headers.get('Cookie');
        if (cookie) {
            headers['Cookie'] = cookie;
        }
    }

    const response = await fetch(API_URL, {
        method: 'POST',
        headers,
        body: JSON.stringify({
            query: query.loc?.source.body,
        }),
    });

    const result = await response.json();

    if (result.errors || !result.data?.activeSellerProfile) {
        throw new Error(result.errors?.[0]?.message || 'Failed to fetch seller profile');
    }

    const profile = result.data.activeSellerProfile;

    // Map Vendure profile to SellerData format
    const seller: SellerData = {
        id: profile.id,
        name: profile.businessName || `${profile.firstName} ${profile.lastName}`,
        email: '', // Will be from customer data
        phone: profile.phone || '',
        location: profile.location || '',
        address: profile.address || '',
        bio: profile.bio || '',
        specialty: profile.tuneTypes?.join(', ') || '',
        initials: `${profile.firstName?.[0] || ''}${profile.lastName?.[0] || ''}`.toUpperCase(),
        rating: profile.rating || 0,
        reviewCount: profile.reviewCount || 0,
        tunesSold: profile.tunesSold || 0,
        verified: profile.verified || false,
        joinDate: new Date(profile.createdAt).toLocaleDateString('en-US', {
            year: 'numeric',
            month: 'long',
        }),
        website: profile.website || '',
        instagram: profile.instagram || '',
        facebook: profile.facebook || '',
        hours: profile.hours ? JSON.parse(profile.hours) : {},
        platforms: profile.vehiclePlatforms || [],
        software: profile.software || [],
        stripeConnected: false, // TODO: Connect to payment info
        paypalConnected: false,
    };

    const stats: StatsData = {
        totalRevenue: 0, // TODO: Calculate from orders
        monthlyRevenue: 0,
        totalOrders: profile.totalOrders || 0,
        pendingOrders: 0, // TODO: Calculate from orders
        totalTunes: profile.tunesSold || 0,
        totalParts: 0,
        totalServices: 0,
        avgRating: profile.rating || 0,
        views: 0,
    };

    return { seller, stats };
}

/**
 * Get seller profile and dashboard stats
 */
export async function getSellerProfile(request?: Request): Promise<FallbackResult<SellerProfileData>> {
    return withDemoFallback(
        () => fetchSellerProfile(request),
        { seller: sellerData, stats: statsData }
    );
}

/**
 * Update seller profile
 */
export async function updateSellerProfile(
    data: Partial<SellerData>,
    request?: Request
): Promise<FallbackResult<SellerData>> {
    const query = gql`
        mutation UpdateSellerProfile($input: UpdateSellerProfileInput!) {
            updateSellerProfile(input: $input) {
                id
                firstName
                lastName
                businessName
            }
        }
    `;

    const headers: Record<string, string> = {
        'Content-Type': 'application/json',
    };

    if (request) {
        const cookie = request.headers.get('Cookie');
        if (cookie) {
            headers['Cookie'] = cookie;
        }
    }

    return withDemoFallback(
        async () => {
            const response = await fetch(API_URL, {
                method: 'POST',
                headers,
                body: JSON.stringify({
                    query: query.loc?.source.body,
                    variables: { input: data },
                }),
            });

            const result = await response.json();
            if (result.errors) {
                throw new Error(result.errors[0]?.message || 'Update failed');
            }
            return { ...sellerData, ...data };
        },
        sellerData
    );
}
