import { useState, useEffect } from 'react';
import { useFetcher } from '@remix-run/react';
import {
    PlusIcon,
    TrashIcon,
    PencilIcon,
    StarIcon,
    CpuChipIcon,
    WrenchScrewdriverIcon,
    CalendarDaysIcon,
} from '@heroicons/react/24/solid';
import { useBuyerAdminStyles } from './useBuyerAdminStyles';
import { type Vehicle } from '~/data/buyer-demo-data';

interface GarageTabProps {
    vehicles?: Vehicle[];
}

export function GarageTab({ vehicles: propVehicles }: GarageTabProps) {
    const { darkMode, cardClasses, inputClasses, textPrimary, textSecondary } = useBuyerAdminStyles();
    const fetcher = useFetcher();

    // Use ONLY provided data - no demo fallback
    const vehicles = propVehicles || [];
    // Empty arrays for tunes/parts/services - will come from Vendure orders later
    const buyerTunes: any[] = [];
    const buyerParts: any[] = [];
    const buyerServices: any[] = [];

    const [selectedVehicle, setSelectedVehicle] = useState<string | null>(vehicles[0]?.id || null);
    const [isAddingVehicle, setIsAddingVehicle] = useState(false);
    const [newVehicle, setNewVehicle] = useState({ year: '', make: '', model: '', trim: '', vin: '' });

    // Update selected vehicle when vehicles change
    useEffect(() => {
        if (vehicles.length > 0 && !vehicles.find(v => v.id === selectedVehicle)) {
            setSelectedVehicle(vehicles[0].id);
        }
    }, [vehicles, selectedVehicle]);

    // Get data for selected vehicle
    const vehicle = vehicles.find(v => v.id === selectedVehicle);
    const vehicleString = vehicle ? `${vehicle.year} ${vehicle.make} ${vehicle.model}` : '';

    const vehicleTunes = buyerTunes.filter(t => t.vehicle.includes(vehicle?.make || '') || t.vehicle.includes(vehicle?.model || ''));
    const vehicleParts = buyerParts.filter(p => p.vehicle.includes(vehicle?.make || '') || p.vehicle.includes(vehicle?.model || ''));
    const vehicleServices = buyerServices.filter(s => s.vehicle.includes(vehicle?.make || '') || s.vehicle.includes(vehicle?.model || ''));

    const handleAddVehicle = () => {
        if (!newVehicle.year || !newVehicle.make || !newVehicle.model) return;

        fetcher.submit(
            {
                intent: 'addVehicle',
                year: newVehicle.year,
                make: newVehicle.make,
                model: newVehicle.model,
                trim: newVehicle.trim,
                vin: newVehicle.vin,
            },
            { method: 'post', action: '/buyeradmin/garage' }
        );
        setIsAddingVehicle(false);
        setNewVehicle({ year: '', make: '', model: '', trim: '', vin: '' });
    };

    const handleRemoveVehicle = (vehicleId: string) => {
        if (!confirm('Are you sure you want to remove this vehicle?')) return;
        fetcher.submit(
            { intent: 'removeVehicle', vehicleId },
            { method: 'post', action: '/buyeradmin/garage' }
        );
    };

    const handleSetPrimary = (vehicleId: string) => {
        fetcher.submit(
            { intent: 'setPrimary', vehicleId },
            { method: 'post', action: '/buyeradmin/garage' }
        );
    };

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <h2 className={`text-2xl font-bold ${textPrimary}`}>My Garage</h2>
                <button
                    onClick={() => setIsAddingVehicle(true)}
                    className="px-4 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded-lg transition-colors flex items-center gap-2"
                >
                    <PlusIcon className="w-4 h-4" />
                    Add Vehicle
                </button>
            </div>

            {/* Vehicle Cards */}
            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-4">
                {vehicles.map((v) => (
                    <div
                        key={v.id}
                        onClick={() => setSelectedVehicle(v.id)}
                        className={`${cardClasses} p-4 cursor-pointer transition-all ${
                            selectedVehicle === v.id
                                ? 'ring-2 ring-blue-500'
                                : 'hover:border-blue-500/50'
                        }`}
                    >
                        <div className="flex items-start justify-between mb-3">
                            <div className="w-16 h-16 rounded-lg bg-gradient-to-br from-blue-500/20 to-purple-500/20 flex items-center justify-center">
                                <span className="text-3xl">🚗</span>
                            </div>
                            <div className="flex items-center gap-2">
                                {v.isPrimary && (
                                    <StarIcon className="w-5 h-5 text-yellow-500" />
                                )}
                                <button className={`p-1.5 rounded ${darkMode ? 'hover:bg-zinc-700' : 'hover:bg-gray-100'}`}>
                                    <PencilIcon className={`w-4 h-4 ${textSecondary}`} />
                                </button>
                            </div>
                        </div>
                        <h3 className={`font-semibold ${textPrimary}`}>
                            {v.year} {v.make} {v.model}
                        </h3>
                        {v.trim && <p className={`text-sm ${textSecondary}`}>{v.trim}</p>}
                        {v.mileage && (
                            <p className={`text-sm ${textSecondary} mt-1`}>
                                {v.mileage.toLocaleString()} miles
                            </p>
                        )}
                        <div className="flex gap-2 mt-3">
                            <span className="px-2 py-0.5 text-xs bg-purple-500/20 text-purple-500 rounded">
                                {vehicleTunes.length} Tunes
                            </span>
                            <span className="px-2 py-0.5 text-xs bg-orange-500/20 text-orange-500 rounded">
                                {vehicleParts.length} Parts
                            </span>
                        </div>
                    </div>
                ))}

                {/* Add Vehicle Card */}
                <div
                    onClick={() => setIsAddingVehicle(true)}
                    className={`${cardClasses} p-4 cursor-pointer border-dashed flex flex-col items-center justify-center min-h-[180px] hover:border-blue-500/50 transition-colors`}
                >
                    <div className={`w-12 h-12 rounded-full ${darkMode ? 'bg-zinc-700' : 'bg-gray-100'} flex items-center justify-center mb-3`}>
                        <PlusIcon className={`w-6 h-6 ${textSecondary}`} />
                    </div>
                    <p className={textSecondary}>Add New Vehicle</p>
                </div>
            </div>

            {/* Selected Vehicle Details */}
            {vehicle && (
                <div className="grid lg:grid-cols-3 gap-6">
                    {/* Vehicle Info */}
                    <div className={cardClasses}>
                        <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                            <h3 className={`font-semibold ${textPrimary}`}>Vehicle Details</h3>
                        </div>
                        <div className="p-4 space-y-3">
                            <div className="flex justify-between">
                                <span className={textSecondary}>Year</span>
                                <span className={textPrimary}>{vehicle.year}</span>
                            </div>
                            <div className="flex justify-between">
                                <span className={textSecondary}>Make</span>
                                <span className={textPrimary}>{vehicle.make}</span>
                            </div>
                            <div className="flex justify-between">
                                <span className={textSecondary}>Model</span>
                                <span className={textPrimary}>{vehicle.model}</span>
                            </div>
                            {vehicle.trim && (
                                <div className="flex justify-between">
                                    <span className={textSecondary}>Trim</span>
                                    <span className={textPrimary}>{vehicle.trim}</span>
                                </div>
                            )}
                            {vehicle.vin && (
                                <div className="flex justify-between">
                                    <span className={textSecondary}>VIN</span>
                                    <span className={`${textPrimary} text-sm font-mono`}>{vehicle.vin}</span>
                                </div>
                            )}
                            {vehicle.mileage && (
                                <div className="flex justify-between">
                                    <span className={textSecondary}>Mileage</span>
                                    <span className={textPrimary}>{vehicle.mileage.toLocaleString()}</span>
                                </div>
                            )}
                            <div className="pt-3 flex gap-2">
                                <button className={`flex-1 px-3 py-2 text-sm ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}>
                                    Edit
                                </button>
                                <button className="px-3 py-2 text-sm bg-red-500/20 text-red-500 hover:bg-red-500/30 rounded-lg transition-colors">
                                    <TrashIcon className="w-4 h-4" />
                                </button>
                            </div>
                        </div>
                    </div>

                    {/* Installed Tunes */}
                    <div className={cardClasses}>
                        <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex items-center justify-between`}>
                            <h3 className={`font-semibold ${textPrimary}`}>Installed Tunes</h3>
                            <CpuChipIcon className="w-5 h-5 text-purple-500" />
                        </div>
                        <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                            {vehicleTunes.length > 0 ? vehicleTunes.map((tune) => (
                                <div key={tune.id} className="p-4">
                                    <p className={`font-medium ${textPrimary}`}>{tune.name}</p>
                                    <p className={`text-sm ${textSecondary}`}>{tune.seller.name}</p>
                                    <div className="flex items-center gap-2 mt-2">
                                        <span className="text-xs text-purple-500">{tune.specs.hp} HP</span>
                                        <span className={`text-xs ${textSecondary}`}>•</span>
                                        <span className="text-xs text-orange-500">{tune.specs.torque} lb-ft</span>
                                    </div>
                                </div>
                            )) : (
                                <div className="p-4 text-center">
                                    <p className={textSecondary}>No tunes installed</p>
                                    <button className="mt-2 text-sm text-purple-500 hover:text-purple-400">
                                        Browse Tunes
                                    </button>
                                </div>
                            )}
                        </div>
                    </div>

                    {/* Installed Parts */}
                    <div className={cardClasses}>
                        <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex items-center justify-between`}>
                            <h3 className={`font-semibold ${textPrimary}`}>Installed Parts</h3>
                            <WrenchScrewdriverIcon className="w-5 h-5 text-orange-500" />
                        </div>
                        <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                            {vehicleParts.length > 0 ? vehicleParts.map((part) => (
                                <div key={part.id} className="p-4">
                                    <p className={`font-medium ${textPrimary}`}>{part.name}</p>
                                    <p className={`text-sm ${textSecondary}`}>{part.seller.name}</p>
                                    <span className={`text-xs px-2 py-0.5 rounded mt-2 inline-block ${
                                        part.status === 'delivered' ? 'bg-green-500/20 text-green-500' :
                                        part.status === 'shipped' ? 'bg-blue-500/20 text-blue-500' :
                                        'bg-yellow-500/20 text-yellow-500'
                                    }`}>{part.status}</span>
                                </div>
                            )) : (
                                <div className="p-4 text-center">
                                    <p className={textSecondary}>No parts added</p>
                                    <button className="mt-2 text-sm text-orange-500 hover:text-orange-400">
                                        Shop Parts
                                    </button>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            )}

            {/* Service History */}
            {vehicle && vehicleServices.length > 0 && (
                <div className={cardClasses}>
                    <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'} flex items-center justify-between`}>
                        <h3 className={`font-semibold ${textPrimary}`}>Service History</h3>
                        <CalendarDaysIcon className="w-5 h-5 text-green-500" />
                    </div>
                    <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                        {vehicleServices.map((service) => (
                            <div key={service.id} className="p-4 flex items-center justify-between">
                                <div>
                                    <p className={`font-medium ${textPrimary}`}>{service.service}</p>
                                    <p className={`text-sm ${textSecondary}`}>{service.seller.name} • {service.date}</p>
                                </div>
                                <span className={`px-2 py-1 text-xs rounded ${
                                    service.status === 'completed' ? 'bg-gray-500/20 text-gray-500' :
                                    service.status === 'scheduled' ? 'bg-green-500/20 text-green-500' :
                                    'bg-red-500/20 text-red-500'
                                }`}>{service.status}</span>
                            </div>
                        ))}
                    </div>
                </div>
            )}

            {/* Add Vehicle Modal */}
            {isAddingVehicle && (
                <div className="fixed inset-0 z-50 bg-black/50 flex items-center justify-center p-4" onClick={() => setIsAddingVehicle(false)}>
                    <div className={`${darkMode ? 'bg-zinc-800' : 'bg-white'} rounded-xl max-w-md w-full p-6`} onClick={e => e.stopPropagation()}>
                        <h3 className={`text-xl font-bold mb-4 ${textPrimary}`}>Add New Vehicle</h3>
                        <div className="space-y-4">
                            <div className="grid grid-cols-2 gap-4">
                                <div>
                                    <label className={`block text-sm font-medium mb-2 ${textSecondary}`}>Year</label>
                                    <input
                                        type="number"
                                        placeholder="2024"
                                        value={newVehicle.year}
                                        onChange={(e) => setNewVehicle({ ...newVehicle, year: e.target.value })}
                                        className={inputClasses}
                                    />
                                </div>
                                <div>
                                    <label className={`block text-sm font-medium mb-2 ${textSecondary}`}>Make</label>
                                    <input
                                        type="text"
                                        placeholder="Ford"
                                        value={newVehicle.make}
                                        onChange={(e) => setNewVehicle({ ...newVehicle, make: e.target.value })}
                                        className={inputClasses}
                                    />
                                </div>
                            </div>
                            <div>
                                <label className={`block text-sm font-medium mb-2 ${textSecondary}`}>Model</label>
                                <input
                                    type="text"
                                    placeholder="Mustang"
                                    value={newVehicle.model}
                                    onChange={(e) => setNewVehicle({ ...newVehicle, model: e.target.value })}
                                    className={inputClasses}
                                />
                            </div>
                            <div>
                                <label className={`block text-sm font-medium mb-2 ${textSecondary}`}>Trim (Optional)</label>
                                <input
                                    type="text"
                                    placeholder="GT Premium"
                                    value={newVehicle.trim}
                                    onChange={(e) => setNewVehicle({ ...newVehicle, trim: e.target.value })}
                                    className={inputClasses}
                                />
                            </div>
                            <div>
                                <label className={`block text-sm font-medium mb-2 ${textSecondary}`}>VIN (Optional)</label>
                                <input
                                    type="text"
                                    placeholder="1FA6P8CF5K5123456"
                                    value={newVehicle.vin}
                                    onChange={(e) => setNewVehicle({ ...newVehicle, vin: e.target.value })}
                                    className={inputClasses}
                                />
                            </div>
                            <div className="flex gap-3 pt-4">
                                <button
                                    onClick={() => setIsAddingVehicle(false)}
                                    className={`flex-1 px-4 py-2 ${darkMode ? 'bg-zinc-700 hover:bg-zinc-600' : 'bg-gray-100 hover:bg-gray-200'} ${textPrimary} rounded-lg transition-colors`}
                                >
                                    Cancel
                                </button>
                                <button
                                    onClick={handleAddVehicle}
                                    disabled={!newVehicle.year || !newVehicle.make || !newVehicle.model || fetcher.state !== 'idle'}
                                    className="flex-1 px-4 py-2 bg-blue-500 hover:bg-blue-600 disabled:bg-blue-400 text-white rounded-lg transition-colors"
                                >
                                    {fetcher.state !== 'idle' ? 'Adding...' : 'Add Vehicle'}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}
