import type { MetaFunction } from '@remix-run/node';
import { useOutletContext } from '@remix-run/react';
import { OverviewTab } from '~/components/buyer-admin';
import type { BuyerData, BuyerStatsData } from '~/data/buyer-demo-data';

export const meta: MetaFunction = () => {
    return [{ title: 'Overview | My Dashboard | TunerSwap' }];
};

interface OutletContext {
    buyer: BuyerData;
    stats: BuyerStatsData;
    isDemo: boolean;
}

export default function BuyerOverviewRoute() {
    const context = useOutletContext<OutletContext>();

    return <OverviewTab buyer={context?.buyer} stats={context?.stats} orders={[]} />;
}
