import { demoOnly, type FallbackResult } from '../utils/vendure-fallback';
import {
    buyerTunes,
    buyerParts,
    buyerServices,
    buyerConnections,
    type BuyerTune,
    type BuyerPart,
    type BuyerService,
    type BuyerConnection
} from '~/data/buyer-demo-data';

/**
 * Get buyer's purchased tunes
 */
export async function getBuyerTunes(): Promise<FallbackResult<BuyerTune[]>> {
    return demoOnly(buyerTunes);
}

/**
 * Get buyer's purchased parts
 */
export async function getBuyerParts(): Promise<FallbackResult<BuyerPart[]>> {
    return demoOnly(buyerParts);
}

/**
 * Get buyer's booked services
 */
export async function getBuyerServices(): Promise<FallbackResult<BuyerService[]>> {
    return demoOnly(buyerServices);
}

/**
 * Get buyer's connections (payment methods, linked vehicles)
 */
export async function getBuyerConnections(): Promise<FallbackResult<BuyerConnection[]>> {
    return demoOnly(buyerConnections);
}

/**
 * Download a tune file
 */
export async function downloadTune(
    _tuneId: string
): Promise<FallbackResult<{ downloadUrl: string }>> {
    return demoOnly({ downloadUrl: '/demo-download' });
}

/**
 * Book a service
 */
export async function bookService(
    _serviceId: string,
    _date: string,
    _time: string
): Promise<FallbackResult<{ success: boolean; bookingId?: string }>> {
    return demoOnly({ success: true, bookingId: 'demo-booking-id' });
}
