import {
    ShoppingCartIcon,
    CpuChipIcon,
    WrenchScrewdriverIcon,
    CalendarDaysIcon,
    CurrencyDollarIcon,
    HeartIcon,
} from '@heroicons/react/24/solid';
import { useBuyerAdminStyles } from './useBuyerAdminStyles';
import {
    type BuyerData,
    type BuyerStatsData,
    type BuyerOrder
} from '~/data/buyer-demo-data';

interface OverviewTabProps {
    buyer?: BuyerData;
    stats?: BuyerStatsData;
    orders?: BuyerOrder[];
}

export function OverviewTab({ buyer, stats: propStats, orders: propOrders }: OverviewTabProps) {
    const { darkMode, cardClasses, textPrimary, textSecondary } = useBuyerAdminStyles();

    // Use ONLY provided data - no demo fallback
    const buyerStats: BuyerStatsData = propStats || {
        ordersCompleted: 0,
        tunesInstalled: 0,
        partsOrdered: 0,
        servicesBooked: 0,
        totalSpent: 0,
        savedSellers: 0,
    };
    const buyerOrders: BuyerOrder[] = propOrders || [];

    const stats = [
        { label: 'Orders Completed', value: buyerStats.ordersCompleted, icon: ShoppingCartIcon, color: 'text-blue-500', bg: 'bg-blue-500/20' },
        { label: 'Tunes Installed', value: buyerStats.tunesInstalled, icon: CpuChipIcon, color: 'text-purple-500', bg: 'bg-purple-500/20' },
        { label: 'Parts Ordered', value: buyerStats.partsOrdered, icon: WrenchScrewdriverIcon, color: 'text-orange-500', bg: 'bg-orange-500/20' },
        { label: 'Services Booked', value: buyerStats.servicesBooked, icon: CalendarDaysIcon, color: 'text-green-500', bg: 'bg-green-500/20' },
        { label: 'Total Spent', value: `$${buyerStats.totalSpent.toLocaleString()}`, icon: CurrencyDollarIcon, color: 'text-emerald-500', bg: 'bg-emerald-500/20' },
        { label: 'Saved Sellers', value: buyerStats.savedSellers, icon: HeartIcon, color: 'text-red-500', bg: 'bg-red-500/20' },
    ];

    return (
        <div className="space-y-6">
            <h2 className={`text-2xl font-bold ${textPrimary}`}>Dashboard Overview</h2>

            {/* Stats Grid */}
            <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-4">
                {stats.map((stat) => (
                    <div key={stat.label} className={cardClasses}>
                        <div className="p-4">
                            <div className={`w-10 h-10 rounded-lg ${stat.bg} flex items-center justify-center mb-3`}>
                                <stat.icon className={`w-5 h-5 ${stat.color}`} />
                            </div>
                            <p className={`text-2xl font-bold ${textPrimary}`}>{stat.value}</p>
                            <p className={`text-sm ${textSecondary}`}>{stat.label}</p>
                        </div>
                    </div>
                ))}
            </div>

            <div className="grid lg:grid-cols-2 gap-6">
                {/* Recent Orders */}
                <div className={cardClasses}>
                    <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                        <h3 className={`font-semibold ${textPrimary}`}>Recent Orders</h3>
                    </div>
                    <div className={`divide-y ${darkMode ? 'divide-zinc-700' : 'divide-gray-200'}`}>
                        {buyerOrders.slice(0, 4).map((order) => (
                            <div key={order.id} className="p-4 flex items-center justify-between">
                                <div className="flex items-center gap-3">
                                    <div className={`w-10 h-10 rounded-lg flex items-center justify-center ${
                                        order.type === 'tune' ? 'bg-purple-500/20' :
                                        order.type === 'parts' ? 'bg-orange-500/20' :
                                        'bg-green-500/20'
                                    }`}>
                                        {order.type === 'tune' ? (
                                            <CpuChipIcon className="w-5 h-5 text-purple-500" />
                                        ) : order.type === 'parts' ? (
                                            <WrenchScrewdriverIcon className="w-5 h-5 text-orange-500" />
                                        ) : (
                                            <CalendarDaysIcon className="w-5 h-5 text-green-500" />
                                        )}
                                    </div>
                                    <div>
                                        <p className={`font-medium ${textPrimary}`}>{order.product}</p>
                                        <p className={`text-sm ${textSecondary}`}>{order.seller.name}</p>
                                    </div>
                                </div>
                                <div className="text-right">
                                    <p className={`font-medium ${textPrimary}`}>${order.price}</p>
                                    <span className={`text-xs px-2 py-0.5 rounded ${
                                        order.status === 'completed' ? 'bg-green-500/20 text-green-500' :
                                        order.status === 'shipped' ? 'bg-blue-500/20 text-blue-500' :
                                        order.status === 'processing' ? 'bg-yellow-500/20 text-yellow-500' :
                                        'bg-purple-500/20 text-purple-500'
                                    }`}>{order.status}</span>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Upcoming Events */}
                <div className={cardClasses}>
                    <div className={`p-4 border-b ${darkMode ? 'border-zinc-700' : 'border-gray-200'}`}>
                        <h3 className={`font-semibold ${textPrimary}`}>Upcoming Events</h3>
                    </div>
                    <div className="p-4">
                        <p className={`text-sm ${textSecondary}`}>No upcoming events</p>
                    </div>
                </div>
            </div>
        </div>
    );
}
